/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from "react";
import { BASE_API_ } from "../../utils/base-url.config";
import axios from "axios";
import Cookies from "js-cookie";

const OrderForm = (pparams) => {
  const [values, setValues] = useState({
    notes: "",
    shippingStatus: "",
  });

  const [renderOrder, setRenderOrder] = useState({});
  const [saveChange, setSaveChange] = useState(false);
  const [successM, setSuccessM] = useState('')
  const [submitError, setSubmitError] = useState({});
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);

  //changing the input Values
  const handleChange = (e) => {
    setSaveChange(true);
   
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmit(true);
  };

  const getOrder = (pparams, historyProps) => {
    let id = pparams.params.id;
  
    const getOrders = JSON.parse(sessionStorage.getItem("orders") || "[]");
    
    if (getOrders.length !== 0) {
      const productDetails = async () => {
        const checkOrder = await getOrders.filter((data) => {
          if (data._id === id) {
            
            return data;
          }
         
          return null;
        });
        if (checkOrder.length !== 0) {
          setValues({
            ...values,
            notes: checkOrder[0].notes,
            shippingStatus: checkOrder[0].shippingStatus,
          });
          setRenderOrder(...checkOrder);
        } else {
          historyProps.goBack();
        }
      };

      productDetails();
    } else {
      const getToken = Cookies.get("_tk");
      axios
        .get(`${BASE_API_}/orders/v1/order/${id}`, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          setValues({
            ...values,
            notes: success.data.notes,
            shippingStatus: success.data.shippingStatus,
          });
          setRenderOrder({ ...success.data });
        })
        .catch((error) => {
          setSubmitError([error]);
        });
    }
  };

  useEffect(() => {
    
    if (Object.keys(errors).length === 0 && isSubmitting) {
       const getToken = Cookies.get("_tk");
      axios
        .patch(
          `${BASE_API_}/orders/v1/updateOrder/${pparams.params.id}`,
          values,
          {
            headers: {
              Authorization: "Bearer " + getToken,
            },
          }
        )
        .then((success) => {
          setIsSubmit(false);
          setValues({
            ...values,
            notes: success.data.notes,
            shippingStatus: success.data.shippingStatus,
          });
          setRenderOrder({ ...success.data });
          setSuccessM('Updated successfully');
        })
        .catch((error) => {
          setIsSubmit(false);
          setSubmitError([error]);
        });
    } else {
      setIsSubmit(false);
    }
  }, [errors, isSubmitting, values]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    getOrder,
    saveChange,
    renderOrder,
    isSubmitting,
    successM
  };
};

export default OrderForm;
