import React from "react";
import {
  ActionWrap,
  Iconwrap,
  Icon,
  IconDropWrap,
  IconDrop,
} from "./styles";
import { BsThreeDots } from "react-icons/bs";
class CDropDown extends React.Component {
  constructor(props) {
    super(props);
    this.state = { deleteActive: false, activedrop: false };
    this.dropRef = React.createRef();
    this.handleOutsideClick = this.handleOutsideClick.bind(this);
  }

  componentDidMount() {
    document.addEventListener("click", this.handleOutsideClick);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleOutsideClick);
  }

  handleOutsideClick = (event) => {
    if (this.dropRef && !this.dropRef.current.contains(event.target)) {
      this.setState({ activedrop: false });
    }
  };

  handleIconClick = (e) => {
    this.setState({ activedrop: !this.state.activedrop });
  };
  render() {
    return (
      <ActionWrap ref={this.dropRef}>
        <Iconwrap id="Icons" onClick={this.handleIconClick}>
          <Icon id="Icons">
            <BsThreeDots />
          </Icon>
        </Iconwrap>
        {this.state.activedrop && (
          <IconDropWrap>
            <IconDrop to={`/dashboard/customers/view/${this.props.id}`}>
              View
            </IconDrop>
          </IconDropWrap>
        )}
      </ActionWrap>
    );
  }
}

export default CDropDown;
