/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  EditBtn,
  PContainer,
  MainWrapper,
  ProductDetailsWrap,
  FormContentWrap,
  ImageUploadWrapper,
  FormContentWrap1,
  FormContent,
  FormContentDesc,
  ImageTitle,
  ImageContainer,
  Image,
  ImageWrap,
  StatusWrapper,
  TagsWrapper,
  CategoriesWrapper,
  VisibilityWrapper,
  StatusContainer,
  TagsContainer,
  VContainer,
  ContentWrap,
  CatWrapper,
  ImageClick,
  Visible,
  FormError,
  FormSubmitError,
  Success,
  FeaturedWrapper,
  FeaturedContainer,
  EditBtn2,
  CreateBtn2,
  ImageContainer2,
  ProductPriceWrap,
  ProductSize,
  ProductSizeTitle,
  ImageAlert,
  SideWrap1,
  TStockWrapper,
  TTReserved,
  TTStock,
  ImageStockWrap,
  SKUWrap,
  ImageStock,
  SizeDisplay,
  SizeDisplayContent,
} from "./styles";
//import validation and form actions
import validateProduct from "../../../../components/FormValidation/validateProduct";
import ProductActions from "../../../../components/FormValidation/productCreateForm";
import { FiUploadCloud } from "react-icons/fi";
import Loader from "react-loader-spinner";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine } from "react-icons/ri";

const MobileCreateProduct = () => {
  //destructure the actions
  const {
    errors,
    successMsg,
    submitError,
    values,
    handleChange,
    handleSubmit,
    imageHandler,
    imagesUrl,
    fetchCategories,
    getcatgeories,
    uploadPercentage,
    isSubmitting,
    colorHandler,
    color,
    sku,
    stock,
    stockHandler,
    skuHandler,
    sizeHandler,
    handleSubmit2,
    size,
    removeSize,
  } = ProductActions(validateProduct);

  useEffect(() => {
    fetchCategories();
  }, []);

  //total stock realtime
  const StockValue =
    Object.values(stock).length > 0 ? Object.values(stock) : [0];
  let totalStock =
    StockValue.reduce(
      (preValue, currentValue) => parseInt(preValue) + parseInt(currentValue)
    ) || 0;

  return (
    <Container>
      <form onSubmit={handleSubmit}>
        {successMsg && (
          <Success>
            {successMsg} {`${uploadPercentage}%`}
          </Success>
        )}
        {submitError.length !== 0 && submitError[0].response && (
          <FormSubmitError>{submitError[0].response.data}</FormSubmitError>
        )}
        {submitError.length !== 0 && !submitError[0].response && (
          <FormSubmitError>
            {submitError[0].message}, check your connection
          </FormSubmitError>
        )}

        <HeadWrap>
          <TitleWrap>
            <TitleHead>Add Product</TitleHead>
          </TitleWrap>
          <TitleSub>Create a product item</TitleSub>
        </HeadWrap>

        <PContainer>
          <MainWrapper>
            <ProductDetailsWrap>
              <FormContentWrap>
                <label htmlFor="productName">Product title*</label>
                <input
                  type="text"
                  name="name"
                  value={values.name}
                  onChange={handleChange}
                  id="productName"
                />
                {errors.name && <FormError>{errors.name}</FormError>}
              </FormContentWrap>

              <FormContentDesc>
                <label htmlFor="descriptions">Product Description*</label>
                <textarea
                  type="text"
                  name="description"
                  value={values.description}
                  onChange={handleChange}
                  id="descriptions"
                />
                {errors.description && (
                  <FormError>{errors.description}</FormError>
                )}
              </FormContentDesc>
            </ProductDetailsWrap>

            <ProductPriceWrap>
              <FormContentWrap1>
                <FormContent>
                  <label htmlFor="cost">Cost per item</label>
                  <input
                    type="text"
                    name="costperitem"
                    value={values.costperitem}
                    onChange={handleChange}
                    id="cost"
                  />
                </FormContent>
                <FormContent>
                  <label htmlFor="Price">Price *</label>
                  <input
                    type="text"
                    name="price"
                    value={values.price}
                    onChange={handleChange}
                    id="Price"
                  />
                  {errors.price && <FormError>{errors.price}</FormError>}
                </FormContent>
              </FormContentWrap1>
            </ProductPriceWrap>

            <ProductSize>
              <ProductSizeTitle>Size</ProductSizeTitle>
              <form onSubmit={handleSubmit2}>
                <input
                  type="text"
                  name="size"
                  value={values.size}
                  onChange={handleChange}
                />
                <CreateBtn2 onClick={sizeHandler}>
                  <AiOutlinePlus />
                </CreateBtn2>
              </form>

              <SizeDisplay>
                {size.map((data, index) => {
                  return (
                    <SizeDisplayContent key={index}>
                      <div onClick={() => removeSize(data)}>
                        <RiDeleteBinLine />
                      </div>
                      <p>{data}</p>
                    </SizeDisplayContent>
                  );
                })}
              </SizeDisplay>
            </ProductSize>

            <ImageUploadWrapper>
              <ImageWrap>
                <ImageTitle>Color</ImageTitle>

                <form onSubmit={handleSubmit2}>
                  <input
                    type="text"
                    name="color"
                    value={values.color}
                    onChange={handleChange}
                  />
                  <CreateBtn2 onClick={colorHandler}>
                    <AiOutlinePlus />
                  </CreateBtn2>
                </form>
                {values.colorError && (
                  <FormError>{values.colorError}</FormError>
                )}

                {color.map((data, index) => {
                  return (
                    <ImageContainer2 key={index}>
                      <ImageTitle>{data}</ImageTitle>
                      <ImageStockWrap>
                        <SKUWrap>
                          <label>SKU</label>
                          <p>
                            SKUcan contain product color, vendor or packaging
                            type.(i.e Sets)
                          </p>
                          <input
                            type="text"
                            name={`sku-${data}`}
                            value={sku[`sku-${data}`]}
                            onChange={(e) => skuHandler(e, data)}
                          />
                        </SKUWrap>

                        <ImageStock>
                          <label>Stock*</label>
                          <input
                            type="text"
                            name={`stock-${data}`}
                            value={stock[`stock-${data}`]}
                            onChange={(e) => stockHandler(e, data)}
                          />
                        </ImageStock>
                      </ImageStockWrap>
                      <ImageContainer>
                        <input
                          type="file"
                          name="imageUpload"
                          id={`upload-${data}`}
                          accept="image/*"
                          className="upload"
                          onChange={(e) => imageHandler(e, data)}
                        />
                        <label htmlFor={`upload-${data}`}>
                          <ImageClick>
                            <div>
                              <FiUploadCloud />
                            </div>
                            <h5>Upload</h5>
                          </ImageClick>
                        </label>

                        {imagesUrl.length !== 0 && (
                          <>
                            {imagesUrl.map((mainData) => {
                              if (mainData.type === data) {
                                return (
                                  <>
                                    {mainData.images.map((imageData, index) => {
                                      return (
                                        <Image>
                                          <img src={imageData} alt="" />
                                        </Image>
                                      );
                                    })}
                                  </>
                                );
                              } else {
                                return null;
                              }
                            })}
                          </>
                        )}
                      </ImageContainer>
                    </ImageContainer2>
                  );
                })}

                <ImageAlert>
                  To add images for this product you need to add a color section
                  at a time, then upload an image from that color section.
                </ImageAlert>
              </ImageWrap>
            </ImageUploadWrapper>

            <SideWrap1>
              <StatusWrapper>
                <StatusContainer>
                  <label>Status *</label>
                  <select
                    onChange={handleChange}
                    name="active"
                    value={values.active}
                  >
                    <option value={false} name="active">
                      Not Active
                    </option>
                    <option value={true} name="active">
                      {" "}
                      Active
                    </option>
                  </select>
                </StatusContainer>
              </StatusWrapper>

              <FeaturedWrapper>
                <FeaturedContainer>
                  <label>Featured </label>
                  <select
                    onChange={handleChange}
                    name="featured"
                    value={values.featured}
                  >
                    <option value={false} name="featured">
                      False
                    </option>
                    <option value={true} name="featured">
                      {" "}
                      True
                    </option>
                  </select>
                </FeaturedContainer>
              </FeaturedWrapper>

              <TagsWrapper>
                <TagsContainer>
                  <label htmlFor="Tags">Tags </label>
                  <input
                    type="text"
                    name="tags"
                    value={values.tags}
                    onChange={handleChange}
                    id="Tags"
                  />
                </TagsContainer>
              </TagsWrapper>

              <CategoriesWrapper>
                <CatWrapper>
                  <label>Categories</label>
                  {getcatgeories.length !== 0 && (
                    <div>
                      {getcatgeories.map((data, index) => {
                        if (data) {
                          return (
                            <ContentWrap key={data._id}>
                              <input
                                type="radio"
                                id={data.name}
                                name="category"
                                value={data._id}
                                onChange={handleChange}
                              />
                              <label htmlFor={data.name}>
                                <p>{data.name}</p>
                              </label>
                            </ContentWrap>
                          );
                        } else {
                          return null;
                        }
                      })}
                    </div>
                  )}
                  {errors.category && <FormError>{errors.category}</FormError>}
                </CatWrapper>
              </CategoriesWrapper>
            </SideWrap1>

            <VisibilityWrapper>
              <VContainer>
                <label>Visibility</label>
                <ContentWrap>
                  {values.active === "true" && <Visible />}

                  <label htmlFor="publish">
                    <p>Publishing on site</p>
                  </label>
                </ContentWrap>
              </VContainer>
            </VisibilityWrapper>

            <TStockWrapper>
              <TTStock>
                <label htmlFor="TotalStock">Total Stock* </label>
                <input
                  type="text"
                  name="totalStock"
                  value={totalStock}
                  readOnly
                  id="TotalStock"
                />
              </TTStock>
              <hr />
              <TTReserved>
                <label htmlFor="Reserved">Reserved </label>
                <input
                  type="text"
                  name="reserved"
                  value={values.reserved}
                  onChange={handleChange}
                  id="Reserved"
                />
              </TTReserved>
            </TStockWrapper>
          </MainWrapper>
        </PContainer>

        {isSubmitting ? (
          <EditBtn2>
            <Loader type="TailSpin" color="white" height={30} width={30} />
          </EditBtn2>
        ) : (
          <EditBtn type="submit">Submit</EditBtn>
        )}
      </form>
    </Container>
  );
};

export default MobileCreateProduct;
