/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import {
  TitleHead,
  TitleWrap,
  HeadWrap,
  EditBtn,
  CustomerContainer,
  OContainer,
  OrderWrapper,
  CustomerWrapper,
  OrderContainer,
  PayInfoWrap,
  ODateWrap,
  OProductWrap,
  TotalsWrap,
  StatusWrap,
  Dates,
  ProductContent,
  TextContent,
  PImage,
  PQuantity,
  Pprice,
  PSubtotal,
  TSubtotal,
  TShipping,
  Ttotal,
  PayButton,
  PayButtonWrap,
  NotesSection,
  PayInfo,
  Mnumber,
  Bname,
  Paybill,
  PayWrap,
  CHeadWrap,
  Ctitle,
  CProfile,
  ProfileWrapper,
  Profile,
  Ptitle,
  PEmail,
  PPhone,
  AddressWrapper,
  DeliverWrap,
  AddContainer,
  NotPayButton,
  DeleteIcon,
  SelectWrap,
  SelectionWrapper,
  EditBtn1,
  SelectionContainer,
  Ptext,
  DeleteContainer,
  DeleteWrapper,
  DeleteErrors,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  DeletesWrap,
  EditBtnL1,
  EditBtnL2,
  SuccessP,
} from "./styles";
import testImage from "../../../../assets/Pimage.png";
import Loader from "react-loader-spinner";
import mpesaLogo from "../../../../assets/M-PESA_LOGO.png";
import profiles from "../../../../assets/profile.png";
import Moments from "moment-timezone";
import axios from "axios";
import orderFormFunction from "../../../../components/FormValidation/ordersForm";
import { RiDeleteBinLine } from "react-icons/ri";
import deleteSvg from "../../../../assets/icons/delete.svg";
import { AiOutlineClose } from "react-icons/ai";
import {BASE_API_} from '../../../../utils/base-url.config';
import Cookies from "js-cookie";

const OrderMobileDetails = ({ pparams, historyProps }) => {
  const {
    values,
    handleChange,
    getOrder,
    saveChange,
    handleSubmit,
    renderOrder,
    isSubmitting,
    successM
  } = orderFormFunction(pparams);
  useEffect(() => {
    getOrder(pparams, historyProps);
  }, [pparams, historyProps]);

  const [deleteSuccess, setDeleteSuccess] = useState("");
  const [loadDelete, setLoadDelete] = useState(false);
  const [deleteError, setDeleteError] = useState([]);
  const [ids, setIds] = useState("");
  const [deleteActive, setDeleteActive] = useState(false);

  const deleteOrder = (id) => {
    setLoadDelete(true);
     const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/orders/v1/removeOrder/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        setDeleteSuccess("Order Deleted successfully");
        setLoadDelete(false);
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        setDeleteError(arrayError);
        setLoadDelete(false);
      });
  };

  const activateDelete = (identity) => {
    setDeleteActive(true);
    setIds(identity);
    //this.setState({ deleteActive: true, ids: identity });
  };

  const closeDelete = () => {
    setDeleteActive(false);
  };

  let creationDate = renderOrder.creationDate;

  let dates = Moments(creationDate).format("LLLL");

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <HeadWrap>
          <TitleWrap>
            <TitleHead>Order Details</TitleHead>
          </TitleWrap>
        </HeadWrap>

        {successM && (
          <SuccessP>{successM}</SuccessP>
        )}
        
        <SelectWrap>
          <SelectionWrapper>
            <SelectionContainer>
              <StatusWrap>
                <select
                  name="shippingStatus"
                  value={values.shippingStatus}
                  onChange={handleChange}
                >
                  <option>Change Status</option>
                  <option value="shipped">Shipped</option>
                  <option value="ready to ship">Ready to ship</option>
                  <option value="Cancelled">Cancelled</option>
                </select>
              </StatusWrap>

              {saveChange && !isSubmitting ? (
                <EditBtn type="submit">Save</EditBtn>
              ) : null}
              {isSubmitting && (
                <EditBtnL1>
                  {" "}
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={20}
                    width={20}
                  />
                </EditBtnL1>
              )}
            </SelectionContainer>

            <DeleteIcon onClick={() => activateDelete(renderOrder._id)}>
              <RiDeleteBinLine />
            </DeleteIcon>
          </SelectionWrapper>

          {saveChange && !isSubmitting ? (
            <EditBtn1 type="submit">Save</EditBtn1>
          ) : null}
          {isSubmitting && (
            <EditBtnL2>
              {" "}
              <Loader type="TailSpin" color="white" height={20} width={20} />
            </EditBtnL2>
          )}
        </SelectWrap>

        <CustomerContainer>
          {renderOrder.userId && (
            <CustomerWrapper>
              <CHeadWrap>
                <Ctitle>Customer</Ctitle>
                <CProfile
                  to={`/dashboard/customers/view/${renderOrder.userId._id}`}
                >
                  View Profile
                </CProfile>
              </CHeadWrap>

              <ProfileWrapper>
                <Profile>
                  {renderOrder.profile ? (
                    <img
                      src={`${BASE_API_}${renderOrder.profile.path}`}
                      alt=""
                    />
                  ) : (
                    <img src={profiles} alt="NoImage" />
                  )}
                </Profile>

                <Ptitle>
                  {renderOrder.userId.firstname} {renderOrder.userId.lastname}
                </Ptitle>
                <PEmail>{renderOrder.userId.email}</PEmail>
                <PPhone>{renderOrder.userId.phonenumber}</PPhone>
              </ProfileWrapper>

              <AddressWrapper>
                <AddContainer>
                  <DeliverWrap>
                    <h1>Deliver to:</h1>
                    {renderOrder.shipping.courier && (
                      <p>{renderOrder.shipping.courier.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentName && (
                      <p>{renderOrder.shipping.agentName.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentLocation && (
                      <p> {renderOrder.shipping.agentLocation},</p>
                    )}
                    <p>{renderOrder.shipping.cityName} </p>
                    <p></p>
                    <p>{renderOrder.shipping.country}</p>
                  </DeliverWrap>
                  <DeliverWrap></DeliverWrap>
                </AddContainer>
              </AddressWrapper>
            </CustomerWrapper>
          )}

          {renderOrder.guestId && (
            <CustomerWrapper>
              <CHeadWrap>
                <Ctitle>Customer</Ctitle>
                <CProfile
                  to={`/dashboard/customers/view/${renderOrder.guestId._id}`}
                >
                  View Profile
                </CProfile>
              </CHeadWrap>

              <ProfileWrapper>
                <Profile>
                  <img src={profiles} alt="" />
                </Profile>
                <Ptitle>
                  {renderOrder.guestId.firstname} {renderOrder.guestId.lastname}
                </Ptitle>
                <PEmail>{renderOrder.guestId.email}</PEmail>
                <PPhone>{renderOrder.guestId.phonenumber}</PPhone>
              </ProfileWrapper>

              <AddressWrapper>
                <AddContainer>
                  <DeliverWrap>
                    <h1>Deliver to:</h1>
                    {renderOrder.shipping.courier && (
                      <p>{renderOrder.shipping.courier.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentName && (
                      <p>{renderOrder.shipping.agentName.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentLocation && (
                      <p> {renderOrder.shipping.agentLocation},</p>
                    )}
                    <p>{renderOrder.shipping.cityName} </p>
                    <p></p>
                    <p>{renderOrder.shipping.country}</p>
                  </DeliverWrap>
                  <DeliverWrap></DeliverWrap>
                </AddContainer>
              </AddressWrapper>
            </CustomerWrapper>
          )}
        </CustomerContainer>

        <OContainer>
          <OrderWrapper>
            <OrderContainer>
              <ODateWrap>
                <Dates>
                  <h1>{dates}</h1>
                  <h5>ID #{renderOrder.orderCode}</h5>
                </Dates>
              </ODateWrap>

              <OProductWrap>
                {renderOrder.products && (
                  <div>
                    {renderOrder.products.map((data, index) => {
                      let productname = data.name;
                      let Id = data.product_id || "";
                      if (Object.keys(Id).length > 5) {
                        Id =
                          "..." + data.product_id.split("").slice(13).join("");
                      }
                      return (
                        <ProductContent>
                          <PImage>
                            {data.imageId ? (
                              <img
                                src={`${BASE_API_}${data.imageId.path}`}
                                alt=""
                              />
                            ) : (
                              <img src={testImage} alt="" />
                            )}

                            <TextContent>
                              <h1>{productname}</h1>
                              <h5>Product ID #{Id}</h5>
                              {data.colortype && (
                                <p>Colortype: {data.colortype}</p>
                              )}
                              {data.size && <p>size: {data.size}</p>}
                            </TextContent>
                          </PImage>

                          <PQuantity>
                            <input
                              type="number"
                              value={data.quantity}
                              name="number"
                            />
                          </PQuantity>

                          <Ptext>X</Ptext>

                          <Pprice>
                            <input value={data.price} />
                          </Pprice>

                          <PSubtotal>
                            <h1>Total</h1>
                            <p>{data.subtotal} kes</p>
                          </PSubtotal>
                        </ProductContent>
                      );
                    })}
                  </div>
                )}
              </OProductWrap>

              <TotalsWrap>
                <TSubtotal>
                  <h1>Subtotal</h1> <p>{renderOrder.subtotal} Kes</p>
                </TSubtotal>
                <TShipping>
                  <h1>Shipping Cost</h1> <p>{renderOrder.shippingPrice} Kes</p>
                </TShipping>

                <Ttotal>
                  <h1>Total</h1> <p>{renderOrder.total} Kes</p>
                </Ttotal>

                <PayButtonWrap>
                  {renderOrder.paymentStatus === "Paid" ? (
                    <PayButton>Payment made</PayButton>
                  ) : (
                    <NotPayButton>Payment not made</NotPayButton>
                  )}
                </PayButtonWrap>
              </TotalsWrap>
            </OrderContainer>

            <PayInfoWrap>
              <NotesSection>
                <label>Notes</label>
                <textarea
                  placeholder="Type something.."
                  name="notes"
                  value={values.notes}
                  onChange={handleChange}
                />
              </NotesSection>
              <PayInfo>
                <PayWrap>
                  <h1>Payment Info :</h1>

                  <Mnumber>
                    <img src={mpesaLogo} alt="" />
                    <p>+254 ---***.**438</p>
                  </Mnumber>
                  <Bname>
                    <h1>Business name:</h1>
                    <p>Safaricom PLC</p>
                  </Bname>
                  <Paybill>
                    <h1>paybill:</h1>
                    <p>7708822</p>
                  </Paybill>
                </PayWrap>
              </PayInfo>
            </PayInfoWrap>
          </OrderWrapper>
        </OContainer>
      </form>

      {deleteActive && (
        <DeleteContainer>
          <DeleteWrapper>
            <DeleteTitle>
              <h5>Delete this Order?</h5>
              <CloseIcon onClick={closeDelete}>
                <AiOutlineClose />
              </CloseIcon>{" "}
            </DeleteTitle>

            {!loadDelete && !deleteSuccess && (
              <div>
                {deleteError.length !== 0 && deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].response}</DeleteErrors>
                )}
                {deleteError.length !== 0 && !deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].message}</DeleteErrors>
                )}

                <DeletesWrap>
                  <img src={deleteSvg} alt="delete" />
                </DeletesWrap>
                <DeleteText>
                  You’ll lose all this product details. We can’t recover them
                  when deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to permanently delete this order?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={closeDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn onClick={() => deleteOrder(ids)}>Yes</YesBtn>
                </ButtonWrap>
              </div>
            )}

            {loadDelete && (
              <LoadingWrapper>
                <Loader type="TailSpin" color="black" height={50} width={50} />
              </LoadingWrapper>
            )}

            {deleteSuccess && !loadDelete && (
              <LoadingWrapper>
                <DeleteMessageWrap>
                  <DeleteMessage>{deleteSuccess}</DeleteMessage>
                  <CloseButton onClick={closeDelete}>Close</CloseButton>
                </DeleteMessageWrap>
              </LoadingWrapper>
            )}
          </DeleteWrapper>
        </DeleteContainer>
      )}
    </div>
  );
};

export default OrderMobileDetails;
