import React from "react";
import {
  TitleHead,
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  StatusContent,
  YellowC,
  TItems,
  TCustomers,
  TCost,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  DeleteErrors,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  OrderId,
  ViewButton,
  Payment,
  OrdersWrapper,
  OrdersLatestWrapper,
  StatsContainer,
  StatsTotalWrap,
  ContentTextContainer,
  ContentTextContainer2,
  ContentTextWrap,
  Totalnum,
  StatsSummaryWrap,
  StatsTableWrap,
  TableTitle,
  TableContainer,
  StatsLineWrap,
  TableWrap,
  TableContent1,
  TableContent2,
  MainContainer,
  Lines,
  TotalSumWrap,
  LineGraph,
} from "./styles";

import numeral from "numeral";
import { AiOutlineClose, AiOutlineRight } from "react-icons/ai";
import { BsSearch } from "react-icons/bs";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";

import axios from "axios";
import Loader from "react-loader-spinner";
import { BASE_API_ } from "../../../../utils/base-url.config";
import { Line } from "react-chartjs-2";
import Cookies from "js-cookie";
import { Tooltip } from "@chakra-ui/react";

class AllMobileOrders extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      cancelOrder: false,
      cancelSuccess: "",
      cancelError: [],
      loadDelete: false,
      loadCancel: false,
      deleteSuccess: "",
      deleteError: [],
      searchActive: false,
      orderSlist: [],
      currentS: 1,
      orderSTotal: 0,
      allsetOrders: [],
      statusQuery: "",
      paidQuery: "",
    };
  }

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");
  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetOrders.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      orderSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.searchAllOrders;
    const resultData = allProducts.filter((orders) => {
      let username;
      if (orders.userId) {
        let userFirstname = orders.userId.firstname;
        let userLastname = orders.userId.lastname;

        username = userLastname + " " + userFirstname;
      }

      if (orders.guestId) {
        let userFirstname = orders.guestId.firstname;
        let userLastname = orders.guestId.lastname;

        username = userLastname + " " + userFirstname;
      }

      let dateCreated =
        orders.dayCreated +
        "." +
        orders.monthCreated +
        "." +
        orders.yearCreated;
      let dates = dateCreated;
      let customerName = username.toLowerCase();
      if (customerName.includes(query.toLowerCase())) {
        return customerName.includes(query.toLowerCase());
      } else if (dates.includes(query)) {
        return dates.includes(query);
      } else {
        return null;
      }
    });
    const totalOrders = resultData.length;

    let showOrders = resultData;

    sessionStorage.setItem(
      "orders",
      JSON.stringify(this.props.searchAllOrders)
    );
    let currentPage = 1;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      orderSlist: currentItems,
      currentS: 1,
      orderSTotal: totalOrders,
      allsetOrders: showOrders,
      results: showOrders,
      loading: true,
      searchActive: true,
    });
  };

  //changing selection for form
  handleSelectorWrap = (e) => {
    if (this.state.statusQuery || this.state.paidQuery) {
      if (e.target.name === "paidQuery") {
        let allProducts;
        if (!this.state.statusQuery) {
          allProducts = this.props.searchAllOrders;
        } else {
          allProducts = this.state.orderSlist;
        }
        const resultData = allProducts.filter((orders) => {
          if (orders.paymentStatus === e.target.value) {
            return orders;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.searchAllOrders)
        );

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          paidQuery: e.target.value,
          orderSlist: currentItems,
          currentS: 1,
          orderSTotal: totalProducts,
          allsetOrders: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      } else if (e.target.name === "statusQuery") {
        let allProducts;
        if (!this.state.paidQuery) {
          allProducts = this.props.searchAllOrders;
        } else {
          allProducts = this.state.orderSlist;
        }

        const resultData = allProducts.filter((orders) => {
          if (orders.shippingStatus === e.target.value) {
            return orders;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.searchAllOrders)
        );

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          statusQuery: e.target.value,
          orderSlist: currentItems,
          currentS: 1,
          orderSTotal: totalProducts,
          allsetOrders: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      }
    }
    //if none is avaliable
    else {
      if (e.target.name === "paidQuery") {
        const allProducts = this.props.searchAllOrders;
        const resultData = allProducts.filter((orders) => {
          if (orders.paymentStatus === e.target.value) {
            return orders;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;
        let showProducts = resultData;

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          paidQuery: e.target.value,
          orderSlist: currentItems,
          currentS: 1,
          orderSTotal: totalProducts,
          allsetOrders: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.searchAllOrders)
        );
      } else if (e.target.name === "statusQuery") {
        const allProducts = this.props.searchAllOrders;
        const resultData = allProducts.filter((orders) => {
          if (orders.shippingStatus === e.target.value) {
            return orders;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.searchAllOrders)
        );

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          statusQuery: e.target.value,
          orderSlist: currentItems,
          currentS: 1,
          orderSTotal: totalProducts,
          allsetOrders: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      }
    }
  };

  //refresh back to default
  refreshItems = () => {
    this.setState({ statusQuery: "", paidQuery: " ", searchActive: false });
  };

  closeDelete = () => {
    this.setState({ deleteActive: false, cancelOrder: false });
  };
  closeCancel = () => {
    this.setState({ cancelActive: false, cancelOrder: false });
  };
  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };
  activateCancelOrder = (id) => {
    this.setState({ cancelOrder: true, deleteActive: false, ids: id });
  };

  deleteOrder = (id) => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/orders/v1/removeOrder/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          deleteSuccess: "Order Deleted successfully",
          loadDelete: false,
        });
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadDelete: false });
      });
  };

  cancelOrder = (id) => {
    this.setState({ loadCancel: true });
    let values = { shippingStatus: "cancelled" };
    const getToken = Cookies.get("_tk");
    axios
      .patch(`${BASE_API_}/orders/v1/updateOrder/${id}`, values, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          cancelSuccess: "Order Cancelled successfully",
          loadCancel: false,
        });
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ cancelError: arrayError, loadCancel: false });
      });
  };

  renderOrders = () => {
    return this.props.orders.map((data, index) => {
      let username;
      let productname = data.products[0].name;
      if (data.userId) {
        let userFirstname = data.userId.firstname;
        let userLastname = data.userId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (data.guestId) {
        let userFirstname = data.guestId.firstname;
        let userLastname = data.guestId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (Object.keys(productname).length > 15) {
        productname = productname.slice(0, 15) + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>OrderID: {data.orderCode}</OrderId>

              <ViewButton to={`/dashboard/orders/view/${data._id}`}>
                <h1>View more</h1>
                <div>
                  <AiOutlineRight />
                </div>
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>

              <Payment
                className={data.paymentStatus === "Paid" ? "paid" : null}
              >
                {data.paymentStatus}
              </Payment>
            </TDate>

            <TCustomers>
              <h1>Customer</h1>

              <h2>{username}</h2>
            </TCustomers>

            <TItems>
              <h1>Products</h1>

              <h2>{productname}</h2>
            </TItems>

            <TCost>
              <h1>Total</h1>

              <h2>{numeral(data.total).format("0,00")}</h2>
            </TCost>

            <TStatus>
              <h1>Delivery Status</h1>
              <StatusContent>
                {data.shippingStatus === "waiting for payment" && (
                  <YellowC color="#FF5035" />
                )}
                {data.shippingStatus === "ready to ship" && (
                  <YellowC color="#ffbf00" />
                )}
                {data.shippingStatus === "shipped" && <YellowC color="green" />}
                <p>{data.shippingStatus}</p>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  //renderSearched Orders
  renderSearchOrders = () => {
    return this.state.orderSlist.map((data, index) => {
      let username;
      let productname = data.products[0].name;
      if (data.userId) {
        let userFirstname = data.userId.firstname;
        let userLastname = data.userId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (data.guestId) {
        let userFirstname = data.guestId.firstname;
        let userLastname = data.guestId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (Object.keys(productname).length > 15) {
        productname = productname.slice(0, 15) + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>OrderID: {data.orderCode}</OrderId>

              <ViewButton to={`/dashboard/orders/view/${data._id}`}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>

              <Payment
                className={(data.paymentStatus = "Paid" ? "paid" : null)}
              >
                {data.paymentStatus}
              </Payment>
            </TDate>

            <TCustomers>
              <h1>Customer</h1>

              <h2>{username}</h2>
            </TCustomers>

            <TItems>
              <h1>Products</h1>

              <h2>{productname}</h2>
            </TItems>

            <TCost>
              <h1>Total</h1>

              <h2>{numeral(data.total).format("0,00")}</h2>
            </TCost>

            <TStatus>
              <h1>Delivery Status</h1>
              <StatusContent>
                {data.shippingStatus === "waiting for payment" && (
                  <YellowC color="#FF5035" />
                )}
                {data.shippingStatus === "ready to ship" && (
                  <YellowC color="#ffbf00" />
                )}
                {data.shippingStatus === "shipped" && <YellowC color="green" />}
                <p>{data.shippingStatus}</p>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  render() {
    return (
      <MainContainer>
        <TitleHead>Orders</TitleHead>

        <SelectionWrapper>
          <SearchWrapper>
            <form onSubmit={this.onFormSubmit}>
              <input
                type="text"
                name="query"
                value={this.state.query}
                onChange={this.handleInputChange}
                placeholder="Search customer / date(dd.mm.yy)"
              />
              <button>
                {" "}
                <BsSearch />
              </button>
            </form>
          </SearchWrapper>
        </SelectionWrapper>

        <StatsContainer>
          <StatsTotalWrap>
            <ContentTextContainer>
              <ContentTextWrap>
                <h1>Fulfilled Orders</h1>
                <Totalnum>{this.props.paysummary.fulfilled || 0}</Totalnum>
              </ContentTextWrap>
            </ContentTextContainer>

            <ContentTextContainer>
              <ContentTextWrap>
                <h1>Unfulfilled Orders</h1>
                <Totalnum>{this.props.paysummary.unfulfilled || 0}</Totalnum>
              </ContentTextWrap>
            </ContentTextContainer>
          </StatsTotalWrap>

          <StatsSummaryWrap>
            <StatsTableWrap>
              <TableTitle>Top Selling Products</TableTitle>
              {this.props.TopSelling && (
                <>
                  {this.props.TopSelling.map((data, index) => {
                    let productname = data.name;
                    if (Object.keys(productname).length > 15) {
                      productname = productname.slice(0, 15) + "...";
                    }

                    let productsku = data.sku || "";
                    if (Object.keys(productsku).length > 15) {
                      productsku = productsku.slice(0, 15) + "...";
                    }
                    return (
                      <TableContainer key={index}>
                        <TableWrap>
                          <TableContent1>
                            <h1>SKU</h1>

                            <h2>{productsku}</h2>
                          </TableContent1>

                          <TableContent2>
                            <h1>Product name</h1>

                            <h2>{productname}</h2>
                          </TableContent2>

                          <TableContent1>
                            <h1>Sales</h1>

                            <h2>{data.quantity}</h2>
                          </TableContent1>

                          <TableContent2>
                            <h1>Total</h1>

                            <Tooltip
                              hasArrow
                              label={`${
                                numeral(data.subtotal).format("0,00") || 0
                              } KES`}
                              bg="gray.600"
                              color="white"
                              fontSize="md"
                            >
                              <h2>
                                {data.subtotal > 100000
                                  ? this.ShortTotals(data.subtotal) || 0
                                  : numeral(data.subtotal).format("0,00")}
                                KES
                              </h2>
                            </Tooltip>
                          </TableContent2>
                        </TableWrap>
                      </TableContainer>
                    );
                  })}
                </>
              )}
            </StatsTableWrap>

            <StatsTotalWrap>
              <ContentTextContainer2>
                <Tooltip
                  hasArrow
                  label={`${
                    numeral(this.props.paysummary.overallSales).format(
                      "0,00"
                    ) || 0
                  } KES`}
                  bg="gray.600"
                  color="white"
                  fontSize="sm"
                >
                  <ContentTextWrap>
                    <h1>Overall Revenue</h1>
                    <Totalnum>
                      {this.props.paysummary.overallSales > 800000
                        ? this.ShortTotals(
                            this.props.paysummary.overallSales
                          ) || 0
                        : numeral(this.props.paysummary.overallSales).format(
                            "0,00"
                          )}{" "}
                      KES
                    </Totalnum>
                  </ContentTextWrap>
                </Tooltip>
              </ContentTextContainer2>

              <ContentTextContainer2>
                <Tooltip
                  hasArrow
                  label={`${
                    numeral(this.props.paysummary.averageSales).format(
                      "0,00"
                    ) || 0
                  } KES`}
                  bg="gray.600"
                  color="white"
                  fontSize="md"
                >
                  <ContentTextWrap>
                    <h1>Average Sales</h1>
                    <Totalnum>
                      {this.props.paysummary.averageSales > 800000
                        ? this.ShortTotals(
                            this.props.paysummary.averageSales
                          ) || 0
                        : numeral(this.props.paysummary.averageSales).format(
                            "0,00"
                          )}{" "}
                      KES
                    </Totalnum>
                  </ContentTextWrap>
                </Tooltip>
              </ContentTextContainer2>
            </StatsTotalWrap>

            <StatsLineWrap>
              <TotalSumWrap>
                <h1>Total Orders this month</h1>

                <Tooltip
                  hasArrow
                  label={`${
                    numeral(this.props.ordersMonth.totals).format("0,00") || 0
                  } `}
                  bg="gray.600"
                  color="white"
                  fontSize="md"
                >
                  <h2>
                    {this.props.ordersMonth.totals > 1000000
                      ? this.ShortTotals(this.props.ordersMonth.totals) || 0
                      : numeral(this.props.ordersMonth.totals).format("0,00")}
                  </h2>
                </Tooltip>
              </TotalSumWrap>

              <LineGraph>
                <Line
                  className="line"
                  data={{
                    labels: this.props.ordersMonth.orderDates,
                    datasets: [
                      {
                        label: "orders",
                        data: this.props.ordersMonth.countDates,
                        fill: false,
                        backgroundColor: "rgba(75,192,192,0.2)",
                        borderColor: "#991D27",
                        pointBorderColor: "#991D27",
                        pointBackgroundColor: "#991D27",
                        pointBorderWidth: 2,
                      },
                    ],
                  }}
                  options={{
                    maintainAspectRatio: false,
                    legend: {
                      display: false,
                      position: "left",
                      onClick: null,
                    },

                    responsive: true,

                    scales: {
                      xAxes: [
                        {
                          display: true,
                          ticks: {
                            display: false,
                            beginAtZero: false,
                          },
                          gridLines: {
                            offsetGridLines: true,
                            display: false,
                            drawBorder: true,
                            borderDash: [6, 2],
                          },
                        },
                      ],
                      yAxes: [
                        {
                          type: "linear",
                          display: true,
                          gridLines: {
                            display: true,
                            drawBorder: false,
                            borderDash: [6, 2],
                            tickMarkLength: 1,
                          },
                          ticks: {
                            display: true,
                            beginAtZero: false,
                          },
                        },
                      ],
                    },
                  }}
                />
              </LineGraph>
            </StatsLineWrap>
          </StatsSummaryWrap>
        </StatsContainer>
        <TitleHead className="title">Orders</TitleHead>
        <Lines />
        <OrderContainer>
          <OrdersWrapper>
            <OrdersLatestWrapper>
              <LatestWrapper>
                {this.state.searchActive ? (
                  <div>
                    {this.state.orderSlist && (
                      <div>{this.renderSearchOrders()}</div>
                    )}
                  </div>
                ) : (
                  <div>
                    {this.props.orders && <div>{this.renderOrders()}</div>}
                  </div>
                )}
              </LatestWrapper>
            </OrdersLatestWrapper>
          </OrdersWrapper>

          {this.state.searchActive ? (
            <Pagination2
              totalOrders={this.state.orderSTotal}
              ordersPerPage={this.props.perPage}
              current={this.state.currentS}
              paramslinks={this.props.pparams}
              historyProps={this.props.historyProps}
              changesearchPage={this.changesearchPage}
            />
          ) : (
            <Pagination
              totalOrders={this.props.totalOrders}
              ordersPerPage={this.props.perPage}
              current={this.props.current}
              paramslinks={this.props.pparams}
              historyProps={this.props.historyProps}
            />
          )}

          {this.state.cancelOrder && (
            <DeleteContainer>
              <DeleteWrapper>
                <DeleteTitle>
                  <h5>Cancel this Order? </h5>
                  <CloseIcon onClick={this.closeCancel}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                {!this.state.loadCancel && !this.state.cancelSuccess && (
                  <div>
                    {this.state.cancelError.length !== 0 &&
                      this.state.cancelError[0].response && (
                        <DeleteErrors>
                          {this.state.cancelError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.cancelError.length !== 0 &&
                      !this.state.cancelError[0].response && (
                        <DeleteErrors>
                          {this.state.cancelError[0].message}
                        </DeleteErrors>
                      )}
                    <DeleteText>
                      Are you sure you want to <h5>cancel</h5> this customer's
                      order?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeCancel}>Cancel</CancelBtn>{" "}
                      <YesBtn onClick={() => this.cancelOrder(this.state.ids)}>
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadCancel && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.cancelSuccess && !this.state.loadCancel && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.cancelSuccess}</DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContainer>
          )}
        </OrderContainer>
      </MainContainer>
    );
  }
}

export default AllMobileOrders;
