import React from "react";
import { Wrapper } from "./style";

import Products from "../ProductlistComponents/Products";
import ViewProduct from "../ProductlistComponents/ViewProduct";
import UpdateProduct from "../ProductlistComponents/UpdateProduct";
import CreateProduct from "../ProductlistComponents/Create";
import MobileViewProduct from "../../Mobile/components/ProductsController/ViewProducts";
import MobileUpdateProduct from '../../Mobile/components/ProductsController/UpdateProducts';
import MobileCreate from '../../Mobile/components/ProductsController/CreateProduct'
import { connect } from "react-redux";
import { fetchProducts, fetchCategories } from "../../store/actions";
import Cookies from "js-cookie";

class index extends React.Component {
  state = { page: undefined, perPage: 8, totalProducts: 0, avproducts: [] };
  componentDidMount() {
    //console.log(window.readyState);

    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      Cookies.set("page", page);

      const ProductFetching = async () => {
        await this.props.fetchProducts(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
        sessionStorage.setItem(
          "products",
          JSON.stringify(this.props.products.products),
          {
            expires: 1,
          }
        );
        this.setState({
          page: page,
          avproducts: this.props.products.products,
        });
      };

      ProductFetching();
    } else {
      const ProductFetching = async () => {
        await this.props.fetchProducts(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );

        sessionStorage.setItem(
          "products",
          JSON.stringify(this.props.products.products),
          {
            expires: 1,
          }
        );
        this.setState({
          page: page,
          avproducts: this.props.products.products,
        });
      };

      ProductFetching();
    }

    this.props.fetchCategories();
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.products.totalProducts !== prevProps.products.totalProducts
    ) {
      this.setState({
        totalProducts: this.props.products.totalProducts,
      });
    }

    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      if (this.props.pparams.path !== "/dashboard/:title/:specific/:id") {
        const getToken = Cookies.get("_tk");
        let page;
        if (this.props.searchProps.search) {
          page = this.props.searchProps.search.split("").slice(3).join("");

          const ProductFetching = async () => {
            await this.props.fetchProducts(
              page,
              this.state.perPage,
              this.redirect,
              getToken
            );

            sessionStorage.setItem(
              "products",
              JSON.stringify(this.props.products.products)
            );

            this.setState({
              page: page,
              avproducts: this.props.products.products,
            });
          };

          ProductFetching();
        } else {
          const ProductFetching = async () => {
            await this.props.fetchProducts(
              page,
              this.state.perPage,
              this.redirect,
              getToken
            );

            sessionStorage.setItem(
              "products",
              JSON.stringify(this.props.products.products)
            );
            this.setState({
              page: page,
              avproducts: this.props.products.products,
            });
          };

          ProductFetching();
        }
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  changeOuterComponent = () => {
    if (this.props.pparams.url === "/dashboard/productlist") {
      return (
        <Products
          mobileActive={this.props.mobileActive}
          pparams={this.props.pparams}
          products={this.props.products.products}
          totalProducts={this.props.products.totalProducts}
          searchAllProducts={this.props.products.searchAll}
          current={this.props.products.current}
          perPage={this.state.perPage}
          historyProps={this.props.historyProps}
          searchProps={this.props.searchProps}
          categories={this.props.categories}
        />
      );
    } else if (this.props.pparams.path === "/dashboard/:title/:specific") {
      if (this.props.pparams.params.specific === "grid") {
        return (
          <Products
            mobileActive={this.props.mobileActive}
            pparams={this.props.pparams}
            products={this.props.products.products}
            searchAllProducts={this.props.products.searchAll}
            totalProducts={this.props.products.totalProducts}
            current={this.props.products.current}
            perPage={this.state.perPage}
            historyProps={this.props.historyProps}
            searchProps={this.props.searchProps}
            categories={this.props.categories}
          />
        );
      } else if (this.props.pparams.params.specific === "create") {
        if (this.props.mobileActive === true) {
          return <MobileCreate />;
        } else {
          return <CreateProduct />;
        }
      }
    } else if (this.props.pparams.path === "/dashboard/:title/:specific/:id") {
      if (this.props.pparams.params.specific === "view") {
        if (this.props.mobileActive === true) {
          return (
            <MobileViewProduct
              products={this.props.products.products}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
              categories={this.props.categories}
            />
          );
        } else {
          return (
            <ViewProduct
              products={this.props.products.products}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
              categories={this.props.categories}
            />
          );
        }
      } else if (this.props.pparams.params.specific === "update") {
        if (this.props.mobileActive === true) {
          return (
            <MobileUpdateProduct
              categories={this.props.categories}
              products={this.props.products.products}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        } else {
          return (
            <UpdateProduct
              categories={this.props.categories}
              products={this.props.products.products}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        }
      }
    }
  };
  render() {
    return <Wrapper>{this.changeOuterComponent()}</Wrapper>;
  }
}

const mapStateToProps = (state) => {
  return {
    products: state.products,
    categories: state.categories,
  };
};

export default connect(mapStateToProps, { fetchProducts, fetchCategories })(
  index
);
