import React from "react";
import {
  EditBtn,
  TitleHead,
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TId,
  OrderId,
  ViewButton,
  TDate,
  TStatus,
  StatusContent,
  TItems,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  DeleteErrors,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  CreateWrap,
  CreateContainer,
  CreateWrapper,
  CreateTitle,
  CreateErrors,
  CreateText,
  CreateMessageWrap,
  CreateMessage,
  CreateBtn,
  ControlButtons,
  DeleteBtn,
  OrdersWrapper,
  OrdersLatestWrapper,
  DeletesWrap,
  CreateTextWrap,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";
import { AiOutlineClose, AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine } from "react-icons/ri";
import { BsSearch } from "react-icons/bs";
import deleteSvg from "../../../../assets/icons/delete.svg";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import axios from "axios";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import { fetchAgents } from "../../../../store/actions";
import listImage from "../../../../assets/icons/NoList.svg";
import { BASE_API_ } from "../../../../utils/base-url.config";
import Cookies from "js-cookie";

class MobileAgentDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      loadDelete: false,
      deleteSuccess: "",
      deleteError: [],
      createActive: false,
      location: "",
      agentLocation: "",
      pickUpAgent: "",
      createError: [],
      loadCreate: false,
      createSuccess: "",
      page: undefined,
      perPage: 8,
      totalProducts: 0,
      errors: {},
      ids: "",
      itemSlist: [],
      currentS: 1,
      itemSTotal: null,
      allsetItems: [],
      searchActive: false,
      deleteLocation: false,
      loadLocationDelete: false,
      pageLoading: false,
    };
  }

  componentDidMount() {
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
      }
    };

    let page;
    let id = this.props.pparams.params.id;
    const getToken = Cookies.get("_tk");

    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getDetails = async () => {
        await this.props.fetchAgents(
          page,
          this.state.perPage,
          id,
          this.redirect,
          getToken
        );

        this.setState({ location: id });
      };

      getDetails();
    } else {
      const getDetails = async () => {
        await this.props.fetchAgents(
          page,
          this.state.perPage,
          id,
          this.redirect,
          getToken
        );

        this.setState({ location: id });
      };

      getDetails();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      let page;
      let id = this.props.pparams.params.id;
      const getToken = Cookies.get("_tk");
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const getDetails = async () => {
          await this.props.fetchAgents(
            page,
            this.state.perPage,
            id,
            this.redirect,
            getToken
          );

          this.setState({ location: id });
        };

        getDetails();
      } else {
        const getDetails = async () => {
          await this.props.fetchAgents(
            page,
            this.state.perPage,
            id,
            this.redirect,
            getToken
          );

          this.setState({ location: id });
        };

        getDetails();
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetItems.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      itemSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.agents.allAgents;

    const resultData = allProducts.filter((agent) => {
      let agentlocation = agent.agentLocation.toLowerCase();

      let pickupAgent = agent.pickUpAgent.toLowerCase();

      if (agentlocation.includes(query.toLowerCase())) {
        return agentlocation.includes(query.toLowerCase());
      } else if (pickupAgent.includes(query.toLowerCase())) {
        return pickupAgent.includes(query.toLowerCase());
      } else {
        return null;
      }
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPage = 1;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      itemSlist: currentItems,
      currentS: 1,
      itemSTotal: totalOrders,
      allsetItems: showOrders,
      results: showOrders,
      loading: true,
      searchActive: true,
    });
  };

  activateCreate = () => {
    this.setState({
      createActive: true,
      deleteActive: false,
      cancelOrder: false,
    });
  };

  closeDelete = () => {
    this.setState({
      deleteActive: false,
      cancelOrder: false,
      createActive: false,
    });
  };

  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };

  activateLocationDelete = () => {
    this.setState({ deleteLocation: true });
  };

  closeLocationDelete = () => {
    this.setState({
      deleteLocation: false,
      cancelOrder: false,
      createActive: false,
    });
  };

  handleChange = (e) => {
    if (e.target.name === "agentLocation") {
      if (e.target.value) {
        this.setState({
          errors: {},
        });
      }
    }
    if (e.target.name === "pickUpAgent") {
      if (e.target.value) {
        this.setState({
          errors: {},
        });
      }
    }
    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    if (
      this.state.agentLocation &&
      this.state.location &&
      this.state.pickUpAgent &&
      Object.keys(this.state.errors).length === 0
    ) {
      let values = {
        agentLocation: this.state.agentLocation,
        location: this.state.location,
        pickUpAgent: this.state.pickUpAgent,
      };
      const getToken = Cookies.get("_tk");
      axios
        .post(`${BASE_API_}/shipping/v1/agentlocation/create`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          this.setState({
            createSuccess: "Agent Created successfully",
            loadCreate: false,
          });
          window.location.reload();
        })
        .catch((error) => {
          let arrayError = [];
          arrayError.push(error);
          this.setState({ createError: arrayError, loadCreate: false });
        });
    } else {
      if (!this.state.agentLocation) {
        this.setState({
          errors: {
            agentLocation: "please insert location",
          },
        });
      }
      if (!this.state.pickUpAgent) {
        this.setState({
          errors: {
            pickUpAgent: "please insert pickUpAgent",
          },
        });
      }
    }
  };

  deleteOrder = (id) => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/shipping/v1/removeAgent/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          deleteSuccess: "Agent Deleted successfully",
          loadDelete: false,
        });
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadDelete: false });
      });
  };

  removingLocation = (id) => {
    this.setState({ loadLocationDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/shipping/v1/removeLocation/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          deleteSuccess: "Location Deleted successfully",
          loadLocationDelete: false,
        });
        window.location.replace("/dashboard/shipping");
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadLocationDelete: false });
      });
  };

  renderOrders = () => {
    return this.props.agents.agents.map((data, index) => {
      let agentlocation;
      let namesCombined = data.agentLocation;
      let pickupAgent;

      if (Object.keys(namesCombined).length > 15) {
        agentlocation = namesCombined.slice(0, 15) + "...";
      } else {
        agentlocation = namesCombined;
      }

      if (Object.keys(data.pickUpAgent).length > 15) {
        pickupAgent = data.pickUpAgent.slice(0, 15) + "...";
      } else {
        pickupAgent = data.pickUpAgent;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>
                <h1>AgentID: {Id}</h1>{" "}
                <TDate>
                  <div>
                    <h1>Date Created :</h1>
                    <h5>
                      {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
                    </h5>
                  </div>
                </TDate>
              </OrderId>

              <ViewButton>
                <div onClick={() => this.activateDelete(data._id)}>
                  <RiDeleteBinLine />
                </div>
              </ViewButton>
            </TId>

            <TItems>
              <h1>Agent Location</h1>

              <h2>{agentlocation}</h2>
            </TItems>

            <TStatus>
              <h1>Pick-Up Agent</h1>
              <StatusContent>
                <p>{pickupAgent}</p>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  renderSearchItems = () => {
    return this.state.itemSlist.map((data, index) => {
      let agentlocation;
      let namesCombined = data.agentLocation;
      let pickupAgent;

      if (Object.keys(namesCombined).length > 15) {
        agentlocation = namesCombined.slice(0, 15) + "...";
      } else {
        agentlocation = namesCombined;
      }

      if (Object.keys(data.pickUpAgent).length > 15) {
        pickupAgent = data.pickUpAgent.slice(0, 15) + "...";
      } else {
        pickupAgent = data.pickUpAgent;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>
                <h1>AgentID: {Id}</h1>{" "}
                <TDate>
                  <div>
                    <h1>Date Created :</h1>
                    <h5>
                      {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
                    </h5>
                  </div>
                </TDate>
              </OrderId>

              <ViewButton>
                <div onClick={() => this.activateDelete(data._id)}>
                  <RiDeleteBinLine />
                </div>
              </ViewButton>
            </TId>

            <TItems>
              <h1>Agent Location</h1>

              <h2>{agentlocation}</h2>
            </TItems>

            <TStatus>
              <h1>Pick-Up Agent</h1>
              <StatusContent>
                <p>{pickupAgent}</p>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };
  render() {
    return (
      <>
        <TitleHead>{this.props.agents.mainLocation}</TitleHead>

        <OrderContainer>
          <SelectionWrapper>
            <SearchWrapper>
              <form onSubmit={this.onFormSubmit}>
                <input
                  type="text"
                  name="query"
                  value={this.state.query}
                  onChange={this.handleInputChange}
                  placeholder="Search agent location / pick-up agent "
                />
                <button>
                  {" "}
                  <BsSearch />
                </button>
              </form>
            </SearchWrapper>

            <ControlButtons>
              <EditBtn onClick={this.activateCreate}>
                <AiOutlinePlus />
              </EditBtn>
              <DeleteBtn onClick={this.activateLocationDelete}>
                <RiDeleteBinLine />
              </DeleteBtn>
            </ControlButtons>
          </SelectionWrapper>

          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.agents.agents.length > 0 ? (
                <>
                  <OrdersWrapper>
                    <OrdersLatestWrapper>
                      <LatestWrapper>
                        {this.state.searchActive ? (
                          <>
                            {this.state.itemSlist && (
                              <div>{this.renderSearchItems()}</div>
                            )}
                          </>
                        ) : (
                          <>
                            {this.props.agents.agents && (
                              <div>{this.renderOrders()}</div>
                            )}
                          </>
                        )}
                      </LatestWrapper>
                    </OrdersLatestWrapper>
                  </OrdersWrapper>

                  {this.state.searchActive ? (
                    <Pagination2
                      totalItems={this.state.itemSTotal}
                      itemPerPage={this.state.perPage}
                      current={this.state.currentS}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      changesearchPage={this.changesearchPage}
                    />
                  ) : (
                    <Pagination
                      totalItems={this.props.agents.totalAgents}
                      itemPerPage={this.state.perPage}
                      current={this.props.agents.current}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      link={`/dashboard/shipping/agent/${this.state.location}`}
                    />
                  )}
                </>
              ) : (
                <PContainer bg={"white"} className="Nolist">
                  <NoListWrapper>
                    <NoListContainer>
                      <img src={listImage} alt="NoList" />
                      <h1>List is Empty</h1>
                      <h5>Please create items on the list.</h5>
                    </NoListContainer>
                  </NoListWrapper>
                </PContainer>
              )}
            </>
          )}

          {this.state.createActive && (
            <CreateWrap>
              <CreateContainer>
                <CreateWrapper>
                  <CreateTitle>
                    {!this.state.loadCreate && !this.state.createSuccess ? (
                      <h5>Create New Agent? </h5>
                    ) : (
                      <h5>Successful </h5>
                    )}
                    <CloseIcon onClick={this.closeDelete}>
                      <AiOutlineClose />
                    </CloseIcon>{" "}
                  </CreateTitle>

                  {!this.state.loadCreate && !this.state.createSuccess && (
                    <div className="creates">
                      {this.state.createError.length !== 0 &&
                        this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].response}
                          </CreateErrors>
                        )}
                      {this.state.createError.length !== 0 &&
                        !this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].message}
                          </CreateErrors>
                        )}
                      <form onSubmit={this.handleSubmit}>
                        <CreateTextWrap>
                          <CreateText>
                            <label>Agent Location*</label>
                            <input
                              type="text"
                              name="agentLocation"
                              value={this.state.agentLocation}
                              onChange={this.handleChange}
                            />
                          </CreateText>

                          <CreateText>
                            <label>Pick -Up Agent*</label>

                            <input
                              type="text"
                              name="pickUpAgent"
                              value={this.state.pickUpAgent}
                              onChange={this.handleChange}
                            />
                          </CreateText>
                        </CreateTextWrap>

                        <ButtonWrap>
                          <CancelBtn onClick={this.closeDelete}>
                            Cancel
                          </CancelBtn>{" "}
                          <CreateBtn type="submit">Yes</CreateBtn>
                        </ButtonWrap>
                      </form>
                    </div>
                  )}

                  {this.state.loadCreate && (
                    <LoadingWrapper>
                      <Loader
                        type="TailSpin"
                        color="black"
                        height={50}
                        width={50}
                      />
                    </LoadingWrapper>
                  )}

                  {this.state.createSuccess && !this.state.loadCreate && (
                    <LoadingWrapper>
                      <CreateMessageWrap>
                        <CreateMessage>
                          {this.state.createSuccess}
                        </CreateMessage>
                        <CreateMessage>
                          this popup will be closed automatically.
                        </CreateMessage>
                        <CloseButton onClick={this.closeDelete}>
                          Close
                        </CloseButton>
                      </CreateMessageWrap>
                    </LoadingWrapper>
                  )}
                </CreateWrapper>
              </CreateContainer>
            </CreateWrap>
          )}

          {this.state.deleteActive && (
            <DeleteContainer>
              <DeleteWrapper>
                <DeleteTitle>
                  {!this.state.loadDelete && !this.state.deleteSuccess ? (
                    <h5>Delete this Agent ? </h5>
                  ) : (
                    <h5>Successful </h5>
                  )}
                  <CloseIcon onClick={this.closeDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                {!this.state.loadDelete && !this.state.deleteSuccess && (
                  <div>
                    {this.state.deleteError.length !== 0 &&
                      this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.deleteError.length !== 0 &&
                      !this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].message}
                        </DeleteErrors>
                      )}

                    <DeletesWrap>
                      <img src={deleteSvg} alt="delete" />
                    </DeletesWrap>
                    <DeleteText>
                      You’ll lose all this agent. We can’t recover them when
                      deleted.
                    </DeleteText>
                    <DeleteText>
                      Are you sure you want to permanently delete this agent?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                      <YesBtn onClick={() => this.deleteOrder(this.state.ids)}>
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadDelete && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.deleteSuccess && !this.state.loadDelete && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContainer>
          )}

          {this.state.deleteLocation && (
            <DeleteContainer>
              <DeleteWrapper>
                <DeleteTitle>
                  <h5>Delete this Location? </h5>
                  <CloseIcon onClick={this.closeLocationDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                {!this.state.loadLocationDelete && !this.state.deleteSuccess && (
                  <div>
                    {this.state.deleteError.length !== 0 &&
                      this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.deleteError.length !== 0 &&
                      !this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].message}
                        </DeleteErrors>
                      )}
                    <DeletesWrap>
                      <img src={deleteSvg} alt="delete" />
                    </DeletesWrap>
                    <DeleteText>
                      You’ll lose all this Location details. We can’t recover
                      them when deleted.
                    </DeleteText>
                    <DeleteText>
                      Are you sure you want to permanently delete this Location?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeLocationDelete}>
                        Cancel
                      </CancelBtn>{" "}
                      <YesBtn
                        onClick={() =>
                          this.removingLocation(this.state.location)
                        }
                      >
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadLocationDelete && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.deleteSuccess && !this.state.loadLocationDelete && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContainer>
          )}
        </OrderContainer>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    agents: state.agents,
  };
};

export default connect(mapStateToProps, { fetchAgents })(MobileAgentDetails);
