/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  OrderContainer,
  Wrapper,
  Container,
  ProfileWrapper,
  FormContent,
  Profile,
  FormContent1,
} from "./styles";

import user from "../../../../assets/profile.png";
import CustomerFormActions from "../../../../components/FormValidation/customerForm";
import {BASE_API_} from '../../../../utils/base-url.config';

const ViewCustomer = ({ customers, Id }) => {
  const { values, getInfo } = CustomerFormActions();

  useEffect(() => {
    const getdetails = async () => {
      getInfo(customers, Id);
    };
    getdetails();
  }, [customers]);

  return (
    <OrderContainer>
      <Wrapper>
        <Container>
          <form>
            <FormContent>
              <label htmlFor="firstname">First name</label>
              <input
                type="text"
                name="firstname"
                value={values.firstname}
                readOnly
                id="firstname"
              />
            </FormContent>
            <FormContent>
              <label htmlFor="lastname">last name</label>
              <input
                id="lastname"
                type="text"
                name="lastname"
                value={values.lastname}
                readOnly
              />
            </FormContent>

            <FormContent>
              <label htmlFor="email">Email</label>
              <input
                id="email"
                type="email"
                name="email"
                value={values.email}
                readOnly
              />
            </FormContent>
            <FormContent>
              <label htmlFor="phonenumber">Phone number</label>
              <input
                id="phonenumber"
                type="text"
                value={values.phonenumber}
                name="phonenumber"
                readOnly
              />
            </FormContent>

            <FormContent1>
              <label>Address</label>
              <input
                type="text"
                value={values.address}
                name="address"
                readOnly
              />
              <input type="text" value={values.city} name="city" readOnly />
              <input
                type="text"
                value={values.country}
                name="country"
                readOnly
              />
            </FormContent1>
          </form>

          <ProfileWrapper>
            <Profile>
              {values.profile ? (
                <img
                  className="default"
                  src={`${BASE_API_}${values.profile.path}`}
                  alt="profile"
                />
              ) : (
                <img className="default" src={user} alt="default-profile" />
              )}
            </Profile>
          </ProfileWrapper>
        </Container>
      </Wrapper>
    </OrderContainer>
  );
};

export default ViewCustomer;
