/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import {
  TitleHead,
  TitleWrap,
  HeadWrap,
  EditBtn,
  OContainer,
  OrderWrapper,
  CustomerWrapper,
  OrderContainer,
  PayInfoWrap,
  ODateWrap,
  OTitleWrap,
  OProductWrap,
  TotalsWrap,
  StatusWrap,
  Dates,
  ProductContent,
  TextContent,
  PImage,
  PQuantity,
  Pprice,
  PSubtotal,
  TSubtotal,
  TShipping,
  Ttotal,
  PayButton,
  PayButtonWrap,
  NotesSection,
  PayInfo,
  Mnumber,
  Bname,
  Paybill,
  PayWrap,
  CHeadWrap,
  Ctitle,
  CProfile,
  ProfileWrapper,
  Profile,
  Ptitle,
  PEmail,
  PPhone,
  AddressWrapper,
  DeliverWrap,
  AddContainer,
  EditBtn2,
  NotPayButton,
  SuccessP,
  EditBtnL1,
  DownloadWrapper,
  DownloadContainer,
  DownloadsButton,
  DownloadBtnWrapper,
  CloseWrap,
  RButton,
  ButtonHeadWrap,
} from "./styles";
import Cookies from "js-cookie";

import { Box, Heading, Text, VStack, useToast } from "@chakra-ui/react";

import testImage from "../../../assets/default.png";
import mpesaLogo from "../../../assets/M-PESA_LOGO.png";
import profiles from "../../../assets/profile.png";
import Moments from "moment-timezone";
import Loader from "react-loader-spinner";
import orderFormFunction from "../../FormValidation/ordersForm";
import ReactToPdf from "react-to-pdf";
import { BASE_API_ } from "../../../utils/base-url.config";
import axios from "axios";

const OrderDetails = ({ pparams, historyProps }) => {
  const toast = useToast();
  const {
    values,
    handleChange,
    getOrder,
    saveChange,
    handleSubmit,
    renderOrder,
    successM,
    isSubmitting,
  } = orderFormFunction(pparams);
  useEffect(() => {
    getOrder(pparams, historyProps);
  }, []);
  const [download, setDownload] = useState(false);
  const [emailloading, setEmailLoading] = useState(false);

  const ref = React.createRef();

  let creationDate = renderOrder.creationDate;

  let dates = Moments(creationDate).format("LLLL");
  const setsDownload = () => {
    setDownload(true);
  };
  const ClosesDownload = () => {
    setDownload(false);
  };

  //resend email
  const getToken = Cookies.get("_tk");
  const resendemail = (id) => {
    setEmailLoading(true);
    axios
      .get(`${BASE_API_}/orders/v1/resendorder/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then(() => {
        toast({
          position: "top",
          title: "Order resend successfully",
          status: "success",
          duration: 10000,
          isClosable: true,
        });
        setEmailLoading(false);
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);

        let message;
        if (errorArray.length !== 0 && errorArray[0].response) {
          message = errorArray[0].response.data;
        } else if (errorArray.length !== 0 && !errorArray[0].response) {
          message = errorArray[0].message;
        }
        toast({
          position: "top",
          title: message,
          status: "error",
          duration: 10000,
          isClosable: true,
        });
        setEmailLoading(false);
      });
  };
  return (
    <div>
      <form onSubmit={handleSubmit}>
        {successM && <SuccessP>{successM}</SuccessP>}
        <HeadWrap>
          <TitleWrap>
            <TitleHead>Order Details</TitleHead>

            <ButtonHeadWrap>
              {saveChange && !isSubmitting && (
                <EditBtn type="submit">Save</EditBtn>
              )}

              {!saveChange && !isSubmitting && <EditBtn2>Save</EditBtn2>}

              {isSubmitting && (
                <EditBtnL1>
                  {" "}
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={20}
                    width={20}
                  />
                </EditBtnL1>
              )}
              <RButton onClick={setsDownload}>Receipt</RButton>

              {emailloading ? (
                <Box
                  style={{
                    marginLeft: "20px",
                    width: "150px",
                    height: "43.85px",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    color: "white",
                    background: "#991d27",
                    cursor: "pointer",
                  }}
                >
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={20}
                    width={20}
                  />
                </Box>
              ) : (
                <Box
                  style={{
                    marginLeft: "20px",
                    width: "150px",
                    height: "43.85px",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    color: "white",
                    background: "#991d27",
                    cursor: "pointer",
                  }}
                  onClick={() => resendemail(renderOrder._id)}
                >
                  Resend Email
                </Box>
              )}
            </ButtonHeadWrap>
          </TitleWrap>
        </HeadWrap>

        <OContainer>
          <OrderWrapper>
            <OrderContainer>
              <ODateWrap>
                <Dates>
                  <h1>{dates}</h1>
                  <h5>ID #{renderOrder.orderCode}</h5>
                </Dates>

                <StatusWrap>
                  <select
                    name="shippingStatus"
                    value={values.shippingStatus}
                    onChange={handleChange}
                  >
                    <option>Change Status</option>
                    <option value="shipped">Shipped</option>
                    <option value="ready to ship">Ready to ship</option>
                    <option value="Cancelled">Cancelled</option>
                  </select>
                </StatusWrap>
              </ODateWrap>

              <OTitleWrap>
                <h1>Product</h1>
                <h1>Quantity</h1>
                <h1>Unit Price</h1>
                <h1>Subtotal</h1>
              </OTitleWrap>

              <OProductWrap>
                {renderOrder.products && (
                  <div>
                    {renderOrder.products.map((data, index) => {
                      let productname = data.name;
                      let Id = data.product_id || "";

                      if (Object.keys(Id).length > 5) {
                        Id = "..." + Id.split("").slice(13).join("");
                      }
                      return (
                        <ProductContent>
                          <PImage>
                            {data.imageId ? (
                              <img
                                src={`${BASE_API_}${data.imageId.path}`}
                                alt=""
                              />
                            ) : (
                              <img src={testImage} alt="" />
                            )}

                            <TextContent>
                              <h1>{productname}</h1>
                              <h5>Product ID #{Id}</h5>
                              {data.colortype && (
                                <p>Colortype: {data.colortype}</p>
                              )}
                              {data.size && <p>size: {data.size}</p>}
                            </TextContent>
                          </PImage>

                          <PQuantity>
                            <input
                              type="number"
                              value={data.quantity}
                              name="number"
                            />
                          </PQuantity>

                          <Pprice>
                            <input value={data.price} />
                          </Pprice>

                          <PSubtotal>
                            <p>{data.subtotal} kes</p>
                          </PSubtotal>
                        </ProductContent>
                      );
                    })}
                  </div>
                )}
              </OProductWrap>

              <TotalsWrap>
                <TSubtotal>
                  <h1>Subtotal</h1> <p>{renderOrder.subtotal} Kes</p>
                </TSubtotal>
                <TShipping>
                  <h1>Shipping Cost</h1> <p>{renderOrder.shippingPrice} Kes</p>
                </TShipping>

                <Ttotal>
                  <h1>Total</h1> <p>{renderOrder.total} Kes</p>
                </Ttotal>

                <PayButtonWrap>
                  {renderOrder.paymentStatus === "Paid" ? (
                    <PayButton>Payment made</PayButton>
                  ) : (
                    <NotPayButton>Payment not made</NotPayButton>
                  )}
                </PayButtonWrap>
              </TotalsWrap>
            </OrderContainer>

            <PayInfoWrap>
              <NotesSection>
                <label>Notes</label>
                <textarea
                  placeholder="Type something.."
                  name="notes"
                  value={values.notes}
                  onChange={handleChange}
                />
              </NotesSection>
              <PayInfo>
                <PayWrap>
                  <h1>Payment Info :</h1>

                  <Mnumber>
                    <img src={mpesaLogo} alt="" />
                    <p>+254 ---***.**438</p>
                  </Mnumber>
                  <Bname>
                    <h1>Business name:</h1>
                    <p>Safaricom PLC</p>
                  </Bname>
                  <Paybill>
                    <h1>paybill:</h1>
                    <p>7708822</p>
                  </Paybill>
                </PayWrap>
              </PayInfo>
            </PayInfoWrap>
          </OrderWrapper>

          {renderOrder.userId && (
            <CustomerWrapper>
              <CHeadWrap>
                <Ctitle>Customer</Ctitle>
                <CProfile
                  to={`/dashboard/customers/view/${renderOrder.userId._id}`}
                >
                  View Profile
                </CProfile>
              </CHeadWrap>

              <ProfileWrapper>
                <Profile>
                  {renderOrder.profile ? (
                    <img
                      src={`${BASE_API_}${renderOrder.profile.path}`}
                      alt=""
                    />
                  ) : (
                    <img src={profiles} alt="" />
                  )}
                </Profile>

                <Ptitle>
                  {renderOrder.userId.firstname} {renderOrder.userId.lastname}
                </Ptitle>
                <PEmail>{renderOrder.userId.email}</PEmail>
                <PPhone>{renderOrder.userId.phonenumber}</PPhone>
              </ProfileWrapper>

              <AddressWrapper>
                <AddContainer>
                  <DeliverWrap>
                    <h1>Deliver to:</h1>
                    {renderOrder.shipping.courier && (
                      <p>{renderOrder.shipping.courier.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentName && (
                      <p>{renderOrder.shipping.agentName.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentLocation && (
                      <p> {renderOrder.shipping.agentLocation},</p>
                    )}
                    <p>{renderOrder.shipping.cityName} </p>
                    <p></p>
                    <p>{renderOrder.shipping.country}</p>
                  </DeliverWrap>
                  <DeliverWrap></DeliverWrap>
                </AddContainer>
              </AddressWrapper>
            </CustomerWrapper>
          )}

          {renderOrder.guestId && (
            <CustomerWrapper>
              <CHeadWrap>
                <Ctitle>Customer</Ctitle>
                <CProfile
                  to={`/dashboard/customers/view/${renderOrder.guestId._id}`}
                >
                  View Profile
                </CProfile>
              </CHeadWrap>

              <ProfileWrapper>
                <Profile>
                  <img src={profiles} alt="" />
                </Profile>
                <Ptitle>
                  {renderOrder.guestId.firstname} {renderOrder.guestId.lastname}
                </Ptitle>
                <PEmail>{renderOrder.guestId.email}</PEmail>
                <PPhone>{renderOrder.guestId.phonenumber}</PPhone>
              </ProfileWrapper>

              <AddressWrapper>
                <AddContainer>
                  <DeliverWrap>
                    <h1>Deliver to:</h1>
                    {renderOrder.shipping.courier && (
                      <p>{renderOrder.shipping.courier.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentName && (
                      <p>{renderOrder.shipping.agentName.toLowerCase()},</p>
                    )}
                    {renderOrder.shipping.agentLocation && (
                      <p> {renderOrder.shipping.agentLocation},</p>
                    )}
                    <p>{renderOrder.shipping.cityName} </p>
                    <p></p>
                    <p>{renderOrder.shipping.country}</p>
                  </DeliverWrap>
                  <DeliverWrap></DeliverWrap>
                </AddContainer>
              </AddressWrapper>
            </CustomerWrapper>
          )}
        </OContainer>
      </form>

      {download && (
        <DownloadWrapper>
          <DownloadContainer ref={ref}>
            <Box width="100%" p="20px">
              <Heading>CLASS AND SASS</Heading>
              {renderOrder.guestId && (
                <VStack my="1rem" alignItems="flex-start">
                  <Text>
                    <b>Customer :</b> {renderOrder?.guestId?.firstname}{" "}
                    {renderOrder?.guestId?.lastname}
                  </Text>
                  <Text>
                    {" "}
                    <b>Contact :</b> {renderOrder?.guestId?.phonenumber}
                  </Text>
                </VStack>
              )}

              {renderOrder.userId && (
                <VStack my="1rem" alignItems="flex-start">
                  <Text>
                    Customer : {renderOrder?.userId?.firstname}{" "}
                    {renderOrder?.guestId?.lastname}
                  </Text>
                  <Text>Contact : {renderOrder?.userId?.phonenumber}</Text>
                </VStack>
              )}
            </Box>
            <OrderContainer>
              <ODateWrap>
                <Dates>
                  <h1>{dates}</h1>
                  <h5>ID #{renderOrder.orderCode}</h5>
                </Dates>
              </ODateWrap>

              <OTitleWrap>
                <h1>Product</h1>
                <h1>Quantity</h1>
                <h1>Unit Price</h1>
                <h1>Subtotal</h1>
              </OTitleWrap>

              <OProductWrap>
                {renderOrder.products && (
                  <div>
                    {renderOrder.products.map((data, index) => {
                      let productname = data.name;
                      let Id = data.product_id || "";

                      if (Object.keys(Id).length > 5) {
                        Id = "..." + Id.split("").slice(13).join("");
                      }
                      return (
                        <ProductContent>
                          <PImage>
                            {data.imageId ? (
                              <img
                                src={`${BASE_API_}${data.imageId.path}`}
                                alt=""
                              />
                            ) : (
                              <img src={testImage} alt="" />
                            )}

                            <TextContent>
                              <h1>{productname}</h1>
                              <h5>Product ID #{Id}</h5>
                              {data.colortype && (
                                <p>Colortype: {data.colortype}</p>
                              )}
                              {data.size && <p>size: {data.size}</p>}
                            </TextContent>
                          </PImage>

                          <PQuantity>
                            <p>{data.quantity}</p>
                          </PQuantity>

                          <Pprice>
                            <p>{data.price}</p>
                          </Pprice>

                          <PSubtotal>
                            <p>{data.subtotal} kes</p>
                          </PSubtotal>
                        </ProductContent>
                      );
                    })}
                  </div>
                )}
              </OProductWrap>

              <TotalsWrap>
                <TSubtotal>
                  <h1>Subtotal</h1> <p>{renderOrder.subtotal} Kes</p>
                </TSubtotal>
                <TShipping>
                  <h1>Shipping Cost</h1> <p>{renderOrder.shippingPrice} Kes</p>
                </TShipping>

                <Ttotal>
                  <h1>Total</h1> <p>{renderOrder.total} Kes</p>
                </Ttotal>

                <PayButtonWrap>
                  {renderOrder.paymentStatus === "Paid" ? (
                    <PayButton className="payMade">Payment made</PayButton>
                  ) : (
                    <NotPayButton className="notPay">
                      Payment not made
                    </NotPayButton>
                  )}
                </PayButtonWrap>
              </TotalsWrap>
            </OrderContainer>
          </DownloadContainer>
          <DownloadBtnWrapper>
            <ReactToPdf
              targetRef={ref}
              filename={`OrderCode_${renderOrder.orderCode}.pdf`}
            >
              {({ toPdf }) => (
                <DownloadsButton onClick={toPdf}>Download</DownloadsButton>
              )}
            </ReactToPdf>

            <CloseWrap onClick={ClosesDownload}>Close</CloseWrap>
          </DownloadBtnWrapper>
        </DownloadWrapper>
      )}
    </div>
  );
};

export default OrderDetails;
