import React from "react";
import {
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TId,
  OrderId,
  ViewButton,
  TDate,
  TCustomers,
  TStatus,
  StatusContent,
  YellowC,
  NotActiveC,
  TItems,
  TCost,
 
} from "./styles";
import numeral from "numeral";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import axios from "axios";
import {BASE_API_} from '../../../../utils/base-url.config';
import Cookies from "js-cookie";

//import Loader from "react-loader-spinner";

import {  AiOutlineRight } from "react-icons/ai";
class ListMobileContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      deleteMessage: "",
      deleteError: [],
      loadDelete: false,
      ids: "",
      query: "",
      searchActive: false,
      productSlist: [],
      currentS: null,
      productSTotal: null,
      current: this.props.current,
    };
  }

  componentDidMount() {
    this.setState({ productlists: this.props.productlist });
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchActive !== prevProps.searchActive) {
      this.setState({ productlists: this.props.productSlist });
    }
  }

  closeDelete = () => {
    this.setState({ deleteActive: false });
  };

  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };

  deleteActionAccepted = () => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/products/v1/delete/${this.state.ids}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ deleteMessage: success.data, loadDelete: false });
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ loadDelete: false });
        this.setState({ deleteError: arrayError });
      });
  };

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");

  renderProducts = () => {
    return this.props.productlist.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = "..." + data._id.split("").slice(13).join("");
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>ProductID: {Id}</OrderId>

              <ViewButton to={`/dashboard/productlist/view/${data._id}`}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Product name</h1>

              <h2>{pName}</h2>
            </TCustomers>

            <TItems>
              <h1>Category</h1>

              <h2>{data.category.name}</h2>
            </TItems>

            <TCost>
              <h1>Price</h1>

              <h2>{numeral(data.price).format("0,00")}</h2>
            </TCost>

            <TStatus>
              <h1> Status</h1>
              <StatusContent>
                {data.active ? (
                  <div>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </div>
                ) : (
                  <div>
                    <NotActiveC />
                    <p>Not Active</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>

            <TCost>
              <h1>Stock</h1>

              <h2>{data.totalStock}</h2>
            </TCost>

            <TStatus>
              <h1>Stock Status</h1>
              <StatusContent>
                {data.totalStock > data.reservedStock ? (
                  <div>
                    <p>In Stock</p>
                  </div>
                ) : (
                  <div>
                    <p className="OutStock">Out of Stock</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  //rendering searchProducts
  renderSearchProducts = () => {
    return this.props.productSlist.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = "..." + data._id.split("").slice(13).join("");
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>ProductID: {Id}</OrderId>

              <ViewButton to={`/dashboard/productlist/view/${data._id}`}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Product name</h1>

              <h2>{pName}</h2>
            </TCustomers>

            <TItems>
              <h1>Category</h1>

              <h2>{data.category.name}</h2>
            </TItems>

            <TCost>
              <h1>Price</h1>

              <h2>{numeral(data.price).format("0,00")}</h2>
            </TCost>

            <TStatus>
              <h1> Status</h1>
              <StatusContent>
                {data.active ? (
                  <div>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </div>
                ) : (
                  <div>
                    <NotActiveC />
                    <p>Not Active</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>

            <TCost>
              <h1>Stock</h1>

              <h2>{data.totalStock}</h2>
            </TCost>

            <TStatus>
              <h1>Stock Status</h1>
              <StatusContent>
                {data.totalStock > data.reservedStock ? (
                  <div>
                    <p>In Stock</p>
                  </div>
                ) : (
                  <div>
                    <p className="OutStock">Out of Stock</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  render() {
    return (
      <>
        <LatestWrapper>
          {this.props.searchActive
            ? this.renderSearchProducts()
            : this.renderProducts()}
        </LatestWrapper>

        {this.props.searchActive ? (
          <Pagination2
            totalProducts={this.props.productSTotal}
            productsPerPage={this.props.perPage}
            current={this.props.currentS}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
            changesearchPage={this.props.changesearchPage}
          />
        ) : (
          <Pagination
            totalProducts={this.props.totalProducts}
            productsPerPage={this.props.perPage}
            current={this.props.current}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
          />
        )}
      </>
    );
  }
}

export default ListMobileContent;
