import React from "react";
import {
  Wrapper,
  Container,
  TitleHead,
  TotalsWrapper,
  TotalOrders,
  TotalCustomers,
  TotalProducts,
  SummaryWrapper,
  OrderSummaryWrap,
  SalesSummaryWrap,
  OrdersWrapper,
  ImageWrap,
  ContentTextWrap,
  Totalnum,
  OrdersContainer,
  OrderTitleWrap,
  SelectionWrapper,
  SelectContent,
  OrdersTContainer,
  TOrdersContent,
  Totals,
  SalesContainer,
  SalesTitleWrap,
  SalesGraphWrap,
  SalesSelectionWrapper,
  OrdersTitleWrapper,
  OrdersLatestWrapper,
  LatestWrapper,
  LatestHeadWrap,
  LatestHeadContainer,
  LId,
  LDate,
  LStatus,
  LItems,
  LCustomers,
  LCountry,
  LCost,
  LAction,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  TItems,
  TCustomers,
  TCountry,
  TCost,
  TAction,
  StatusContent,
  YellowC,
  NoListWrapper,
  NoListContainer,
} from "./style";
import numeral from "numeral";
import { connect } from "react-redux";
import DropDown from "./ODropdown";
import orderImage from "../../assets/OrdersIcon.svg";
import customerImage from "../../assets/CustomerIcon.svg";
import productImage from "../../assets/ProductsIcon.svg";
import listImage from "../../assets/icons/NoList.svg";
import {
  fetchOrders,
  fetchTotals,
  fetchActiveSummary,
  fetchActiveSales,
} from "../../store/actions";
import Chart from "chart.js";

import { Bar } from "react-chartjs-2";
import Cookies from 'js-cookie'

Chart.defaults.global.legend.display = true;
Chart.defaults.global.legend.onClick = null;
Chart.defaults.global.legend.position = "top";
Chart.defaults.global.tooltips.mode = "index";
Chart.defaults.global.scales = {
  xAxes: [
    {
      ticks: {
        display: true,
      },
      gridLines: {
        display: false,
        drawBorder: false,
      },
    },
  ],
  yAxes: [
    {
      type: "linear",
      ticks: {
        display: true,
      },
      gridLines: {
        display: false,
        drawBorder: false,
      },
    },
  ],
};
class HomeSection extends React.Component {
  state = {
    activeSelect: "today",
    page: undefined,
    perPage: 4,
    graphDate: "monthly",
    labels: [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sept",
      "Oct",
      "Nov",
      "Dec",
    ],
  };
  componentDidMount() {
    const getToken = Cookies.get("_tk");

    if (!this.props.mobileActive) {
      let page;
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const getOrders = async () => {
          await this.props.fetchTotals(this.redirect, getToken);
          await this.props.fetchOrders(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
          await this.props.fetchActiveSummary(
            this.state.activeSelect,
            this.redirect,
            getToken
          );
          await this.props.fetchActiveSales(
            this.state.graphDate,
            this.redirect,
            getToken
          );
        };

        getOrders();
      } else {
        const getOrders = async () => {
          await this.props.fetchTotals(this.redirect, getToken);
          await this.props.fetchOrders(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
          await this.props.fetchActiveSummary(
            this.state.activeSelect,
            this.redirect,
            getToken
          );
          await this.props.fetchActiveSales(
            this.state.graphDate,
            this.redirect,
            getToken
          );
        };

        getOrders();
      }
    }
  }

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  //date selection on Summary
  handleDateSelection = (e) => {
    let selection = e.target.id;

    this.setState({ activeSelect: e.target.id });

    // let selection = e.target.value;
    const getSummaries = async () => {
      const getToken = Cookies.get("_tk");
      await this.props.fetchActiveSummary(selection, this.redirect, getToken);
    };
    getSummaries();
  };

  //Graph changes
  handleOnChangeGraph = (e) => {
    if (e.target.value === "weekly") {
      this.setState({
        labels: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        graphDate: "weekly",
      });
    } else if (e.target.value === "monthly") {
      this.setState({
        labels: [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sept",
          "Oct",
          "Nov",
          "Dec",
        ],
        graphDate: "monthly",
      });
    }
    let selection = e.target.value;

    const fetchSales = async () => {
      const getToken = Cookies.get("_tk");
      await this.props.fetchActiveSales(selection, this.redirect, getToken);
    };

    fetchSales();
  };

  options = {
    legend: {
      display: true,
      position: "left",
      onClick: null,
    },
    responsive: true,
    maintainAspectRatio: true,
    tooltips: {
      mode: "label",
    },
    scales: {
      xAxes: [
        {
          ticks: {
            display: false,
          },
          gridLines: {
            display: false,
            drawBorder: false,
          },
        },
      ],
      yAxes: [
        {
          type: "linear",
          ticks: {
            display: false,
          },
          gridLines: {
            display: false,
            drawBorder: false,
          },
        },
      ],
    },
  };

  renderOrders = () => {
    return this.props.orders.orders.map((data, index) => {
      let username;
      let productname = data.products[0].name;
      if (data.userId) {
        let userFirstname = data.userId.firstname;
        let userLastname = data.userId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (data.guestId) {
        let userFirstname = data.guestId.firstname;
        let userLastname = data.guestId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (Object.keys(productname).length > 15) {
        productname = productname.slice(0, 15) + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>{data.orderCode}</TId>
            <TDate>
              {" "}
              {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
            </TDate>
            <TStatus>
              <StatusContent>
                {data.shippingStatus === "waiting for payment" && <YellowC />}
                {data.shippingStatus === "ready to ship" && <YellowC />}
                {data.shippingStatus === "shipped" && <YellowC />}
                <p>{data.shippingStatus}</p>
              </StatusContent>
            </TStatus>
            <TItems>{productname}</TItems>

            <TCustomers>{username}</TCustomers>

            <TCountry>{data.shipping.country}</TCountry>
            <TCost>{this.ShortTotals(data.total)}</TCost>
            <TAction>
              <DropDown
                activateDelete={this.activateDelete}
                activateCancel={this.activateCancelOrder}
                ids={data._id}
              />
            </TAction>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };
  render() {
    return (
      <Wrapper>
        <Container>
          <TitleHead>Dashboard</TitleHead>
          <TotalsWrapper>
            <TotalOrders>
              <ImageWrap>
                <img src={orderImage} alt="" />
              </ImageWrap>
              <ContentTextWrap>
                <h1>Total Orders</h1>
                <Totalnum>{this.props.totals.totalOrders || 0}</Totalnum>
              </ContentTextWrap>
            </TotalOrders>

            <TotalCustomers>
              <ImageWrap>
                <img src={customerImage} alt="" />
              </ImageWrap>
              <ContentTextWrap>
                <h1>Total Customers</h1>
                <Totalnum>{this.props.totals.totalCustomers || 0}</Totalnum>
              </ContentTextWrap>
            </TotalCustomers>

            <TotalProducts>
              <ImageWrap>
                <img src={productImage} alt="" />
              </ImageWrap>
              <ContentTextWrap>
                <h1>Total Products</h1>
                <Totalnum>{this.props.totals.totalProducts || 0}</Totalnum>
              </ContentTextWrap>
            </TotalProducts>
          </TotalsWrapper>

          <SummaryWrapper>
            <OrderSummaryWrap>
              <OrdersContainer>
                <OrderTitleWrap>
                  <h5>Order Summary</h5>
                  <SelectionWrapper>
                    <SelectContent
                      id="monthly"
                      names="monthly"
                      onClick={(e) => this.handleDateSelection(e)}
                      active={this.state.activeSelect}
                    >
                      <h5 id="monthly">Monthly</h5>
                    </SelectContent>

                    <SelectContent
                      id="weekly"
                      names="weekly"
                      onClick={(e) => this.handleDateSelection(e)}
                      active={this.state.activeSelect}
                    >
                      <h5 id="weekly">Weekly</h5>
                    </SelectContent>

                    <SelectContent
                      id="today"
                      names="today"
                      onClick={(e) => this.handleDateSelection(e)}
                      active={this.state.activeSelect}
                    >
                      <h5 id="today" value="today">
                        Today
                      </h5>
                    </SelectContent>
                  </SelectionWrapper>
                </OrderTitleWrap>

                <OrdersTContainer>
                  <TOrdersContent>
                    <h1>Total Orders</h1>
                    <Totals>{this.props.summary.Torders || 0}</Totals>
                  </TOrdersContent>

                  <TOrdersContent>
                    <h1>Total Sales</h1>
                    <Totals>{this.props.summary.Tsales || 0}</Totals>
                  </TOrdersContent>

                  <TOrdersContent>
                    <h1>Pending Orders</h1>
                    <Totals>{this.props.summary.Porders || 0}</Totals>
                  </TOrdersContent>

                  <TOrdersContent>
                    <h1>Delivered Orders</h1>
                    <Totals>{this.props.summary.Dorders || 0}</Totals>
                  </TOrdersContent>

                  <TOrdersContent className="bottomnull">
                    <h1>Cancelled Orders</h1>
                    <Totals>{this.props.summary.Corders || 0}</Totals>
                  </TOrdersContent>
                </OrdersTContainer>
              </OrdersContainer>
            </OrderSummaryWrap>

            <SalesSummaryWrap>
              <SalesContainer>
                <SalesTitleWrap>
                  <h5>Sales</h5>
                  <SalesSelectionWrapper>
                    <select
                      onChange={this.handleOnChangeGraph}
                      value={this.state.graphDate}
                    >
                      <option value="monthly">Monthly</option>
                      <option value="weekly">weekly</option>
                    </select>
                  </SalesSelectionWrapper>
                </SalesTitleWrap>

                <SalesGraphWrap>
                  {}
                  <Bar
                    data={{
                      labels: this.state.labels,

                      datasets: [
                        {
                          label: "sales",
                          backgroundColor: "#1D3899",
                          data: this.props.sales.sales,
                        },
                        {
                          label: "orders",
                          width: 20,
                          height: 20,
                          backgroundColor: "#7FE3D7",
                          data: this.props.sales.orders,
                        },
                      ],
                    }}
                    option={this.options}
                  />
                </SalesGraphWrap>
              </SalesContainer>
            </SalesSummaryWrap>
          </SummaryWrapper>

          {this.props.orders.orders.length > 0 ? (
            <OrdersWrapper>
              <OrdersLatestWrapper>
                <OrdersTitleWrapper>
                  <h5>Latest Orders</h5>
                </OrdersTitleWrapper>

                <LatestWrapper>
                  <LatestHeadWrap>
                    <LatestHeadContainer>
                      <LId>ID</LId>
                      <LDate>Date</LDate>
                      <LStatus>Status</LStatus>
                      <LItems>Items</LItems>
                      <LCustomers>Customer</LCustomers>
                      <LCountry>Country</LCountry>
                      <LCost>Cost</LCost>
                      <LAction>Action</LAction>
                    </LatestHeadContainer>
                  </LatestHeadWrap>

                  {this.props.orders.orders && <div>{this.renderOrders()}</div>}
                </LatestWrapper>
              </OrdersLatestWrapper>
            </OrdersWrapper>
          ) : (
            <OrdersWrapper>
              <OrdersLatestWrapper>
                <OrdersTitleWrapper>
                  <h5>Latest Orders</h5>
                </OrdersTitleWrapper>

                <NoListWrapper>
                  <NoListContainer>
                    <img src={listImage} alt="NoList" />
                    <h1>List is Empty</h1>
                    <h5>Please wait for the orders</h5>
                  </NoListContainer>
                </NoListWrapper>
              </OrdersLatestWrapper>
            </OrdersWrapper>
          )}
        </Container>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    orders: state.orders,
    totals: state.totals,
    summary: state.summary,
    sales: state.sales,
  };
};

export default connect(mapStateToProps, {
  fetchOrders,
  fetchTotals,
  fetchActiveSummary,
  fetchActiveSales,
})(HomeSection);
