/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  EditBtn,
  PContainer,
  MainWrapper,
  ProductDetailsWrap,
  FormContentWrap,
  ImageUploadWrapper,
  FormContentWrap1,
  FormContent,
  FormContentDesc,
  ImageTitle,
  ImageContainer,
  Image,
  ImageWrap,
  StatusWrapper,
  TagsWrapper,
  CategoriesWrapper,
  VisibilityWrapper,
  StatusContainer,
  TagsContainer,
  VContainer,
  ContentWrap,
  CatWrapper,
  ImageClick,
  FeaturedWrapper,
  FeaturedContainer,
  EditBtn2,
  FormError,
  FormSubmitError,
  Success,
  ImageRemove,
  ImageRemoveWrap,
  DeleteContainer,
  DeleteWrapper,
  DeleteErrors,
  DeleteTitle,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  ProductPriceWrap,
  ProductSize,
  SizeDisplay,
  ProductSizeTitle,
  CreateBtn2,
  ImageContainer2,
  ImageStockWrap,
  SKUWrap,
  ImageStock,
  TStockWrapper,
  TTStock,
  TTReserved,
  SideWrap2,
  SizeDisplayContent,
  Visible,
  DeletesWrap,
  ColorDelete,
} from "./styles";
import deleteSvg from "../../../../assets/icons/delete.svg";
import {  AiOutlinePlus } from "react-icons/ai";
import Loader from "react-loader-spinner";
import UpdateActions from "../../../../components/FormValidation/productUpdate";
import validateUpdate from "../../../../components/FormValidation/validateProduct";
import { FiUploadCloud } from "react-icons/fi";
import { RiDeleteBinLine } from "react-icons/ri";
import axios from "axios";
import {BASE_API_} from '../../../../utils/base-url.config'
import Cookies from "js-cookie";

const MobileUpdateProduct = ({
  categories,
  pparams,
  products,
  historyProps,
}) => {
  const [deleteActive, setDeleteActive] = useState(false);
  const [Identity, setIdentity] = useState("");
  const [ImageIdentity, setImageIdentity] = useState("");
  const [deleteError, setDeleteError] = useState([]);
  const [loadDelete, setLoadDelete] = useState(false);
  const [deleteSuccess, setDeleteSuccess] = useState("");

  const [deleteColorActive, setDeleteColorActive] = useState(false);
  const [ColorIdentity, setColorIdentity] = useState("");
  const [ImageColor, setImageColor] = useState("");
  const {
    errors,
    successMsg,
    submitError,
    values,
    handleChange,
    handleSubmit,
    imageHandler,
    imagesUrl,
    fetchProductData,
    uploadPercentage,
    oldUrls,
    changeActive,
    imageUploadError,
    isSubmitting,
    handleSubmit2,
    sizeHandler,
    size,
    colorHandler,
    skuHandler,
    stockHandler,
    oldColor,
    sku,
    stock,
    color,
    removeSize,
  } = UpdateActions(validateUpdate);

  useEffect(() => {
    let id = pparams.params.id;

    fetchProductData(id, historyProps);
  }, []);

  const removeImages = (prodId, ImageIds) => {
    setLoadDelete(true);
     const getToken = Cookies.get("_tk");

    axios
      .delete(`${BASE_API_}/products/v1/deleteImages/${prodId}/${ImageIds}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        setDeleteSuccess(success.data);
        setLoadDelete(false);
        sessionStorage.setItem("products", "[]");
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        setDeleteError(arrayError);
      });
  };

  const closeDelete = () => {
    setDeleteActive(false);
  };

  const activateDelete = (identity, imageId) => {
    setDeleteActive(true);
    setIdentity(identity);
    setImageIdentity(imageId);
  };

  const activateColorDelete = (identity, imageId) => {
    setDeleteColorActive(true);
    setColorIdentity(identity);
    setImageColor(imageId);
  };

  const removeColor = (prodId, color) => {
    setLoadDelete(true);
     const getToken = Cookies.get("_tk");

    axios
      .delete(`${BASE_API_}/products/v1/deleteColor/${prodId}/${color}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        setDeleteSuccess(success.data);
        setLoadDelete(false);
        sessionStorage.setItem("products", "[]");
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        setDeleteError(arrayError);
      });
  };

  const closeColorDelete = () => {
    setDeleteColorActive(false);
  };

  //total stock realtime
  const StockValue =
    Object.values(stock).length > 0 ? Object.values(stock) : [0];
  let totalStock =
    StockValue.reduce(
      (preValue, currentValue) => parseInt(preValue) + parseInt(currentValue)
    ) || 0;

  return (
    <Container>
      <form onSubmit={handleSubmit}>
        {successMsg && (
          <Success>
            {successMsg} {`${uploadPercentage}%`}
          </Success>
        )}
        {submitError.length !== 0 && submitError[0].response && (
          <FormSubmitError>{submitError[0].response.data}</FormSubmitError>
        )}
        {submitError.length !== 0 && !submitError[0].response && (
          <FormSubmitError>
            {submitError[0].message}, check your connection
          </FormSubmitError>
        )}
        <HeadWrap>
          <TitleWrap>
            <TitleHead>Update Product</TitleHead>
          </TitleWrap>
          <TitleSub>Edit product item details</TitleSub>
        </HeadWrap>

        <PContainer>
          <MainWrapper>
            <ProductDetailsWrap>
              <FormContentWrap>
                <label htmlFor="productName">Product title</label>
                <input
                  type="text"
                  name="name"
                  value={values.name}
                  onChange={handleChange}
                  id="productName"
                />
                {errors.name && <FormError>{errors.name}</FormError>}
              </FormContentWrap>

              <FormContentDesc>
                <label htmlFor="descriptions">Product Description*</label>
                <textarea
                  type="text"
                  name="description"
                  value={values.description}
                  onChange={handleChange}
                  id="descriptions"
                />
                {errors.description && (
                  <FormError>{errors.description}</FormError>
                )}
              </FormContentDesc>
            </ProductDetailsWrap>

            <ProductPriceWrap>
              <FormContentWrap1>
                <FormContent>
                  <label htmlFor="cost">Cost per item</label>
                  <input
                    type="text"
                    name="costperitem"
                    value={values.costperitem}
                    onChange={handleChange}
                    id="cost"
                  />
                </FormContent>
                <FormContent>
                  <label htmlFor="Price">Price *</label>
                  <input
                    type="text"
                    name="price"
                    value={values.price}
                    onChange={handleChange}
                    id="Price"
                  />
                  {errors.price && <FormError>{errors.price}</FormError>}
                </FormContent>
              </FormContentWrap1>
            </ProductPriceWrap>

            <ProductSize>
              <ProductSizeTitle>Size</ProductSizeTitle>
              <form onSubmit={handleSubmit2}>
                <input
                  type="text"
                  name="size"
                  value={values.size}
                  onChange={handleChange}
                />
                <CreateBtn2 onClick={sizeHandler}>
                  <AiOutlinePlus />
                </CreateBtn2>
              </form>

              <SizeDisplay>
                {size.map((data, index) => {
                  return (
                    <SizeDisplayContent key={index}>
                      <div onClick={() => removeSize(data)}>
                        <RiDeleteBinLine />
                      </div>
                      <p>{data}</p>
                    </SizeDisplayContent>
                  );
                })}
              </SizeDisplay>
            </ProductSize>

            {imageUploadError && (
              <FormSubmitError>{imageUploadError}</FormSubmitError>
            )}
            <ImageUploadWrapper>
              <ImageWrap>
                <ImageTitle>Color</ImageTitle>

                <form onSubmit={handleSubmit2}>
                  <input
                    type="text"
                    name="color"
                    value={values.color}
                    onChange={handleChange}
                  />
                  <CreateBtn2 onClick={colorHandler}>
                    <AiOutlinePlus />
                  </CreateBtn2>
                </form>
                {values.colorError && (
                  <FormError>{values.colorError}</FormError>
                )}

                {color.map((data, index) => {
                  return (
                    <ImageContainer2 key={index}>
                      <ImageTitle>{data}</ImageTitle>
                      <ImageStockWrap>
                        <SKUWrap>
                          <label>SKU</label>
                          <p>
                            SKUcan contain product color, vendor or packaging
                            type.(i.e Sets)
                          </p>
                          <input
                            type="text"
                            name={`sku-${data}`}
                            value={sku[`sku-${data}`]}
                            onChange={(e) => skuHandler(e, data)}
                          />
                        </SKUWrap>

                        <ImageStock>
                          <label>Stock*</label>
                          <input
                            type="text"
                            name={`stock-${data}`}
                            value={stock[`stock-${data}`]}
                            onChange={(e) => stockHandler(e, data)}
                          />
                        </ImageStock>
                      </ImageStockWrap>
                      <ImageContainer>
                        <input
                          type="file"
                          name="imageUpload"
                          id={`upload-${data}`}
                          accept="image/*"
                          className="upload"
                          onChange={(e) => imageHandler(e, data)}
                        />
                        <label htmlFor={`upload-${data}`}>
                          <ImageClick>
                            <div>
                              <FiUploadCloud />
                            </div>
                            <h5>Upload</h5>
                          </ImageClick>
                        </label>

                        {imagesUrl.length !== 0 && (
                          <>
                            {imagesUrl.map((mainData) => {
                              if (mainData.type === data) {
                                return (
                                  <>
                                    {mainData.images.map((imageData, index) => {
                                      return (
                                        <Image>
                                          <img src={imageData} alt="" />
                                        </Image>
                                      );
                                    })}
                                  </>
                                );
                              } else {
                                return null;
                              }
                            })}
                          </>
                        )}
                      </ImageContainer>
                    </ImageContainer2>
                  );
                })}

                {oldColor.map((data, index) => {
                  return (
                    <ImageContainer2 key={index}>
                      <ImageTitle>
                        {" "}
                        <div>{data.Colortype}</div>
                        <ColorDelete
                          onClick={() =>
                            activateColorDelete(values._id, data.Colortype)
                          }
                        >
                          <RiDeleteBinLine />
                        </ColorDelete>
                      </ImageTitle>

                      <ImageStockWrap>
                        <SKUWrap>
                          <label>SKU</label>
                          <p>
                            SKUcan contain product color, vendor or packaging
                            type.(i.e Sets)
                          </p>
                          <input
                            type="text"
                            name={`sku-${data.Colortype}`}
                            value={sku[`sku-${data.Colortype}`]}
                            onChange={(e) => skuHandler(e, data.Colortype)}
                          />
                        </SKUWrap>

                        <ImageStock>
                          <label>Stock*</label>
                          <input
                            type="text"
                            name={`stock-${data.Colortype}`}
                            value={stock[`stock-${data.Colortype}`]}
                            onChange={(e) => stockHandler(e, data.Colortype)}
                          />
                        </ImageStock>
                      </ImageStockWrap>
                      <ImageContainer>
                        <input
                          type="file"
                          name="imageupload"
                          id={`upload-${data.Colortype}`}
                          accept="image/*"
                          className="upload"
                          onChange={(e) => imageHandler(e, data.Colortype)}
                        />
                        <label htmlFor={`upload-${data.Colortype}`}>
                          <ImageClick>
                            <div>
                              <FiUploadCloud />
                            </div>
                            <h5>Upload</h5>
                          </ImageClick>
                        </label>
                        {oldUrls && (
                          <>
                            {data.Images.map((datax, index) => {
                              return (
                                <Image key={datax.imageId._id}>
                                  <ImageRemoveWrap>
                                    <ImageRemove
                                      onClick={() =>
                                        activateDelete(
                                          values._id,
                                          datax.imageId._id
                                        )
                                      }
                                    >
                                      <RiDeleteBinLine />
                                    </ImageRemove>
                                  </ImageRemoveWrap>

                                  <img
                                    src={`${BASE_API_}${datax.imageId.path}`}
                                    alt=""
                                  />
                                </Image>
                              );
                            })}
                          </>
                        )}

                        {imagesUrl.length !== 0 && (
                          <>
                            {imagesUrl.map((mainData) => {
                              if (mainData.type === data.Colortype) {
                                return (
                                  <>
                                    {mainData.images.map((imageData, index) => {
                                      return (
                                        <Image>
                                          <img src={imageData} alt="" />
                                        </Image>
                                      );
                                    })}
                                  </>
                                );
                              } else {
                                return null;
                              }
                            })}
                          </>
                        )}
                      </ImageContainer>
                    </ImageContainer2>
                  );
                })}
              </ImageWrap>
            </ImageUploadWrapper>

            <SideWrap2>
              <StatusWrapper>
                <StatusContainer>
                  <label>Status *</label>
                  <select
                    value={values.active}
                    onChange={handleChange}
                    name="active"
                  >
                    <option value={false} name="active">
                      Not Active
                    </option>
                    <option value={true} name="active">
                      {" "}
                      Active
                    </option>
                  </select>
                </StatusContainer>
              </StatusWrapper>

              <FeaturedWrapper>
                <FeaturedContainer>
                  <label>Featured </label>
                  <select
                    onChange={handleChange}
                    name="featured"
                    value={values.featured}
                  >
                    <option value={false} name="featured">
                      False
                    </option>
                    <option value={true} name="featured">
                      {" "}
                      True
                    </option>
                  </select>
                </FeaturedContainer>
              </FeaturedWrapper>

              <TagsWrapper>
                <TagsContainer>
                  <label htmlFor="Tags">Tags </label>
                  <input
                    value={values.tags}
                    type="text"
                    name="tags"
                    onChange={handleChange}
                    id="Tags"
                  />
                </TagsContainer>
              </TagsWrapper>

              <CategoriesWrapper>
                <CatWrapper>
                  <label>Categories</label>
                  <div>
                    {categories.map((data) => {
                      return (
                        <ContentWrap key={data._id}>
                          <input
                            type="radio"
                            id={data.name}
                            name="category"
                            value={data._id}
                            onChange={handleChange}
                            checked={
                              data._id === values.category._id ||
                              data._id === values.category
                                ? true
                                : ""
                            }
                          />
                          <label htmlFor={data.name}>
                            <p>{data.name}</p>
                          </label>
                        </ContentWrap>
                      );
                    })}
                  </div>

                  {errors.category && <FormError>{errors.category}</FormError>}
                </CatWrapper>
              </CategoriesWrapper>
            </SideWrap2>

            <VisibilityWrapper>
              <VContainer>
                <label>Visibility</label>
                <ContentWrap>
                  {values.active === "true" && <Visible />}

                  <label htmlFor="publish">
                    <p>Publishing on site</p>
                  </label>
                </ContentWrap>
              </VContainer>
            </VisibilityWrapper>

            <TStockWrapper>
              <TTStock>
                <label htmlFor="TotalStock">Total Stock* </label>
                <input
                  type="text"
                  name="totalStock"
                  value={totalStock}
                  readOnly
                  id="TotalStock"
                />
              </TTStock>
              <hr />
              <TTReserved>
                <label htmlFor="Reserved">Reserved </label>
                <input
                  type="text"
                  name="reserved"
                  value={values.reserved}
                  onChange={handleChange}
                  id="Reserved"
                />
              </TTReserved>
            </TStockWrapper>
          </MainWrapper>
        </PContainer>

        {changeActive ? (
          <>
            {isSubmitting ? (
              <EditBtn2>
                <Loader type="TailSpin" color="white" height={30} width={30} />
              </EditBtn2>
            ) : (
              <EditBtn type="submit">Submit</EditBtn>
            )}
          </>
        ) : (
          <EditBtn2>Submit</EditBtn2>
        )}
      </form>

      {deleteColorActive && (
        <DeleteContainer>
          <DeleteWrapper>
            <DeleteTitle>
              {!loadDelete && !deleteSuccess ? (
                <h5>Delete this Color?</h5>
              ) : (
                <h5>Successful</h5>
              )}
            </DeleteTitle>

            {!loadDelete && !deleteSuccess && (
              <div>
                {deleteError.length !== 0 && deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].response}</DeleteErrors>
                )}
                {deleteError.length !== 0 && !deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].message}</DeleteErrors>
                )}

                <DeletesWrap>
                  <img src={deleteSvg} alt="delete" />
                </DeletesWrap>
                <DeleteText>
                  You’ll lose all the color details. We can’t recover them when
                  deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to permanently delete this Color?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={closeColorDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn
                    onClick={() => removeColor(ColorIdentity, ImageColor)}
                  >
                    Yes
                  </YesBtn>
                </ButtonWrap>
              </div>
            )}

            {loadDelete && (
              <LoadingWrapper>
                <Loader type="TailSpin" color="black" height={50} width={50} />
              </LoadingWrapper>
            )}

            {deleteSuccess && !loadDelete && (
              <LoadingWrapper>
                <DeleteMessageWrap>
                  <DeleteMessage>{deleteSuccess}</DeleteMessage>
                  <DeleteMessage>
                    this popup will be closed automatically.
                  </DeleteMessage>
                  <CloseButton onClick={closeColorDelete}>Close</CloseButton>
                </DeleteMessageWrap>
              </LoadingWrapper>
            )}
          </DeleteWrapper>
        </DeleteContainer>
      )}

      {deleteActive && (
        <DeleteContainer>
          <DeleteWrapper>
            <DeleteTitle>
              {!loadDelete && !deleteSuccess ? (
                <h5>Delete this Image?</h5>
              ) : (
                <h5>Successful</h5>
              )}
            </DeleteTitle>

            {!loadDelete && !deleteSuccess && (
              <div>
                {deleteError.length !== 0 && deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].response}</DeleteErrors>
                )}
                {deleteError.length !== 0 && !deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].message}</DeleteErrors>
                )}

                <DeletesWrap>
                  <img src={deleteSvg} alt="delete" />
                </DeletesWrap>
                <DeleteText>
                  You’ll lose all the image details. We can’t recover them when
                  deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to permanently delete this Image?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={closeDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn onClick={() => removeImages(Identity, ImageIdentity)}>
                    Yes
                  </YesBtn>
                </ButtonWrap>
              </div>
            )}

            {loadDelete && (
              <LoadingWrapper>
                <Loader type="TailSpin" color="black" height={50} width={50} />
              </LoadingWrapper>
            )}

            {deleteSuccess && !loadDelete && (
              <LoadingWrapper>
                <DeleteMessageWrap>
                  <DeleteMessage>{deleteSuccess}</DeleteMessage>
                  <DeleteMessage>this popup will be closed automatically.</DeleteMessage>
                  <CloseButton onClick={closeDelete}>Close</CloseButton>
                </DeleteMessageWrap>
              </LoadingWrapper>
            )}
          </DeleteWrapper>
        </DeleteContainer>
      )}
    </Container>
  );
};

export default MobileUpdateProduct;
