/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  Wrapper,
  Container,
  FormWrapper,
  FormTitle,
  FormButton,
  FormContentWrap,
  FormContent,
  FormActivation,
  DeleteButton,
  SlideF,
  FormButtonWrap,
  FormButton2,
  FormSuccess,
} from "./styles";
import Loader from "react-loader-spinner";
import SocialDetail from '../../FormValidation/socialDetail'
import Trash from "../../../assets/trash-2.png";
const SeoSection =()=>  {
 
  const {
    values,
    handleChange,
    handleSubmit,
    isSubmitting,
    handleClick,
    getInfo,
    submitSuccess,
  } = SocialDetail();
  
  useEffect(()=>{
    getInfo();
  }, [])
    return (
      <Wrapper>
        <Container>
          <FormWrapper onSubmit={handleSubmit}>
            <FormTitle>Socials</FormTitle>
            <hr />
            {submitSuccess && <FormSuccess>{submitSuccess}</FormSuccess>}
            <FormContentWrap>
              <label>Facebook</label>
              <FormContent>
                <FormActivation
                  onClick={() =>
                    handleClick("facebookActive", !values.facebookActive)
                  }
                >
                  <SlideF
                    active={values.facebookActive}
                    value={!values.facebookActive}
                    name="facebookActive"
                  />
                </FormActivation>
                <input
                  name="facebookLink"
                  value={values.facebookLink}
                  onChange={handleChange}
                  placeholder="type URL (i.e https://www.facebook.com/pagename)"
                />
                {values.facebookActive && values.facebookLink && (
                  <DeleteButton>
                    <img src={Trash} alt="" />
                  </DeleteButton>
                )}
              </FormContent>
            </FormContentWrap>

            <FormContentWrap>
              <label>Twitter</label>
              <FormContent>
                <FormActivation
                  onClick={() =>
                    handleClick("twitterActive", !values.twitterActive)
                  }
                >
                  <SlideF active={values.twitterActive} name="twitterActive" />
                </FormActivation>
                <input
                  name="twitterLink"
                  value={values.twitterLink}
                  onChange={handleChange}
                  placeholder="type URL (i.e https://www.facebook.com/pagename)"
                />
                {values.twitterActive && values.twitterLink && (
                  <DeleteButton>
                    <img src={Trash} alt="" />
                  </DeleteButton>
                )}
              </FormContent>
            </FormContentWrap>

            <FormContentWrap>
              <label>Instagram</label>
              <FormContent>
                <FormActivation
                  onClick={() =>
                    handleClick("instagramActive", !values.instagramActive)
                  }
                >
                  <SlideF active={values.instagramActive} name="Fslide" />
                </FormActivation>
                <input
                  name="instagramLink"
                  value={values.instagramLink}
                  onChange={handleChange}
                  placeholder="type URL (i.e https://www.facebook.com/pagename)"
                />
                {values.instagramActive && values.instagramLink && (
                  <DeleteButton>
                    <img src={Trash} alt="" />
                  </DeleteButton>
                )}
              </FormContent>
            </FormContentWrap>

            <FormContentWrap>
              <label>Pinterest</label>
              <FormContent>
                <FormActivation
                  onClick={() =>
                    handleClick("pinterestActive", !values.pinterestActive)
                  }
                >
                  <SlideF active={values.pinterestActive} name="Fslide" />
                </FormActivation>
                <input
                  name="pinterestLink"
                  value={values.pinterestLink}
                  onChange={handleChange}
                  placeholder="type URL (i.e https://www.facebook.com/pagename)"
                />
                {values.pinterestActive && values.pinterestLink && (
                  <DeleteButton>
                    <img src={Trash} alt="" />
                  </DeleteButton>
                )}
              </FormContent>
            </FormContentWrap>

            <FormContentWrap>
              <label>Youtube</label>
              <FormContent>
                <FormActivation
                  onClick={() =>
                    handleClick("youtubeActive", !values.youtubeActive)
                  }
                >
                  <SlideF active={values.youtubeActive} />
                </FormActivation>
                <input
                  name="youtubeLink"
                  value={values.youtubeLink}
                  onChange={handleChange}
                  placeholder="type URL (i.e https://www.facebook.com/pagename)"
                />
                {values.youtubeActive && values.youtubeLink && (
                  <DeleteButton>
                    <img src={Trash} alt="" />
                  </DeleteButton>
                )}
              </FormContent>
            </FormContentWrap>

            <FormButtonWrap>
              {isSubmitting ? (
                <FormButton2>
                  {" "}
                  <Loader
                    type="TailSpin"
                    color="white"
                    height={30}
                    width={30}
                  />
                </FormButton2>
              ) : (
                <FormButton type="submit">Save Changes</FormButton>
              )}
            </FormButtonWrap>
          </FormWrapper>
        </Container>
      </Wrapper>
    );
  
}

export default SeoSection;
