import axios from "axios";
import Cookies from "js-cookie";

import { BASE_API_ } from "../../utils/base-url.config";
//get all the featured

//checkIfUseristhere

export const checkUser = () => {
  //const
  const getToken = Cookies.get("_tk");
  const getId = Cookies.get("sid");

  if (getToken !== undefined && getId !== undefined) {
    return {
      type: "LOG_USER",
      payload: "loggedIn",
    };
  } else {
    return {
      type: "LOG_USER",
      payload: "notloggedIn",
    };
  }
};

