//customer integration request
export const getallUsers = (redirect, getToken) => {
  return async (dispatch) => {
    dispatch({ type: "GET_CUSTOMERS_REQUEST" });
    try {
      const getUsers = await axios.get(`${BASE_API_}/user/v1/guestUser/`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      });

      dispatch({
        type: "GET_CUSTOMERS_SUCCESS",
        payload: getUsers.data,
      });
    } catch (error) {
      let errorArray = [];
      errorArray.push(error);

      if (errorArray[0].response && errorArray.length !== 0) {
        if (errorArray[0].response.status === 401) {
          Cookies.remove("_tk");
          redirect();
        }
      }
      dispatch({
        type: "GET_CUSTOMERS_FAILURE",
        error: errorArray,
      });
    }
  };
};

//product integration request
export const fetchProducts = (page, perPage, redirect, getToken) => {
  return async (dispatch) => {
    dispatch({ type: "FETCH_PRODUCTS_REQUEST" });
    try {
      const getProducts = await axios.get(
        `${BASE_API_}/products/v1/createdProducts/?page=${page}`,
        {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        }
      );

      if (
        getProducts.data.products !== null ||
        getProducts.data.products !== undefined
      ) {
        dispatch({
          type: "FETCH_PRODUCTS_SUCCESS",
          payload: getProducts.data,
        });
      } else {
        dispatch({
          type: "FETCH_PRODUCTS_SUCCESS",
          payload: {
            products: [],
            totalProducts: "",
            current: "",
          },
        });
      }
    } catch (error) {
      let errorArray = [];
      errorArray.push(error);

      if (errorArray[0].response && errorArray.length !== 0) {
        if (errorArray[0].response.status === 401) {
          Cookies.remove("_tk");
          redirect();
        }
      }

      dispatch({
        type: "FETCH_PRODUCTS_FAILURE",
        payload: errorArray,
      });
    }
  };
};

