export const CheckMobile = () => {
  const screenChange = window.matchMedia("(max-width: 1365px)");

  if (screenChange.matches) {
    return {
      type: "MOBILE",
      payload: true,
    };
  } else {
    return {
      type: "MOBILE",
      payload: false,
    };
  }
};

