import React from "react";
import {
  EditButton,
  EditButtonWrap,
  IconDropWrap,
  IconDrop,
  DIconDrop,
} from "./styles";

import EditBn from "../../../assets/Edit-2.png";
class GridDropDown extends React.Component {
  constructor(props) {
    super(props);
    this.state = { deleteActive: false, activedrop: false };
    this.dropRef = React.createRef();
    this.handleOutsideClick = this.handleOutsideClick.bind(this);
  }

  componentDidMount() {
    document.addEventListener("click", this.handleOutsideClick);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleOutsideClick);
  }

  handleOutsideClick = (event) => {
    if (this.dropRef && !this.dropRef.current.contains(event.target)) {
      this.setState({ activedrop: false });
    }
  };

  handleIconClick = (e) => {
    this.setState({ activedrop: !this.state.activedrop });
  };
  render() {
    return (
      <EditButtonWrap ref={this.dropRef}>
        <EditButton onClick={this.handleIconClick}>
          <img src={EditBn} alt="" />
        </EditButton>
        {this.state.activedrop && (
          <IconDropWrap>
            <IconDrop
              onClick={() => this.props.activateEdit(this.props.editData)}
            >
              Edit
            </IconDrop>
            <DIconDrop
              onClick={() => this.props.activateDelete(this.props.ids)}
            >
              Delete
            </DIconDrop>
          </IconDropWrap>
        )}
      </EditButtonWrap>
    );
  }
}

export default GridDropDown;
