export function validateInfo(values) {
  let errors = {};
  if (!values.email) {
    errors.email = "email is required";
  }

  if (!values.firstname.trim()) {
    errors.firstname = "firstname is required";
  }

  if (!values.lastname.trim()) {
    errors.lastname = "lastname is required";
  }

  if (!values.phonenumber) {
    errors.phonenumber = "phone number required";
  }

  return errors;
}

export function validatePass(values) {
  let errors = {};

  if (!values.currentPass) {
    errors.currentPass = "current password is required";
  }

  if (!values.newPass) {
    errors.newPass = "new password is required";
  }

  if (!values.confirmPass) {
    errors.confirmPass = "confirm password please ";
  }

  return errors;
}
