/* eslint-disable array-callback-return */
/* eslint-disable no-unused-vars */
import { useState, useEffect } from "react";
import axios from "axios";
import { BASE_API_ } from "../../utils/base-url.config";
import Cookies from "js-cookie";

const ProductCreationDetails = (validateProduct) => {
  const [values, setValues] = useState({
    name: "",
    price: "",
    label: "",
    description: "",
    quantity: "",
    active: false,
    status: "",
    imageDefault: "",
    category: "",
    featured: false,
    color: "",
    size: "",
  });

  const [sku, setSKU] = useState({});
  const [stock, setStock] = useState({});
  const [size, setSize] = useState([]);
  const [color, setColor] = useState([]);
  const [oldColor, setOldColor] = useState([]);

  const [changeActive, setChangeActive] = useState(false);
  const [successMsg, setSuccessMsg] = useState("");
  const [submitError, setSubmitError] = useState([]);
  const [getId, setGetId] = useState("");
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [file, setFile] = useState([]);
  const [imagesUrl, setImageUrl] = useState([]);
  const [oldUrls, setOldImageUrl] = useState([]);
  const [uploadPercentage, setUploadPercentage] = useState(0);
  const [imageUploadError, setImageUploadError] = useState("");

  //handle change on the inputs
  const handleChange = (e) => {
    setChangeActive(true);
    setValues({
      ...values,
      colorError: "",
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validateProduct(values));
    setIsSubmit(true);
  };

  const handleSubmit2 = (e) => {
    e.preventDefault();
  };

  const sizeHandler = (e) => {
    e.preventDefault();
    setChangeActive(true);
    setSize([...size, values.size]);
    setValues({ ...values, size: "" });
  };

  const removeSize = (sizes) => {
    setChangeActive(true);
    let allSizes = size;
    allSizes.filter((data, index) => {
      if (data === sizes) {
        allSizes.splice(index, 1);
      }
    });

    setSize([...allSizes]);
  };
  const colorHandler = (e) => {
    e.preventDefault();
     if (values.color) {
       if (oldColor.length > 0) {
         for (let iteration = 0; iteration < oldColor.length; iteration++) {
           let ttIteration = iteration + 1;

           if (oldColor[iteration].Colortype === values.color) {
             setValues({
               ...values,
               colorError: `${values.color} already exists`,
             });
             break;
           } else if (
             ttIteration === oldColor.length &&
             oldColor[iteration].Colortype !== values.color
           ) {
             if (!values.colorError) {
               setChangeActive(true);
               setColor([...color, values.color]);
               setValues({ ...values, color: "" });
             }
           }
         }
       } else {
         setChangeActive(true);
         setColor([...color, values.color]);
         setValues({ ...values, color: "" });
       }
     } else {
       setValues({ ...values, colorError: `please type the color` });
     }

  };

  const skuHandler = (e, data) => {
    setChangeActive(true);
    setSKU({
      ...sku,
      [e.target.name]: e.target.value,
    });
  };

  const stockHandler = (e, data) => {
    setChangeActive(true);
    setStock({
      ...stock,
      [e.target.name]: e.target.value,
    });
  };

  const imageHandler = (e, data) => {
    e.preventDefault();

    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        let imagesUrlArray = [...imagesUrl];
        if (file.length === 0) {
          let target = e.target.files[0];

          target.colortype = data;
          setChangeActive(true);
          setFile([...file, target]);
          setImageUrl([...imagesUrl, { type: data, images: [reader.result] }]);
        } else {
          setChangeActive(true);
          for (
            let iteration = 0;
            iteration < imagesUrlArray.length;
            iteration++
          ) {
            let totalIteration = iteration + 1;
            if (imagesUrlArray[iteration].type === data) {
              let target = e.target.files[0];

              target.colortype = data;

              imagesUrlArray[iteration].images.push(reader.result);
              setFile([...file, target]);
              return setImageUrl([...imagesUrlArray]);
            } else if (
              totalIteration === imagesUrlArray.length &&
              imagesUrlArray[iteration].type !== data
            ) {
              let target = e.target.files[0];

              target.colortype = data;
              setFile([...file, target]);
              setImageUrl([
                ...imagesUrl,
                { type: data, images: [reader.result] },
              ]);
            }
          }
        }
      } else {
      }
    };
    if (e.target.files.length > 1) {
      for (let iteration = 0; iteration < e.target.files.length; iteration++) {
        reader.readAsDataURL(e.target.files[iteration]);
      }
    } else {
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  //get the categories
  const fetchProductData = (id, historyProps) => {
    const productDetails = async () => {
      const getProducts = JSON.parse(
        sessionStorage.getItem("products") || "[]"
      );

      if (getProducts.length !== 0) {
        const checkProduct = await getProducts.filter((data) => {
          if (data._id === id) {
            setGetId(data._id);
            return data;
          }

          return null;
        });
        if (checkProduct.length !== 0) {
          let productDetails = checkProduct[0];
          let skuDetail = {};
          let stockDetail = {};
          productDetails.productTypes.filter((data, index) => {
            skuDetail[`sku-${data.Colortype}`] = data.SKU;
            stockDetail[`stock-${data.Colortype}`] = data.Stock || 0;
            setSKU({
              ...sku,
              ...skuDetail,
            });

            setStock({
              ...stock,
              ...stockDetail,
            });
            return null;
          });

          setValues({
            ...values,
            ...productDetails,
            reserved: productDetails.reservedStock,
          });
          setSize(productDetails.sizes);
          setOldColor(productDetails.productTypes || []);
          setOldImageUrl(productDetails.productTypes);
        } else {
          historyProps.goBack();
        }
      } else {
          const getToken = Cookies.get("_tk");
        axios
          .get(`${BASE_API_}/products/v1/products/${id}`, {
            headers: {
              Authorization: "Bearer " + getToken,
            },
          })
          .then((success) => {
            setGetId(success.data._id);

            let skuDetail = {};
            let stockDetail = {};
            success.data.productTypes.filter((data, index) => {
              skuDetail[`sku-${data.Colortype}`] = data.SKU;
              stockDetail[`stock-${data.Colortype}`] = data.Stock || 0;
              setSKU({
                ...sku,
                ...skuDetail,
              });

              setStock({
                ...stock,
                ...stockDetail,
              });
              return null;
            });
            setValues({
              ...values,
              ...success.data,
              reserved: success.data.reservedStock,
            });
            setSize(success.data.sizes);
            setOldColor(success.data.productTypes || []);
            setOldImageUrl(success.data.productTypes);
          })
          .catch((error) => {
            setSubmitError([error]);
          });
      }
    };

    productDetails();
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
       const getToken = Cookies.get("_tk");
      const postDetails = async () => {
        try {
          const formData = new FormData();

          for (let iteration = 0; iteration < file.length; iteration++) {
            formData.append("colorTypes", file[iteration].colortype);
            formData.append("files", file[iteration]);
          }
          let ProductId = getId;
          //create product first
          let value = {
            ...values,
            colors: color,
            sku: sku,
            stock: stock,
            sizes: size,
          };

          const postProductText = await axios.patch(
            `${BASE_API_}/products/v1/productUpdate/${ProductId}`,
            value,
            {
              headers: {
                Authorization: "Bearer " + getToken,
              },
            }
          );

          if (postProductText) {
            if (file.length !== 0) {
              const postImages = await axios.patch(
                `${BASE_API_}/products/v1/productImageUpdate/${ProductId}`,
                formData,
                {
                  headers: {
                    "Content-Type": "multipart/form-data",
                    Authorization: "Bearer " + getToken,
                  },
                  onUploadProgress: (progressEvent) => {
                    setUploadPercentage(
                      parseInt(
                        Math.round(
                          (progressEvent.loaded * 100) / progressEvent.total
                        )
                      )
                    );
                  },
                }
              );
              if (postImages) {
                setSuccessMsg(
                  `Product Created successfully and Images Uploaded -`
                );
                setIsSubmit(false);
                sessionStorage.setItem("products", "[]");
                window.location.reload();
              } else {
                setSubmitError([postImages]);
                setIsSubmit(false);
              }
            } else {
              sessionStorage.setItem("products", "[]");
              setSuccessMsg("Product Updated successfully");
              window.location.reload();
            }
          } else {
            setSubmitError([postProductText]);
            setIsSubmit(false);
          }
        } catch (error) {
          setIsSubmit(false);

          setSubmitError([error]);
        }
      };

      postDetails();
    } else {
      setIsSubmit(false);
    }
  }, [values, errors, file, isSubmitting, getId, stock, sku, color, size]);

  return {
    values,
    submitError,
    successMsg,
    handleChange,
    handleSubmit,
    imageHandler,
    imagesUrl,
    fetchProductData,
    uploadPercentage,
    errors,
    oldUrls,
    changeActive,
    imageUploadError,
    isSubmitting,
    handleSubmit2,
    sizeHandler,
    size,
    colorHandler,
    skuHandler,
    stockHandler,
    oldColor,
    sku,
    stock,
    color,
    removeSize,
  };
};

export default ProductCreationDetails;
