import React from "react";
import {
  Wrapper,
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  CreateBtn,
  PContainer,
  SelectionWrapper,
  SearchWrapper,
  SelectWrapper,
  MobileSearchWrapper,
  MobileSearch,
  CreateBtn2,
  LatestWrapper,
  LatestWrapContent,
  ProductContainer,
  ProductImageWrap,
  ProductContentWrap,
  ProductContent,
  Ptitle,
  PPrice,
  EditSection,
  EditWrapSection,
  EditContainer,
  TitleWrap2,
  TitleText,
  CloseIcon2,
  FormContentWrap,
  ImageUploadWrapper,
  ImageTitle,
  ImageContainer,
  ImageWrap,
  Image,
  ImageClick,
  FormContent3,
  EditButtonWrap2,
  SaveEditButton,
  CancelEditButton,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  LoadingWrapper,
  CloseButton,
  DeleteMessage,
  DeleteMessageWrap,
  DeleteErrors,
  DeleteContent,
  DeletesWrap,
  NoListWrapper,
  NoListContainer,
} from "./styles";
import listImage from "../../assets/icons/NoList.svg";

import { AiOutlinePlus, AiOutlineClose } from "react-icons/ai";

import { BsSearch } from "react-icons/bs";
import { FiUploadCloud } from "react-icons/fi";
import Loader from "react-loader-spinner";
import Pagination2 from "./pagination2";
import Pagination from "./pagination";
import DropDown from "./DropDown";
import Pimage from "../../assets/default.png";
import axios from "axios";
import { connect } from "react-redux";
import { fetchBlogs } from "../../store/actions";
import deleteSvg from "../../assets/icons/delete.svg";
import {BASE_API_} from '../../utils/base-url.config';
import Cookies from "js-cookie";

class Blog extends React.Component {
  state = {
    page: undefined,
    perPage: 8,
    searchActive: false,
    itemSlist: [],
    currentS: null,
    blogSTotal: null,
    allsetItems: [],
    query: "",
    createContent: false,
    createError: [],
    createSuccess: "",
    createLoading: false,
    File: [],
    ImageUrl: [],
    blogtitle: "",
    bloglink: "",
    blogtype: "",
    errors: {
      blogtitle: "",
      bloglink: "",
      blogtype: "",
    },
    editContent: false,
    OldImageUrl: [],
    blogId: " ",
    checkImage: false,
    editSuccess: "",
    editError: [],
    editLoading: false,
    deleteActive: false,
    loadDelete: false,
    deleteSuccess: "",
    deleteError: [],
    pageLoading: false,
  };

  componentDidMount() {
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
      }
    };
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getBlogs = async () => {
        await this.props.fetchBlogs(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      getBlogs();
    } else {
      const getBlogs = async () => {
        await this.props.fetchBlogs(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      getBlogs();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      const getToken = Cookies.get("_tk");
      let page;
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const getBlogs = async () => {
          await this.props.fetchBlogs(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        getBlogs();
      } else {
        const getBlogs = async () => {
          await this.props.fetchBlogs(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        getBlogs();
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  activateDelete = (id) => {
    this.setState({ deleteActive: true, ids: id });
  };
  closeDelete = () => {
    this.setState({
      deleteActive: false,
      loadDelete: false,
      deleteSuccess: " ",
      deleteError: [],
    });
  };

  //remove blog
  removeBlog = (id) => {
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/blog/v1/blog/remove/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ deleteSuccess: success.data, loadDelete: false });
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ deleteError: errorArray, loadDelete: false });
      });
  };

  activateContent = () => {
    this.setState({ createContent: true });
  };
  deactivatecreate = () => {
    this.setState({
      createContent: false,
      File: [],
      ImageUrl: [],
      blogtitle: "",
      bloglink: "",
      blogtype: "",
      errors: {
        blogtitle: "",
        bloglink: "",
        blogtype: "",
        imagelink: "",
      },
      checkImage: false,
      createSuccess: "",
      createError: [],
    });
  };

  activateEdit = (data) => {
    let imageUrl = [];
    imageUrl.push(`${BASE_API_}${data.featuredImage.path}`);
    this.setState({
      editContent: true,
      blogtitle: data.title,
      bloglink: data.link,
      blogtype: data.type,
      blogId: data._id,
      OldImageUrl: imageUrl,
    });
  };

  deactivateedit = () => {
    this.setState({
      editContent: false,
      File: [],
      ImageUrl: [],
      blogtitle: "",
      bloglink: "",
      blogtype: "",
      errors: {
        blogtitle: "",
        bloglink: "",
        blogtype: "",
        imagelink: "",
      },
      OldImageUrl: [],
      blogId: " ",
      checkImage: false,
      editSuccess: "",
      editError: [],
    });
  };

  //imageUploader
  imageHandler = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        this.setState({ File: [e.target.files[0]] });
        this.setState({
          ImageUrl: [reader.result],
          OldImageUrl: [],
          checkImage: true,
          createError: [],
          editError: [],
        });
      } else {
      }
    };

    reader.readAsDataURL(e.target.files[0]);
  };

  //changes in the form
  handleChange = (e) => {
    if (e.target.name === "blogtitle") {
      this.setState({
        [e.target.name]: e.target.value,
        createError: [],
        editError: [],
      });
    } else if (e.target.name === "bloglink") {
      this.setState({
        bloglink: e.target.value,
        createError: [],
        editError: [],
      });
    } else if (e.target.name === "blogtype") {
      this.setState({
        blogtype: e.target.value,
        createError: [],
        editError: [],
      });
    }
  };

  //submit the creation of blog
  handleSubmit = (e) => {
    e.preventDefault();
    if (
      !this.state.blogtitle ||
      !this.state.bloglink ||
      this.state.File.length === 0 ||
      !this.state.blogtype
    ) {
      let errors = {};
      if (!this.state.blogtitle) {
        errors.blogtitle = "blogtitle required";
      }
      if (!this.state.bloglink) {
        errors.bloglink = "bloglink required";
      }
      if (!this.state.blogtype) {
        errors.blogtype = "blogtype required";
      }
      if (this.state.File.length === 0) {
        errors.imagelink = "Image required";
      }
      this.setState({
        errors: errors,
      });
    } else {
      this.setState({ createLoading: true });
      this.postSubmission();
    }
  };

  //request to submit form

  postSubmission = () => {
    const formData = new FormData();

    formData.append("files", this.state.File[0]);
    formData.append("blogtitle", this.state.blogtitle);
    formData.append("bloglink", this.state.bloglink);
    formData.append("blogtype", this.state.blogtype);
     const getToken = Cookies.get("_tk");
    axios
      .post(`${BASE_API_}/blog/v1/blog/create`, formData, {
        headers: {
          Authorization: "Bearer " + getToken,
          "Content-Type": "multipart/form-data",
          
        },
      })
      .then((success) => {
        this.setState({ createSuccess: success.data, createLoading: false });
        window.location.reload();
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ createError: errorArray, createLoading: false });
      });
  };

  //submit the creation of blog
  handleSubmit2 = (e) => {
    e.preventDefault();
    if (!this.state.blogtitle || !this.state.bloglink || !this.state.blogtype) {
      let errors = {};
      if (!this.state.blogtitle) {
        errors.blogtitle = "blogtitle required";
      }
      if (!this.state.bloglink) {
        errors.bloglink = "bloglink required";
      }
      if (!this.state.blogtype) {
        errors.blogtype = "blogtype required";
      }

      this.setState({
        errors: errors,
      });
    } else {
      this.setState({ editLoading: true });
      this.editSubmission();
    }
  };

  //request to submit form

  editSubmission = () => {
    const formData = new FormData();

    formData.append("files", this.state.File[0]);
    formData.append("blogtitle", this.state.blogtitle);
    formData.append("bloglink", this.state.bloglink);
    formData.append("blogtype", this.state.blogtype);
    formData.append("checkImage", this.state.checkImage);
    const getToken = Cookies.get("_tk");
    axios
      .put(`${BASE_API_}/blog/v1/edit/blog/${this.state.blogId}`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ editSuccess: success.data, editLoading: false });
        window.location.reload();
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ editError: errorArray, editLoading: false });
      });
  };

  //handling search
  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allitems = this.props.blogs.allBlogs;
    const resultData = allitems.filter((item) => {
      let itemName = item.title.toLowerCase();
      return itemName.includes(query.toLowerCase());
    });
    const totalitems = resultData.length;

    let showItems = resultData;

    let currentPage = this.state.currentS;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showItems.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      itemSlist: currentItems,
      currentS: 1,
      blogSTotal: totalitems,
      allsetItems: showItems,
      results: showItems,
      loading: true,
      searchActive: true,
    });
  };

  //changing the page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetItems.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      itemSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  renderProducts = () => {
    return this.props.blogs.blogs.map((data, index) => {
      let pName = data.title;
      if (Object.keys(pName).length > 10) {
        pName = data.title.slice(0, 10) + "...";
      }

      return (
        <ProductContainer>
          <ProductImageWrap>
            {data.featuredImage ? (
              <img src={`${BASE_API_}${data.featuredImage.path}`} alt="" />
            ) : (
              <img src={Pimage} alt="" />
            )}
          </ProductImageWrap>
          <ProductContentWrap>
            <ProductContent>
              <Ptitle>{pName}</Ptitle>
              <PPrice href={data.link} target="_blank">
                open link
              </PPrice>
            </ProductContent>
            <DropDown
              activateDelete={this.activateDelete}
              activateEdit={this.activateEdit}
              editData={data}
              ids={data._id}
            />
          </ProductContentWrap>
        </ProductContainer>
      );
    });
  };

  //render search products
  renderSearchProducts = () => {
    return this.state.itemSlist.map((data, index) => {
      let pName = data.title;
      if (Object.keys(pName).length > 10) {
        pName = data.title.slice(0, 10) + "...";
      }

      return (
        <ProductContainer>
          <ProductImageWrap>
            {data.featuredImage ? (
              <img src={`${BASE_API_}${data.featuredImage.path}`} alt="" />
            ) : (
              <img src={Pimage} alt="" />
            )}
          </ProductImageWrap>
          <ProductContentWrap>
            <ProductContent>
              <Ptitle>{pName}</Ptitle>
              <PPrice href={data.link} target="_blank">
                open link
              </PPrice>
            </ProductContent>
            <DropDown activateDelete={this.activateDelete} ids={data._id} />
          </ProductContentWrap>
        </ProductContainer>
      );
    });
  };
  render() {
    return (
      <Wrapper>
        <Container>
          <HeadWrap>
            <TitleWrap>
              <TitleHead>Blog list</TitleHead>
              <CreateBtn onClick={this.activateContent}>+ Create New</CreateBtn>
            </TitleWrap>
            <TitleSub>View Blog post List</TitleSub>
          </HeadWrap>

          <MobileSearchWrapper>
            <MobileSearch>
              <form onSubmit={this.onFormSubmit}>
                <input
                  type="text"
                  name="query"
                  value={this.state.query}
                  onChange={this.handleInputChange}
                  placeholder="Search..."
                />
                <button>
                  {" "}
                  <BsSearch />
                </button>
              </form>
            </MobileSearch>

            <CreateBtn2 onClick={this.activateContent}>
              <AiOutlinePlus />
            </CreateBtn2>
          </MobileSearchWrapper>

          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.blogs.blogs.length > 0 ? (
                <PContainer bg={this.state.bgColor}>
                  <SelectionWrapper>
                    <SearchWrapper>
                      <form onSubmit={this.onFormSubmit}>
                        <input
                          type="text"
                          name="query"
                          value={this.state.query}
                          onChange={this.handleInputChange}
                          placeholder="Search..."
                        />
                        <button>
                          {" "}
                          <BsSearch />
                        </button>
                      </form>
                    </SearchWrapper>

                    <SelectWrapper></SelectWrapper>
                  </SelectionWrapper>

                  <LatestWrapper>
                    <LatestWrapContent>
                      {" "}
                      {this.state.searchActive
                        ? this.renderSearchProducts()
                        : this.renderProducts()}
                    </LatestWrapContent>
                  </LatestWrapper>

                  {this.state.searchActive ? (
                    <Pagination2
                      totalProducts={this.state.blogSTotal}
                      productsPerPage={this.state.perPage}
                      current={this.state.currentS}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      changesearchPage={this.changesearchPage}
                    />
                  ) : (
                    <Pagination
                      totalProducts={this.props.blogs.totalBlogs}
                      productsPerPage={this.state.perPage}
                      current={this.props.blogs.current}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                    />
                  )}
                </PContainer>
              ) : (
                <PContainer bg={"white"} className="Nolist">
                  <NoListWrapper>
                    <NoListContainer>
                      <img src={listImage} alt="NoList" />
                      <h1>List is Empty</h1>
                      <h5>Please create items on the list.</h5>
                    </NoListContainer>
                  </NoListWrapper>
                </PContainer>
              )}
            </>
          )}
        </Container>

        {this.state.createContent && (
          <EditSection>
            <EditWrapSection>
              <EditContainer>
                <TitleWrap2>
                  {!this.state.createLoading && !this.state.createSuccess ? (
                    <TitleText>Create New blog list?</TitleText>
                  ) : (
                    <TitleText>Successful</TitleText>
                  )}
                </TitleWrap2>
                <hr />

                {!this.state.createLoading && !this.state.createSuccess && (
                  <>
                    {this.state.createError.length !== 0 &&
                      this.state.createError[0].response && (
                        <DeleteErrors>
                          {this.state.createError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.createError.length !== 0 &&
                      !this.state.createError[0].response && (
                        <DeleteErrors>
                          {this.state.createError[0].message}
                        </DeleteErrors>
                      )}
                    <form onSubmit={this.handleSubmit}>
                      <FormContentWrap>
                        <ImageUploadWrapper>
                          <ImageWrap>
                            <ImageTitle>Images</ImageTitle>
                            <ImageContainer>
                              <input
                                type="file"
                                name="imageupload"
                                id="upload"
                                accept="image/*"
                                onChange={this.imageHandler}
                              />
                              <label htmlFor="upload">
                                <ImageClick>
                                  <div>
                                    <FiUploadCloud />
                                  </div>
                                  <h5>Upload</h5>
                                </ImageClick>
                              </label>

                              {this.state.ImageUrl.length !== 0 && (
                                <>
                                  {this.state.ImageUrl.map((data, index) => {
                                    return (
                                      <Image>
                                        <img src={data} alt="" />
                                      </Image>
                                    );
                                  })}
                                </>
                              )}
                            </ImageContainer>
                            {this.state.errors.imagelink && (
                              <p>{this.state.errors.imagelink}</p>
                            )}
                          </ImageWrap>
                        </ImageUploadWrapper>
                        <FormContent3>
                          <label>Blog type*</label>
                          <select
                            name="blogtype"
                            value={this.state.blogtype}
                            onChange={this.handleChange}
                          >
                            <option value="">select blog type</option>
                            <option value="blog"> article</option>
                            <option value="video">video</option>
                          </select>

                          {this.state.errors.blogtype && (
                            <p>{this.state.errors.blogtype}</p>
                          )}
                        </FormContent3>
                        <FormContent3>
                          <label>Blog title*</label>
                          <input
                            type="text"
                            name="blogtitle"
                            value={this.state.blogtitle}
                            onChange={this.handleChange}
                          />
                          {this.state.errors.blogtitle && (
                            <p>{this.state.errors.blogtitle}</p>
                          )}
                        </FormContent3>
                        <FormContent3>
                          <label>Blog link*</label>
                          <input
                            type="text"
                            name="bloglink"
                            value={this.state.bloglink}
                            onChange={this.handleChange}
                          />
                          {this.state.errors.bloglink && (
                            <p>{this.state.errors.bloglink}</p>
                          )}
                        </FormContent3>
                      </FormContentWrap>

                      <EditButtonWrap2>
                        <CancelEditButton onClick={this.deactivatecreate}>
                          Cancel
                        </CancelEditButton>
                        <SaveEditButton>Create</SaveEditButton>
                      </EditButtonWrap2>
                    </form>
                  </>
                )}

                {this.state.createLoading && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.createSuccess && !this.state.createLoading && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.createSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.deactivatecreate}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </EditContainer>
            </EditWrapSection>
          </EditSection>
        )}

        {this.state.editContent && (
          <EditSection>
            <EditWrapSection>
              <EditContainer>
                <TitleWrap2>
                  {!this.state.editLoading && !this.state.editSuccess ? (
                    <TitleText>Edit blog list?</TitleText>
                  ) : (
                    <TitleText>Successful</TitleText>
                  )}

                  <CloseIcon2 onClick={this.deactivateedit}>
                    <AiOutlineClose />
                  </CloseIcon2>
                </TitleWrap2>
                <hr />

                {!this.state.editLoading && !this.state.editSuccess && (
                  <>
                    {this.state.editError.length !== 0 &&
                      this.state.editError[0].response && (
                        <DeleteErrors>
                          {this.state.editError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.editError.length !== 0 &&
                      !this.state.editError[0].response && (
                        <DeleteErrors>
                          {this.state.editError[0].message}
                        </DeleteErrors>
                      )}
                    <form onSubmit={this.handleSubmit2}>
                      <FormContentWrap>
                        <ImageUploadWrapper>
                          <ImageWrap>
                            <ImageTitle>Images</ImageTitle>
                            <ImageContainer>
                              <input
                                type="file"
                                name="imageupload"
                                id="upload"
                                accept="image/*"
                                onChange={this.imageHandler}
                              />
                              <label htmlFor="upload">
                                <ImageClick>
                                  <div>
                                    <FiUploadCloud />
                                  </div>
                                  <h5>Upload</h5>
                                </ImageClick>
                              </label>

                              {this.state.ImageUrl.length !== 0 && (
                                <>
                                  {this.state.ImageUrl.map((data, index) => {
                                    return (
                                      <Image>
                                        <img src={data} alt="" />
                                      </Image>
                                    );
                                  })}
                                </>
                              )}

                              {this.state.OldImageUrl.length !== 0 && (
                                <>
                                  {this.state.OldImageUrl.map((data, index) => {
                                    return (
                                      <Image>
                                        <img src={data} alt="" />
                                      </Image>
                                    );
                                  })}
                                </>
                              )}
                            </ImageContainer>
                            {this.state.errors.imagelink && (
                              <p>{this.state.errors.imagelink}</p>
                            )}
                          </ImageWrap>
                        </ImageUploadWrapper>
                        <FormContent3>
                          <label>Blog type*</label>
                          <select
                            name="blogtype"
                            value={this.state.blogtype}
                            onChange={this.handleChange}
                          >
                            <option value="">select blog type</option>
                            <option value="blog"> article</option>
                            <option value="video">video</option>
                          </select>

                          {this.state.errors.blogtype && (
                            <p>{this.state.errors.blogtype}</p>
                          )}
                        </FormContent3>
                        <FormContent3>
                          <label>Blog title*</label>
                          <input
                            type="text"
                            name="blogtitle"
                            value={this.state.blogtitle}
                            onChange={this.handleChange}
                          />
                          {this.state.errors.blogtitle && (
                            <p>{this.state.errors.blogtitle}</p>
                          )}
                        </FormContent3>
                        <FormContent3>
                          <label>Blog link*</label>
                          <input
                            type="text"
                            name="bloglink"
                            value={this.state.bloglink}
                            onChange={this.handleChange}
                          />
                          {this.state.errors.bloglink && (
                            <p>{this.state.errors.bloglink}</p>
                          )}
                        </FormContent3>
                      </FormContentWrap>

                      <EditButtonWrap2>
                        <CancelEditButton onClick={this.deactivatecreate}>
                          Cancel
                        </CancelEditButton>
                        <SaveEditButton type="submit">Update</SaveEditButton>
                      </EditButtonWrap2>
                    </form>
                  </>
                )}

                {this.state.editLoading && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.editSuccess && !this.state.editLoading && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.editSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.deactivateedit}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </EditContainer>
            </EditWrapSection>
          </EditSection>
        )}

        {this.state.deleteActive && (
          <DeleteContainer>
            <DeleteContent>
              <DeleteWrapper>
                <DeleteTitle>
                  {!this.state.loadDelete && !this.state.deleteSuccess ? (
                    <h5>Delete this Blog?</h5>
                  ) : (
                    <h5>Successful</h5>
                  )}
                  <CloseIcon onClick={this.closeDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                {!this.state.loadDelete && !this.state.deleteSuccess && (
                  <div>
                    {this.state.deleteError.length !== 0 &&
                      this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.deleteError.length !== 0 &&
                      !this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].message}
                        </DeleteErrors>
                      )}
                    <DeletesWrap>
                      <img src={deleteSvg} alt="delete" />
                    </DeletesWrap>
                    <DeleteText>
                      You’ll lose all the blog details. We can’t recover them
                      when deleted.
                    </DeleteText>
                    <DeleteText>
                      Are you sure you want to permanently delete this Blog?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                      <YesBtn onClick={() => this.removeBlog(this.state.ids)}>
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadDelete && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.deleteSuccess && !this.state.loadDelete && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContent>
          </DeleteContainer>
        )}
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    blogs: state.blogs,
  };
};

export default connect(mapStateToProps, { fetchBlogs })(Blog);
