import React from "react";
import {
  Wrapper,
  Container,
  ProfileSettingWrapper,
  Profile,
  ProfileDetailWrap,
  ProfileSetting,
  ProfileSetting1,
  SignOut,
  ProfileWrap,
  ProfileDetail,
  HamMenu,
} from "./style";
import ProfileImage from "../../../assets/profile.png";
import Cookies from "js-cookie";
import settingIcon from '../../../assets/icons/settingsMenu.svg'
import LogOutIcon from "../../../assets/icons/logout-outlined.svg";
import MobileLogo from "../../../assets/Mobilelogo.svg"
import { GiHamburgerMenu } from "react-icons/gi";
class ProfileSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activedrop: false, profile: "", user: {} };
    this.dropRef = React.createRef();
    this.handleOutsideClick = this.handleOutsideClick.bind(this);
  }

  componentDidMount() {
    let profileData = Cookies.get("profile")
    let userData = JSON.parse(Cookies.get("user")|| '{}');
    this.setState({user: userData})
    if (profileData){
      this.setState({profile: profileData})
    }
      document.addEventListener("click", this.handleOutsideClick);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleOutsideClick);
  }

  handleOutsideClick = (event) => {
    if (this.dropRef && !this.dropRef.current.contains(event.target)) {
      this.setState({ activedrop: false });
    }
  };

  handleProfileClick = () => {
    this.setState({ activedrop: !this.state.activedrop });
  };

  signingout = () => {
    Cookies.remove("_tk");
    Cookies.remove("sid");
    Cookies.remove("user");
    Cookies.remove("isLoggedIn");
    window.location.reload()
  };

   
  render() {
    return (
      <Wrapper>
        <Container>
          <HamMenu>
            <div onClick={() => this.props.toggle()}>
              <GiHamburgerMenu />
            </div>

          </HamMenu>
          <HamMenu>
            <img src={MobileLogo} alt="class and sass " />
          </HamMenu>
          <ProfileSettingWrapper ref={this.dropRef}>
            <ProfileWrap onClick={this.handleProfileClick}>
              <Profile>
                {this.state.profile ? (
                  <img src={this.state.profile} alt="" />
                ) : (
                  <img src={ProfileImage} alt="" />
                )}
              </Profile>
            </ProfileWrap>

            <ProfileDetailWrap active={this.state.activedrop}>
              <ProfileDetail>
                <ProfileSetting>
                  <h1> {this.state.user.firstname}</h1>
                  <h2>Admin</h2>
                </ProfileSetting>
                <ProfileSetting1 to="/dashboard/setting">
                  <img src={settingIcon} alt="setting" />
                  <h1> Setting</h1>
                </ProfileSetting1>
                <SignOut onClick={this.signingout}>
                  <img src={LogOutIcon} alt="logout" />

                  <h1> Logout</h1>
                </SignOut>
              </ProfileDetail>
            </ProfileDetailWrap>
          </ProfileSettingWrapper>
        </Container>
      </Wrapper>
    );
  }
}

export default ProfileSection;
