import React from "react";
import {
 
  EditBtn,
  TitleHead,
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,

  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  StatusContent,
  
  DeleteWrapper,
  DeleteTitle,
 
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  DeleteErrors,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  CreateWrap,
  CreateContainer,
  CreateWrapper,
  CreateTitle,
  CreateErrors,
  CreateText,
  CreateMessageWrap,
  CreateMessage,
  CreateBtn,
  OrderId,
  ViewButton,
  TCustomers,
  TitleSub,
  OrdersWrapper,
  OrdersLatestWrapper,
  DeleteBtn,
  DeletesWrap,
  ControlButtons,
  CreateTextWrap,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";
import numeral from "numeral";
import listImage from "../../../../assets/icons/NoList.svg";
import { BsSearch } from "react-icons/bs";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import { AiOutlineRight, AiOutlinePlus } from "react-icons/ai";
import deleteSvg from "../../../../assets/icons/delete.svg";
import axios from "axios";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import { fetchLocations } from "../../../../store/actions";
import { RiDeleteBinLine } from "react-icons/ri";
import { BASE_API_ } from "../../../../utils/base-url.config";
import Cookies from "js-cookie";

class MobileViewShipping extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      loadDelete: false,
      deleteSuccess: "",
      deleteError: [],
      createActive: false,
      courier: "",
      location: "",
      price: null,
      createError: [],
      loadCreate: false,
      createSuccess: "",
      page: undefined,
      perPage: 8,
      totalProducts: 0,
      errors: {},
      query: "",
      itemSlist: [],
      currentS: 1,
      itemSTotal: null,
      allsetItems: [],
      searchActive: false,
      pageLoading: false,
    };
  }

  componentDidMount() {
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
      }
    };
    const getToken = Cookies.get("_tk");
    let page;
    let id = this.props.pparams.params.id;

    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getDetails = async () => {
        await this.props.fetchLocations(
          page,
          this.state.perPage,
          id,
          this.redirect,
          getToken
        );

        this.setState({ courier: id });
      };

      getDetails();
    } else {
      const getDetails = async () => {
        await this.props.fetchLocations(
          page,
          this.state.perPage,
          id,
          this.redirect,
          getToken
        );

        this.setState({ courier: id });
      };

      getDetails();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      const getToken = Cookies.get("_tk");
      let page;
      let id = this.props.pparams.params.id;

      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const getDetails = async () => {
          await this.props.fetchLocations(
            page,
            this.state.perPage,
            id,
            this.redirect,
            getToken
          );

          this.setState({ courier: id });
        };

        getDetails();
      } else {
        const getDetails = async () => {
          await this.props.fetchLocations(
            page,
            this.state.perPage,
            id,
            this.redirect,
            getToken
          );

          this.setState({ courier: id });
        };

        getDetails();
      }
    }
  }

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");
  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetItems.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      itemSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };
  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.locations.allLocation;

    const resultData = allProducts.filter((shipping) => {
      let courier = shipping.location.toLowerCase();

      return courier.includes(query.toLowerCase());
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPage = 1;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      itemSlist: currentItems,
      currentS: 1,
      itemSTotal: totalOrders,
      allsetItems: showOrders,
      results: showOrders,
      loading: true,
      searchActive: true,
    });
  };

  activateCreate = () => {
    this.setState({
      createActive: true,
      deleteActive: false,
      cancelOrder: false,
    });
  };

  closeDelete = () => {
    this.setState({
      deleteActive: false,
      cancelOrder: false,
      createActive: false,
    });
  };
  closeCancel = () => {
    this.setState({ cancelActive: false, cancelOrder: false });
  };
  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };
  activateCancelOrder = (id) => {
    this.setState({ cancelOrder: true, deleteActive: false, ids: id });
  };

  handleChange = (e) => {
    if (e.target.name === "price") {
      if (isNaN(e.target.value)) {
        this.setState({
          errors: {
            price: "price should be a number",
          },
        });
      }
    }

    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    if (
      this.state.courier &&
      this.state.location &&
      this.state.price &&
      Object.keys(this.state.errors).length === 0
    ) {
      let values = {
        courier: this.state.courier,
        location: this.state.location,
        price: this.state.price,
      };
      const getToken = Cookies.get("_tk");
      axios
        .post(`${BASE_API_}/shipping/v1/location/create`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          this.setState({
            createSuccess: "location Created successfully",
            loadCreate: false,
          });
          window.location.reload();
        })
        .catch((error) => {
          let arrayError = [];
          arrayError.push(error);
          this.setState({ createError: arrayError, loadCreate: false });
        });
    } else {
      if (!this.state.location) {
        this.setState({
          errors: {
            location: "please insert location",
          },
        });
      }
      if (!this.state.courier) {
        this.setState({
          errors: {
            courier: "please insert Courier",
          },
        });
      }
    }
  };

  //   deleteOrder = (id) => {
  //     this.setState({ loadDelete: true });
  //     axios
  //       .delete(
  //         `${BASE_API_}/shipping/v1/removeLocation/${id}`
  //       )
  //       .then((success) => {
  //         this.setState({
  //           deleteSuccess: "Location Deleted successfully",
  //           loadDelete: false,
  //         });
  //         window.location.reload();
  //       })
  //       .catch((error) => {
  //         let arrayError = [];
  //         arrayError.push(error);
  //         this.setState({ deleteError: arrayError, loadDelete: false });
  //       });
  //   };

  //remove the courier Provider
  deleteOrder = (id) => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/shipping/v1/removeCouriers/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          deleteSuccess: "Courier Deleted successfully",
          loadDelete: false,
        });
        window.location.replace("/dashboard/shipping");
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadDelete: false });
      });
  };

  renderOrders = () => {
    return this.props.locations.location.map((data, index) => {
      let location;
      let namesCombined = data.location;

      if (Object.keys(namesCombined).length > 15) {
        location = namesCombined.slice(0, 15) + "...";
      } else {
        location = namesCombined;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>LocationID: {Id}</OrderId>

              <ViewButton to={`/dashboard/shipping/agent/${data._id}`}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Location</h1>

              <h2>{location}</h2>
            </TCustomers>

            <TStatus>
              <h1> Price</h1>
              <StatusContent>
                <div>
                  <p>{numeral(data.price).format("0,00")} KES</p>
                </div>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  renderSearchItems = () => {
    return this.state.itemSlist.map((data, index) => {
      let location;
      let namesCombined = data.location;

      if (Object.keys(namesCombined).length > 15) {
        location = namesCombined.slice(0, 15) + "...";
      } else {
        location = namesCombined;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>LocationID: {Id}</OrderId>

              <ViewButton to={`/dashboard/shipping/agent/${data._id}`}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Location</h1>

              <h2>{location}</h2>
            </TCustomers>

            <TStatus>
              <h1> Price</h1>
              <StatusContent>
                <div>
                  <p>{numeral(data.price).format("0,00")} KES</p>
                </div>
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };
  render() {
    return (
      <>
        <TitleHead>{this.props.locations.courierName}</TitleHead>
        <TitleSub>View individual courier provider information</TitleSub>

        <OrderContainer>
          <SelectionWrapper>
            <SearchWrapper>
              <form onSubmit={this.onFormSubmit}>
                <input
                  type="text"
                  name="query"
                  value={this.state.query}
                  onChange={this.handleInputChange}
                  placeholder="Search location "
                />
                <button>
                  {" "}
                  <BsSearch />
                </button>
              </form>
            </SearchWrapper>

            <ControlButtons>
              <EditBtn onClick={this.activateCreate}>
                <AiOutlinePlus />
              </EditBtn>
              <DeleteBtn onClick={this.activateDelete}>
                <RiDeleteBinLine />
              </DeleteBtn>
            </ControlButtons>
          </SelectionWrapper>

          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.locations.location.length > 0 ? (
                <>
                  <OrdersWrapper>
                    <OrdersLatestWrapper>
                      <LatestWrapper>
                        {this.state.searchActive ? (
                          <>
                            {this.state.itemSlist && (
                              <div>{this.renderSearchItems()}</div>
                            )}
                          </>
                        ) : (
                          <>
                            {this.props.locations.location && (
                              <div>{this.renderOrders()}</div>
                            )}
                          </>
                        )}
                      </LatestWrapper>
                    </OrdersLatestWrapper>
                  </OrdersWrapper>

                  {this.state.searchActive ? (
                    <Pagination2
                      totalItems={this.state.itemSTotal}
                      itemPerPage={this.state.perPage}
                      current={this.state.currentS}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      changesearchPage={this.changesearchPage}
                    />
                  ) : (
                    <Pagination
                      totalItems={this.props.locations.totalLocation}
                      itemPerPage={this.state.perPage}
                      current={this.props.locations.current}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      link={`/dashboard/shipping/view/${this.state.courier}`}
                    />
                  )}
                </>
              ) : (
                <PContainer bg={"white"} className="Nolist">
                  <NoListWrapper>
                    <NoListContainer>
                      <img src={listImage} alt="NoList" />
                      <h1>List is Empty</h1>
                      <h5>Please create items on the list.</h5>
                    </NoListContainer>
                  </NoListWrapper>
                </PContainer>
              )}
            </>
          )}

          {this.state.createActive && (
            <CreateWrap>
              <CreateContainer>
                <CreateWrapper>
                  <CreateTitle>
                    {!this.state.loadCreate && !this.state.createSuccess ? (
                      <h5>Create New Location? </h5>
                    ) : (
                      <h5>Successful </h5>
                    )}
                  </CreateTitle>

                  {!this.state.loadCreate && !this.state.createSuccess && (
                    <div className="creates">
                      {this.state.createError.length !== 0 &&
                        this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].response}
                          </CreateErrors>
                        )}
                      {this.state.createError.length !== 0 &&
                        !this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].message}
                          </CreateErrors>
                        )}
                      <form onSubmit={this.handleSubmit}>
                        <CreateTextWrap>
                          <CreateText>
                            <label>New Location*</label>
                            <input
                              type="text"
                              name="location"
                              value={this.state.location}
                              onChange={this.handleChange}
                            />
                          </CreateText>

                          <CreateText>
                            <label>Cost of transportation*</label>
                            <div>
                              <input
                                type="number"
                                name="price"
                                value={this.state.price}
                                onChange={this.handleChange}
                              />
                              <p>KES</p>
                            </div>
                          </CreateText>
                        </CreateTextWrap>

                        <ButtonWrap>
                          <CancelBtn onClick={this.closeDelete}>
                            Cancel
                          </CancelBtn>{" "}
                          <CreateBtn type="submit">Yes</CreateBtn>
                        </ButtonWrap>
                      </form>
                    </div>
                  )}

                  {this.state.loadCreate && (
                    <LoadingWrapper>
                      <Loader
                        type="TailSpin"
                        color="black"
                        height={50}
                        width={50}
                      />
                    </LoadingWrapper>
                  )}

                  {this.state.createSuccess && !this.state.loadCreate && (
                    <LoadingWrapper>
                      <CreateMessageWrap>
                        <CreateMessage>
                          {this.state.createSuccess}
                        </CreateMessage>
                        <CreateMessage>
                          this popup will be closed automatically.
                        </CreateMessage>
                        <CloseButton onClick={this.closeDelete}>
                          Close
                        </CloseButton>
                      </CreateMessageWrap>
                    </LoadingWrapper>
                  )}
                </CreateWrapper>
              </CreateContainer>
            </CreateWrap>
          )}

          {this.state.deleteActive && (
            <DeleteContainer>
              <DeleteWrapper>
                <DeleteTitle>
                  {!this.state.loadDelete && !this.state.deleteSuccess ? (
                    <h5>Delete this Courier? </h5>
                  ) : (
                    <h5>Successful </h5>
                  )}
                </DeleteTitle>

                {!this.state.loadDelete && !this.state.deleteSuccess && (
                  <div>
                    {this.state.deleteError.length !== 0 &&
                      this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.deleteError.length !== 0 &&
                      !this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].message}
                        </DeleteErrors>
                      )}

                    <DeletesWrap>
                      <img src={deleteSvg} alt="delete" />
                    </DeletesWrap>
                    <DeleteText>
                      You’ll lose all the courier details. We can’t recover them
                      when deleted.
                    </DeleteText>
                    <DeleteText>
                      Are you sure you want to permanently delete this Courier?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                      <YesBtn
                        onClick={() => this.deleteOrder(this.state.courier)}
                      >
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadDelete && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.deleteSuccess && !this.state.loadDelete && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically.
                      </DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContainer>
          )}
        </OrderContainer>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    locations: state.locations,
  };
};

export default connect(mapStateToProps, { fetchLocations })(MobileViewShipping);
