/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  EditBtn,
  PContainer,
  MainWrapper,
  SideWrapper,
  ProductDetailsWrap,
  FormContentWrap,
  ProductDescriptionWrap,
  ImageUploadWrapper,
  FormContentWrap1,
  FormContent,
  FormContentDesc,
  ImageTitle,
  ImageContainer,
  Image,
  ImageWrap,
  PriceWrapper,
  StatusWrapper,
  TagsWrapper,
  CategoriesWrapper,
  VisibilityWrapper,
  PriceContainer,
  StatusContainer,
  TagsContainer,
  VContainer,
  ContentWrap,
  Check,
  CatWrapper,
  ContentWrap2,
  FeaturedWrapper,
  FeaturedContainer,
  ProductPriceWrap,
  ProductSize,
  ProductSizeTitle,
  SizeDisplay,
  ImageContainer2,
  ImageStockWrap,
  SKUWrap,
  ImageStock,
  ImageClick,
  SideWrap1,
  TStockWrapper,
  TTStock,
  TTReserved,
  SizeDisplayContent,
} from "./styles";
import {BASE_API_} from '../../../utils/base-url.config'

const ViewProduct = ({ products, pparams, historyProps, categories }) => {
  const [renderProduct, setRenderProduct] = useState({});
  const [submitError, setSubmitError] = useState([]);
  useEffect(() => {
    let id = pparams.params.id;
    const getProducts = JSON.parse(sessionStorage.getItem("products") || "[]");

    if (getProducts.length !== 0) {
      const productDetails = async () => {
        const checkProduct = await getProducts.filter((data) => {
          if (data._id === id) {
           
            return data;
          }
         
          return null;
        });
        if (checkProduct.length !== 0) {
          setRenderProduct(...checkProduct);
        } else {
          historyProps.goBack();
        }
      };

      productDetails();
    } else {
      axios
        .get(`${BASE_API_}/products/v1/products/${id}`)
        .then((success) => {
          setRenderProduct({ ...success.data });
        })
        .catch((error) => {
          setSubmitError([error]);
        });
    }
  }, [pparams, historyProps]);
  return (
    <Container>
      <HeadWrap>
        <TitleWrap>
          <TitleHead>Products</TitleHead>
          <EditBtn to={`/dashboard/productlist/update/${renderProduct._id}`}>
            Edit
          </EditBtn>
        </TitleWrap>
        <TitleSub>View product item details</TitleSub>
      </HeadWrap>

      <PContainer>
        <MainWrapper>
          <ProductDetailsWrap>
            <FormContentWrap>
              <label htmlFor="productName">Product title*</label>
              <input
                type="text"
                name="name"
                value={renderProduct.name}
                readOnly
                id="productName"
              />
            </FormContentWrap>

            <FormContentDesc>
              <label htmlFor="descriptions">Product Description*</label>
              <textarea
                type="text"
                name="description"
                value={renderProduct.description}
                readOnly
                id="descriptions"
              />
            </FormContentDesc>
          </ProductDetailsWrap>

          <ProductPriceWrap>
            <FormContentWrap1>
              <FormContent>
                <label htmlFor="cost">Cost per item</label>
                <input
                  type="text"
                  name="costperitem"
                  value={renderProduct.costperitem}
                  readOnly
                  id="cost"
                />
              </FormContent>
              <FormContent>
                <label htmlFor="Price">Price *</label>
                <input
                  type="text"
                  name="price"
                  value={renderProduct.price}
                  readOnly
                  id="Price"
                />
              </FormContent>
            </FormContentWrap1>
          </ProductPriceWrap>

          <ProductSize>
            <ProductSizeTitle>Size</ProductSizeTitle>

            <SizeDisplay>
              {" "}
              {renderProduct.sizes && (
                <>
                  {renderProduct.sizes.map((data, index) => {
                    return (
                      <SizeDisplayContent key={index}>
                        <p>{data}</p>
                      </SizeDisplayContent>
                    );
                  })}
                </>
              )}
            </SizeDisplay>
          </ProductSize>

          <ImageUploadWrapper>
            <ImageWrap>
              <ImageTitle>Color</ImageTitle>
              {renderProduct.productTypes && (
                <>
                  {renderProduct.productTypes.map((data, index) => {
                    return (
                      <ImageContainer2 key={index}>
                        <ImageTitle>{data.Colortype}</ImageTitle>
                        <ImageStockWrap>
                          <SKUWrap>
                            <label>SKU</label>
                            <p>
                              SKUcan contain product color, vendor or packaging
                              type.(i.e Sets)
                            </p>
                            <input
                              type="text"
                              name={`sku-${data}`}
                              value={data.SKU}
                              readOnly
                            />
                          </SKUWrap>

                          <ImageStock>
                            <label>Stock*</label>
                            <input
                              type="text"
                              name={`stock-${data}`}
                              value={data.Stock}
                              readOnly
                            />
                          </ImageStock>
                        </ImageStockWrap>
                        <ImageContainer>
                          {data.Images.length !== 0 && (
                            <>
                              {data.Images.map((mainData) => {
                                return (
                                  <>
                                    <Image key={mainData.imageId._id}>
                                      <img
                                        src={`${BASE_API_}${mainData.imageId.path}`}
                                        alt=""
                                      />
                                    </Image>
                                  </>
                                );
                              })}
                            </>
                          )}
                        </ImageContainer>
                      </ImageContainer2>
                    );
                  })}
                </>
              )}
            </ImageWrap>
          </ImageUploadWrapper>
        </MainWrapper>

        <SideWrapper>
          <SideWrap1>
            <StatusWrapper>
              <StatusContainer>
                <label>Status *</label>
                <p>{renderProduct.active ? "Active" : "Inactive"}</p>
              </StatusContainer>
            </StatusWrapper>

            <FeaturedWrapper>
              <FeaturedContainer>
                <label>Featured </label>
                <p>{renderProduct.featured ? "Active" : "Inactive"}</p>
              </FeaturedContainer>
            </FeaturedWrapper>

            <TagsWrapper>
              <TagsContainer>
                <label>Tags </label>
                <p>{renderProduct.tags}</p>
              </TagsContainer>
            </TagsWrapper>
            <CategoriesWrapper>
              <CatWrapper>
                <label>Categories</label>
                {categories.length !== 0 && renderProduct.category && (
                  <div>
                    {categories.map((data, index) => {
                      return (
                        <ContentWrap key={data._id}>
                          <input
                            type="radio"
                            id={data.name}
                            name="category"
                            value={data._id}
                            readOnly
                            checked={
                              data._id === renderProduct.category._id
                                ? true
                                : false
                            }
                          />
                          <label htmlFor={data.name}>
                            <p>{data.name}</p>
                          </label>
                        </ContentWrap>
                      );
                    })}
                  </div>
                )}
              </CatWrapper>
            </CategoriesWrapper>
          </SideWrap1>

          <VisibilityWrapper>
            <VContainer>
              <label>Visibility</label>
              <ContentWrap2>
                <Check className={renderProduct.active ? "active" : ""} />{" "}
                <p>Published on site</p>
              </ContentWrap2>
            </VContainer>
          </VisibilityWrapper>

          <TStockWrapper>
            <TTStock>
              <label htmlFor="TotalStock">Total Stock* </label>
              <input
                type="text"
                name="totalStock"
                value={renderProduct.totalStock}
                readOnly
                id="TotalStock"
              />
            </TTStock>
            <hr />
            <TTReserved>
              <label htmlFor="Reserved">Reserved </label>
              <input
                type="text"
                name="reserved"
                value={renderProduct.reservedStock}
                readOnly
                id="Reserved"
              />
            </TTReserved>
          </TStockWrapper>
        </SideWrapper>
      </PContainer>
    </Container>
  );
};

export default ViewProduct;
