import React from "react";
import {
  LatestWrapper,
  LatestWrapContent,
  ProductContainer,
  ProductImageWrap,
  ProductContentWrap,
  ProductContent,
  Ptitle,
  PPrice,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  LoadingWrapper,
  CloseButton,
  DeleteMessage,
  DeleteMessageWrap,
  DeleteErrors,
} from "./styles";
import { AiOutlineClose } from "react-icons/ai";
import Pimage from "../../../assets/default.png";
import DropDown from "../GridDropDown";
import Pagination from "./pagination";
import axios from "axios";
import Loader from "react-loader-spinner";
import Pagination2 from "./pagination2";
import {BASE_API_} from '../../../utils/base-url.config';
import Cookies from "js-cookie";
import numeral from "numeral";
class GridContent extends React.Component {
  state = {
    deleteActive: false,
    deleteMessage: "",
    deleteError: [],
    loadDelete: false,
    ids: "",
  };

  closeDelete = () => {
    this.setState({ deleteActive: false });
  };
  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };

  deleteActionAccepted = () => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/products/v1/delete/${this.state.ids}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ deleteMessage: success.data, loadDelete: false });
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ loadDelete: false });
        this.setState({ deleteError: arrayError });
      });
  };

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");

  renderProducts = () => {
    return this.props.productlist.map((data, index) => {
      let pName = data.name;
      if (Object.keys(pName).length > 18) {
        pName = data.name.slice(0, 18) + "...";
      }
      return (
        <ProductContainer>
          <ProductImageWrap>
            {data.featuredImage ? (
              <img src={`${BASE_API_}${data.featuredImage.path}`} alt="" />
            ) : (
              <img src={Pimage} alt="" />
            )}
          </ProductImageWrap>
          <ProductContentWrap>
            <ProductContent>
              <Ptitle>{pName}</Ptitle>
              <PPrice>{this.ShortTotals(data.price)} KES</PPrice>
            </ProductContent>
            <DropDown activateDelete={this.activateDelete} ids={data._id} />
          </ProductContentWrap>
        </ProductContainer>
      );
    });
  };

  //render search products
  renderSearchProducts = () => {
    return this.props.productSlist.map((data, index) => {
      let pName = data.name;
      if (Object.keys(pName).length > 18) {
        pName = data.name.slice(0, 18) + "...";
      }
      return (
        <ProductContainer>
          <ProductImageWrap>
            {data.featuredImage ? (
              <img src={`${BASE_API_}${data.featuredImage.path}`} alt="" />
            ) : (
              <img src={Pimage} alt="" />
            )}
          </ProductImageWrap>
          <ProductContentWrap>
            <ProductContent>
              <Ptitle>{pName}</Ptitle>
              <PPrice>{this.ShortTotals(data.price)} KES</PPrice>
            </ProductContent>
            <DropDown activateDelete={this.activateDelete} ids={data._id} />
          </ProductContentWrap>
        </ProductContainer>
      );
    });
  };
  render() {
    return (
      <>
        <LatestWrapper>
          <LatestWrapContent>
            {" "}
            {this.props.searchActive
              ? this.renderSearchProducts()
              : this.renderProducts()}
          </LatestWrapContent>
        </LatestWrapper>
        {this.props.searchActive ? (
          <Pagination2
            totalProducts={this.props.productSTotal}
            productsPerPage={this.props.perPage}
            current={this.props.currentS}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
            changesearchPage={this.props.changesearchPage}
          />
        ) : (
          <Pagination
            totalProducts={this.props.totalProducts}
            productsPerPage={this.props.perPage}
            current={this.props.current}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
          />
        )}

        {this.state.deleteActive && (
          <DeleteContainer>
            {!this.state.loadDelete && !this.state.deleteMessage && (
              <DeleteWrapper>
                {this.state.deleteError.length !== 0 &&
                  this.state.deleteError[0].response && (
                    <DeleteErrors>
                      {this.state.deleteError[0].response}
                    </DeleteErrors>
                  )}

                {this.state.deleteError.length !== 0 &&
                  !this.state.deleteError[0].response && (
                    <DeleteErrors>
                      {this.state.deleteError[0].message}
                    </DeleteErrors>
                  )}

                <DeleteTitle>
                  <h5>Delete this product? </h5>
                  <CloseIcon onClick={this.closeDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                <DeleteText>
                  You’ll lose all this product details. We can’t recover them
                  when deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to <h5>permanently</h5> delete this
                  product?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn onClick={this.deleteActionAccepted}>Yes</YesBtn>
                </ButtonWrap>
              </DeleteWrapper>
            )}

            {this.state.loadDelete && (
              <DeleteWrapper>
                <LoadingWrapper>
                  <Loader
                    type="TailSpin"
                    color="black"
                    height={50}
                    width={50}
                  />
                </LoadingWrapper>
              </DeleteWrapper>
            )}

            {this.state.deleteMessage && !this.state.loadDelete && (
              <DeleteWrapper>
                <LoadingWrapper>
                  <DeleteMessageWrap>
                    <DeleteMessage>{this.state.deleteMessage}</DeleteMessage>
                    <CloseButton onClick={this.closeDelete}>Close</CloseButton>
                  </DeleteMessageWrap>
                </LoadingWrapper>
              </DeleteWrapper>
            )}
          </DeleteContainer>
        )}
      </>
    );
  }
}

export default GridContent;
