import React from "react";
import {
  Wrapper,
  Container,
  LogoWrapper,
  LogoT,
  MenuWrap,
  MenuitemLink,
  SideMenuWrap,
  SideMenuItem,
  ArrowI,
} from "./style";
import SubMenu from "../SubMenu";
import Cookies from "js-cookie";

import { connect } from "react-redux";
import { checkUser } from "../../../store/actions";
import { NavData } from "../../../data/navigation";
import settingIcon from "../../../assets/icons/settings.svg";
import LogoImage from "../../../assets/DashboardLogo.svg";
import smallLogoImage from "../../../assets/smalldashboard.svg";
import arrowleft from "../../../assets/icons/ArrowLeft.svg";
import arrowRight from "../../../assets/icons/feather_chevrons-right.svg";
class Navigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = { log: false, activeSlide: false, title: "", active: "" };
  }

  componentDidMount() {
    if (this.props.paramslinks.path === "/dashboard") {
      this.setState({ title: "dashboard", active: "dashboard" });
    } else if (
      this.props.paramslinks.path === "/dashboard/:title" ||
      this.props.paramslinks.path === "/dashboard/:title/:specific" ||
      this.props.paramslinks.path === "/dashboard/:title/:specific/:id"
    ) {
      if (this.props.paramslinks.params.title === "orders") {
        this.setState({ title: "orders", active: "orders" });
      } else if (this.props.paramslinks.params.title === "customers") {
        this.setState({ title: "customers", active: "customers" });
      } else if (this.props.paramslinks.params.title === "transaction") {
        this.setState({ title: "transaction", active: "transaction" });
      } else if (this.props.paramslinks.params.title === "setting") {
        this.setState({ title: "setting", active: "setting" });
      } else if (this.props.paramslinks.params.title === "shipping") {
        this.setState({ title: "shipping", active: "shipping" });
      } else if (this.props.paramslinks.params.title === "blog") {
        this.setState({ title: "blog", active: "blog" });
      } else if (
        this.props.paramslinks.params.title === "categories" ||
        this.props.paramslinks.params.title === "productlist" ||
        this.props.paramslinks.params.title === "brands"
      ) {
        this.setState({ title: "products", active: "products" });
      }
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.paramslinks.path !== prevProps.paramslinks.path) {
      
      if (this.props.paramslinks.path === "/dashboard") {
        this.setState({ title: "dashboard", active: "dashboard" });
      } else if (
        this.props.paramslinks.path === "/dashboard/:title" ||
        this.props.paramslinks.path === "/dashboard/:title/:specific"
      ) {
       
        if (this.props.paramslinks.params.title === "orders") {
          this.setState({ title: "orders", active: "orders" });
        } else if (this.props.paramslinks.params.title === "customers") {
          this.setState({ title: "customers", active: "customers" });
        } else if (this.props.paramslinks.params.title === "transaction") {
          this.setState({ title: "transaction", active: "transaction" });
        } else if (this.props.paramslinks.params.title === "setting") {
          this.setState({ title: "setting", active: "setting" });
        } else if (this.props.paramslinks.params.title === "shipping") {
          this.setState({ title: "shipping", active: "shipping" });
        } else if (this.props.paramslinks.params.title === "blog") {
          this.setState({ title: "blog", active: "blog" });
        } else if (
          this.props.paramslinks.params.title === "categories" ||
          this.props.paramslinks.params.title === "productlist" ||
          this.props.paramslinks.params.title === "brands"
        ) {
          this.setState({ title: "products", active: "products" });
        }
      }
    } else if (
      this.props.paramslinks.path === "/dashboard/:title" &&
      this.props.paramslinks.params.title !== prevProps.paramslinks.params.title
    ) {
      
      if (this.props.paramslinks.params.title === "orders") {
        this.setState({ title: "orders", active: "orders" });
      } else if (this.props.paramslinks.params.title === "customers") {
        this.setState({ title: "customers", active: "customers" });
      } else if (this.props.paramslinks.params.title === "transaction") {
        this.setState({ title: "transaction", active: "transaction" });
      } else if (this.props.paramslinks.params.title === "setting") {
        this.setState({ title: "setting", active: "setting" });
      } else if (this.props.paramslinks.params.title === "shipping") {
        this.setState({ title: "shipping", active: "shipping" });
      } else if (this.props.paramslinks.params.title === "blog") {
        this.setState({ title: "blog", active: "blog" });
      } else if (
        this.props.paramslinks.params.title === "categories" ||
        this.props.paramslinks.params.title === "productlist" ||
        this.props.paramslinks.params.title === "brands"
      ) {
        this.setState({ title: "products", active: "products" });
      }
    }
  }

  signingout = () => {
    Cookies.remove("_tk");
    Cookies.remove("sid");
    Cookies.remove("user");
    Cookies.remove("isLoggedIn");
  };

  changeNavWidth=()=>{
   this.setState({activeSlide: !this.state.activeSlide});
  }
  changeNavWidthProduct = ()=>{
    if(this.state.activeSlide === false){
        this.setState({ activeSlide: true });
    }
  }
  render() {
    return (
      <Wrapper width={this.props.width} activeslide={this.state.activeSlide}>
        <Container
          width={this.props.width}
          activeslide={this.state.activeSlide}
        >
          <LogoWrapper activeslide={this.state.activeSlide}>
            <LogoT activeslide={this.state.activeSlide}>
              {this.state.activeSlide ? (
                <img src={LogoImage} alt="" />
              ) : (
                <img src={smallLogoImage} alt="" />
              )}
            </LogoT>
            <ArrowI
              activeslide={this.state.activeSlide}
              onClick={this.changeNavWidth}
            >
              {this.state.activeSlide ? (
                <img src={arrowleft} alt="" />
              ) : (
                <img src={arrowRight} alt="" />
              )}
            </ArrowI>
          </LogoWrapper>
          <MenuWrap>
            {NavData.map((data, index) => {
              return (
                <SubMenu
                  key={index}
                  navdata={data}
                  active={this.state.title}
                  activeslide={this.state.activeSlide}
                  changewidth={this.changeNavWidthProduct}
                />
              );
            })}
            <hr />
            <MenuitemLink
              name="setting"
              active={this.state.title}
              to="/dashboard/setting"
              activeslide={this.state.activeSlide}
            >
              <SideMenuWrap activeslide={this.state.activeSlide}>
                <img src={settingIcon} alt="" />
                <SideMenuItem activeslide={this.state.activeSlide}>
                  Setting
                </SideMenuItem>
              </SideMenuWrap>
            </MenuitemLink>
          </MenuWrap>
        </Container>
      </Wrapper>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    logs: state.logs,
  };
};

export default connect(mapStateToProps, {
  checkUser,
})(Navigation);
