import React from "react";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  CreateBtn,
  PContainer,
  SelectionWrapper,
  SearchWrapper,
  MobileSearchWrapper,
  MobileSearch,
  CreateBtn2,
  CreateBtnsWrap,
  CategoryBtn,
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  StatusContent,
  YellowC,
  NotActiveC,
  TItems,
  DeleteWrapper,
  DeleteTitle,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  LoadingWrapper,
  CloseButton,
  DeleteMessage,
  DeleteMessageWrap,
  DeleteErrors,
  Wrapper,
  CreateWrap,
  CreateContainer,
  CreateWrapper,
  CreateTitle,
  CreateErrors,
  CreateText,
  CreateMessageWrap,
  CreateMessage,
  CreatesBtn,
  CreateTextError,
  OrderId,
  ViewButton,
  OrdersWrapper,
  OrdersLatestWrapper,
  ButtonsWrap,
  CategoryBtn2,
  DeletesWrap,
  EditsBtn,
  CreateTextWrap,
  NoListWrapper,
  NoListContainer,
 
} from "./styles";
import { BsSearch } from "react-icons/bs";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine } from "react-icons/ri";
import { connect } from "react-redux";
import { fetchAdminCategories } from "../../../../store/actions";
import { BiPencil } from "react-icons/bi";
import axios from "axios";
import Loader from "react-loader-spinner";
import deleteSvg from "../../../../assets/icons/delete.svg";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import listImage from "../../../../assets/icons/NoList.svg";
import {BASE_API_} from '../../../../utils/base-url.config';
import Cookies from "js-cookie";

class Categories extends React.Component {
  constructor(props) {
    super(props);

    // this.handleChange = handleChange

    this.state = {
      listActive: false,
      gridActive: false,
      productlist: this.props.products,
      productTotal: this.props.totalProducts,

      current: this.props.current,
      searchActive: false,
      itemSlist: [],
      currentS: null,
      itemSTotal: null,
      allsetItems:[],


      query: "",
      priceQuery: "",
      statusQuery: "",
      categoryQuery: "",
      page: undefined,
      perPage: 8,
      totalCategories: 0,
      avproducts: [],

      createActive: false,
      loadCreate: false,
      createSuccess: "",
      createError: [],
      category: "",
      status: "",
      errors: {},

      editActive: false,
      loadEdit: false,
      editSuccess: "",
      editError: [],

      deleteActive: false,
      ids: "",
      loadDelete: false,
      deleteSuccess: "",
      deleteError: [],
      pageLoading: false,
    };
  }

  componentDidMount() {
    //console.log(window.readyState);
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
      }
    };
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");

      const CategoryFetching = async () => {
        await this.props.fetchAdminCategories(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );

        this.setState({
          page: page,
        });
      };

      CategoryFetching();
    } else {
      const CategoryFetching = async () => {
        await this.props.fetchAdminCategories(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );

        this.setState({
          page: page,
        });
      };

      CategoryFetching();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
       const getToken = Cookies.get("_tk");
      let page;
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const CategoryFetching = async () => {
          await this.props.fetchAdminCategories(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );

          this.setState({
            page: page,
          });
        };

        CategoryFetching();
      } else {
        const CategoryFetching = async () => {
          await this.props.fetchAdminCategories(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );

          this.setState({
            page: page,
          });
        };

        CategoryFetching();
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  activateCreate = () => {
    this.setState({
      createActive: true,
      deleteActive: false,
      cancelOrder: false,
    });
  };

  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };

  closeDelete = () => {
    this.setState({
      deleteActive: false,
      cancelOrder: false,
      createActive: false,
      errors: {},
      categoryError: "",
      ids: "",
      deleteSuccess: "",
    });
  };

  handleChange = (e) => {
    if (e.target.name === "category") {
      this.setState({
        category: e.target.value,
        categoryError: "",
      });
    }
    if (e.target.name === "status") {
      this.setState({
        status: e.target.value,
        errors: {},
      });
    }
   
  };

  //creation of the category
  handleSubmit = (e) => {
    e.preventDefault();
    
    this.setState({ loadCreate: true });
    if (
      this.state.category &&
      this.state.status &&
      Object.keys(this.state.errors).length === 0 &&
      !this.state.categoryError
    ) {
      let values = {
        category: this.state.category,
        status: this.state.status,
      };
      const getToken = Cookies.get("_tk");
      axios
        .post(`${BASE_API_}/category/v1/create`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          this.setState({
            createSuccess: "Category Created successfully",
            loadCreate: false,
          });
          window.location.reload();
        })
        .catch((error) => {
          let arrayError = [];
          arrayError.push(error);
          this.setState({ createError: arrayError, loadCreate: false });
        });
    } else {
      this.setState({ loadCreate: false });
      if (!this.state.category) {
        this.setState({
          categoryError: "please insert category",
        });
      }
      if (!this.state.status) {
        this.setState({
          errors: {
            ...this.state.errors,
            status: "please select Status",
          },
        });
      }
    }
  };

  //edit the category
  handleSubmit2 = (e) => {
    e.preventDefault();
    
    this.setState({ loadCreate: true });
    if (
      this.state.category &&
      this.state.status &&
      Object.keys(this.state.errors).length === 0 &&
      !this.state.categoryError
    ) {
      let values = {
        category: this.state.category,
        status: this.state.status,
      };
      const getToken = Cookies.get("_tk");
      axios
        .patch(`${BASE_API_}/category/v1/update/${this.state.ids}`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          this.setState({
            createSuccess: "Category Updated successfully",
            loadCreate: false,
          });
          window.location.reload();
        })
        .catch((error) => {
          let arrayError = [];
          arrayError.push(error);
          this.setState({ createError: arrayError, loadCreate: false });
        });
    } else {
      this.setState({ loadCreate: false });
      if (!this.state.category) {
        this.setState({
          categoryError: "please insert category",
        });
      }
      if (!this.state.status) {
        this.setState({
          errors: {
            ...this.state.errors,
            status: "please select Status",
          },
        });
      }
    }
  };

  //search
  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetItems.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      itemSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    
    const allProducts = this.props.categories.allItems;

    const resultData = allProducts.filter((category) => {
      let categories = category.name.toLowerCase();

      return categories.includes(query.toLowerCase());
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPage = 1;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      itemSlist: currentItems,
      currentS: 1,
      itemSTotal: totalOrders,
      allsetItems: showOrders,
      results: showOrders,
      loading: true,
      searchActive: true,
    });
  };

  //edit
  activateEdit = (category) => {
    this.setState({
      category: category.name,
      status: category.status,
      ids: category._id,
      editActive: true,
    });
  };

  closeEdit = () => {
    this.setState({
      category: "",
      status: "",
      editActive: false,
      error: {},
      ids: "",
    });
  };

  deleteItem = (id) => {
    this.setState({ loadDelete: true });
     const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/category/v1/deleteCategory/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        if (success.data !== "Category has  products") {
          this.setState({
            deleteSuccess: "This category was successfully created.",
            loadDelete: false,
          });
          window.location.reload();
        } else {
          this.setState({
            deleteSuccess:
              "You cannot delete this category, it has product items.",
            loadDelete: false,
          });
        }
      })
      .catch((error) => {
     
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadDelete: false });
      });
  };

  renderProducts = () => {
    return this.props.categories.items.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = "..." + data._id.split("").slice(13).join("");
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>
                <h1>CategoryID: {Id}</h1>{" "}
                <TDate>
                  <div>
                    <h1>Date Created :</h1>
                    <h5>
                      {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                    </h5>
                  </div>
                </TDate>
              </OrderId>

              <ViewButton>
                <EditsBtn onClick={() => this.activateEdit(data)}>
                  <BiPencil />
                </EditsBtn>
                <div onClick={() => this.activateDelete(data._id)}>
                  <RiDeleteBinLine />
                </div>
              </ViewButton>
            </TId>

            <TItems>
              <h1>Category name</h1>

              <h2>{pName}</h2>
            </TItems>

            <TStatus>
              <h1>Status</h1>
              <StatusContent>
                {data.status ? (
                  <div>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </div>
                ) : (
                  <div>
                    <NotActiveC />
                    <p>Not Active</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  //rendering searchProducts
  renderSearchProducts = () => {
    return this.state.itemSlist.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>
                <h1>CategoryID: {Id}</h1>{" "}
                <TDate>
                  <div>
                    <h1>Date Created :</h1>
                    <h5>
                      {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                    </h5>
                  </div>
                </TDate>
              </OrderId>

              <ViewButton>
                <EditsBtn onClick={() => this.activateEdit(data)}>
                  <BiPencil />
                </EditsBtn>
                <div onClick={() => this.activateDelete(data._id)}>
                  <RiDeleteBinLine />
                </div>
              </ViewButton>
            </TId>

            <TItems>
              <h1>Category name</h1>

              <h2>{pName}</h2>
            </TItems>

            <TStatus>
              <h1>Status</h1>
              <StatusContent>
                {data.status ? (
                  <div>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </div>
                ) : (
                  <div>
                    <NotActiveC />
                    <p>Not Active</p>
                  </div>
                )}
              </StatusContent>
            </TStatus>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  render() {
    return (
      <Wrapper>
        <Container>
          <HeadWrap>
            <TitleWrap>
              <TitleHead>Categories</TitleHead>
              <CreateBtnsWrap>
                <CreateBtn onClick={this.activateCreate}>
                  + Create New
                </CreateBtn>
              </CreateBtnsWrap>
            </TitleWrap>
            <TitleSub>View Product List</TitleSub>
          </HeadWrap>

          <MobileSearchWrapper>
            <MobileSearch>
              <form onSubmit={this.onFormSubmit}>
                <input
                  type="text"
                  name="query"
                  value={this.state.query}
                  onChange={this.handleInputChange}
                  placeholder="Search..."
                />
                <button>
                  {" "}
                  <BsSearch />
                </button>
              </form>
            </MobileSearch>

            <ButtonsWrap>
              <CategoryBtn to="/dashboard/productlist">
                View Products
              </CategoryBtn>
              <CreateBtn2 onClick={this.activateCreate}>
                <AiOutlinePlus />
              </CreateBtn2>
            </ButtonsWrap>
          </MobileSearchWrapper>

          <CategoryBtn2 to="/dashboard/productlist">View Products</CategoryBtn2>

          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.categories.items.length > 0 ? (
                <PContainer bg={this.state.bgColor}>
                  <SelectionWrapper>
                    <SearchWrapper>
                      <form onSubmit={this.onFormSubmit}>
                        <input
                          type="text"
                          name="query"
                          value={this.state.query}
                          onChange={this.handleInputChange}
                          placeholder="Search..."
                        />
                        <button>
                          {" "}
                          <BsSearch />
                        </button>
                      </form>
                    </SearchWrapper>
                  </SelectionWrapper>

                  <OrdersWrapper>
                    <OrdersLatestWrapper>
                      <LatestWrapper>
                        {this.state.searchActive
                          ? this.renderSearchProducts()
                          : this.renderProducts()}
                      </LatestWrapper>
                    </OrdersLatestWrapper>
                  </OrdersWrapper>

                  {this.state.searchActive ? (
                    <Pagination2
                      totalItems={this.state.itemSTotal}
                      itemPerPage={this.state.perPage}
                      current={this.state.currentS}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      changesearchPage={this.changesearchPage}
                    />
                  ) : (
                    <Pagination
                      totalItems={this.props.categories.totalItems}
                      itemPerPage={this.state.perPage}
                      current={this.props.categories.current}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      link={`/dashboard/categories`}
                    />
                  )}
                </PContainer>
              ) : (
                <PContainer bg={"white"} className="Nolist">
                  <NoListWrapper>
                    <NoListContainer>
                      <img src={listImage} alt="NoList" />
                      <h1>List is Empty</h1>
                      <h5>Please create items on the list.</h5>
                    </NoListContainer>
                  </NoListWrapper>
                </PContainer>
              )}
            </>
          )}
        </Container>

        {this.state.createActive && (
          <CreateWrap>
            <CreateContainer>
              <CreateWrapper>
                <CreateTitle>
                  {!this.state.loadCreate && !this.state.createSuccess ? (
                    <h5>Create a Product Category? </h5>
                  ) : (
                    <h5>Successful </h5>
                  )}
                </CreateTitle>

                {!this.state.loadCreate && !this.state.createSuccess && (
                  <div className="creates">
                    {this.state.createError.length !== 0 &&
                      this.state.createError[0].response && (
                        <CreateErrors>
                          {this.state.createError[0].response}
                        </CreateErrors>
                      )}
                    {this.state.createError.length !== 0 &&
                      !this.state.createError[0].response && (
                        <CreateErrors>
                          {this.state.createError[0].message}
                        </CreateErrors>
                      )}
                    <form onSubmit={this.handleSubmit}>
                      <CreateTextWrap>
                        <CreateText>
                          <label>Category name*</label>
                          <input
                            type="text"
                            name="category"
                            value={this.state.category}
                            onChange={this.handleChange}
                          />
                          {this.state.categoryError && (
                            <CreateTextError>
                              {this.state.categoryError}
                            </CreateTextError>
                          )}
                        </CreateText>

                        <CreateText>
                          <label>status*</label>
                          <div>
                            <select
                              name="status"
                              value={this.state.status}
                              onChange={this.handleChange}
                            >
                              <option value="">select status</option>
                              <option value={true}>activate</option>
                              <option value={false}>deactivate</option>
                            </select>
                          </div>
                          {this.state.errors.status && (
                            <CreateTextError>
                              {this.state.errors.status}
                            </CreateTextError>
                          )}
                        </CreateText>
                      </CreateTextWrap>

                      <ButtonWrap>
                        <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                        <CreatesBtn type="submit">Submit</CreatesBtn>
                      </ButtonWrap>
                    </form>
                  </div>
                )}

                {this.state.loadCreate && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.createSuccess && !this.state.loadCreate && (
                  <LoadingWrapper>
                    <CreateMessageWrap>
                      <CreateMessage>{this.state.createSuccess}</CreateMessage>
                      <CreateMessage>
                        this popup will close automatically.
                      </CreateMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </CreateMessageWrap>
                  </LoadingWrapper>
                )}
              </CreateWrapper>
            </CreateContainer>
          </CreateWrap>
        )}

        {this.state.editActive && (
          <CreateWrap>
            <CreateContainer>
              <CreateWrapper>
                <CreateTitle>
                  {!this.state.loadEdit && !this.state.editSuccess ? (
                    <h5>Edit a Product Category? </h5>
                  ) : (
                    <h5>Successful </h5>
                  )}
                </CreateTitle>

                {!this.state.loadEdit && !this.state.editSuccess && (
                  <div className="creates">
                    {this.state.editError.length !== 0 &&
                      this.state.editError[0].response && (
                        <CreateErrors>
                          {this.state.editError[0].response}
                        </CreateErrors>
                      )}
                    {this.state.editError.length !== 0 &&
                      !this.state.editError[0].response && (
                        <CreateErrors>
                          {this.state.editError[0].message}
                        </CreateErrors>
                      )}
                    <form onSubmit={this.handleSubmit2}>
                      <CreateTextWrap>
                        <CreateText>
                          <label>Category name*</label>
                          <input
                            type="text"
                            name="category"
                            value={this.state.category}
                            onChange={this.handleChange}
                          />
                          {this.state.categoryError && (
                            <CreateTextError>
                              {this.state.categoryError}
                            </CreateTextError>
                          )}
                        </CreateText>

                        <CreateText>
                          <label>status* {this.state.status}</label>
                          <div>
                            <select
                              name="status"
                              value={this.state.status}
                              onChange={this.handleChange}
                            >
                              <option value="">select status</option>
                              <option value={true}>activate</option>
                              <option value={false}>deactivate</option>
                            </select>
                          </div>
                          {this.state.errors.status && (
                            <CreateTextError>
                              {this.state.errors.status}
                            </CreateTextError>
                          )}
                        </CreateText>
                      </CreateTextWrap>

                      <ButtonWrap>
                        <CancelBtn onClick={this.closeEdit}>Cancel</CancelBtn>{" "}
                        <CreatesBtn type="submit">Submit</CreatesBtn>
                      </ButtonWrap>
                    </form>
                  </div>
                )}

                {this.state.loadEdit && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.editSuccess && !this.state.loadEdit && (
                  <LoadingWrapper>
                    <CreateMessageWrap>
                      <CreateMessage>{this.state.editSuccess}</CreateMessage>
                      <CreateMessage>
                        this popup will close automatically.
                      </CreateMessage>
                      <CloseButton onClick={this.closeEdit}>Close</CloseButton>
                    </CreateMessageWrap>
                  </LoadingWrapper>
                )}
              </CreateWrapper>
            </CreateContainer>
          </CreateWrap>
        )}

        {this.state.deleteActive && (
          <DeleteContainer>
            <DeleteWrapper>
              <DeleteTitle>
                <h5>Delete this Category? </h5>
              </DeleteTitle>

              {!this.state.loadDelete && !this.state.deleteSuccess && (
                <div>
                  {this.state.deleteError.length !== 0 &&
                    this.state.deleteError[0].response && (
                      <DeleteErrors>
                        {this.state.deleteError[0].response}
                      </DeleteErrors>
                    )}
                  {this.state.deleteError.length !== 0 &&
                    !this.state.deleteError[0].response && (
                      <DeleteErrors>
                        {this.state.deleteError[0].message}
                      </DeleteErrors>
                    )}

                  <DeletesWrap>
                    <img src={deleteSvg} alt="delete" />
                  </DeletesWrap>
                  <DeleteText>
                    You’ll lose all this category details. We can’t recover them
                    when deleted.
                  </DeleteText>
                  <DeleteText>
                    Are you sure you want to permanently delete this category?
                  </DeleteText>

                  <ButtonWrap>
                    <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                    <YesBtn onClick={() => this.deleteItem(this.state.ids)}>
                      Yes
                    </YesBtn>
                  </ButtonWrap>
                </div>
              )}

              {this.state.loadDelete && (
                <LoadingWrapper>
                  <Loader
                    type="TailSpin"
                    color="black"
                    height={50}
                    width={50}
                  />
                </LoadingWrapper>
              )}

              {this.state.deleteSuccess && !this.state.loadDelete && (
                <LoadingWrapper>
                  <DeleteMessageWrap>
                    <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                    <DeleteMessage>
                      this popup will close automatically.
                    </DeleteMessage>
                    <CloseButton onClick={this.closeDelete}>Close</CloseButton>
                  </DeleteMessageWrap>
                </LoadingWrapper>
              )}
            </DeleteWrapper>
          </DeleteContainer>
        )}
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    categories: state.adminCategories,
  };
};
export default connect(mapStateToProps, { fetchAdminCategories })(Categories);
