import React from "react";
import {
  LatestWrapper,
  LatestHeadWrap,
  LatestHeadContainer,
  LId,
  LDate,
  LStatus,
  LItems,
  LCategory,
  LCost,
  LAction,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  StatusContent,
  YellowC,
  NotActiveC,
  TItems,
  TCategory,
  TCost,
  TAction,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  LoadingWrapper,
  CloseButton,
  DeleteMessage,
  DeleteMessageWrap,
  DeleteErrors,
  TStock,
} from "./styles";
import { AiOutlineClose } from "react-icons/ai";
import DropDown from "../../common/DropDown";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import axios from "axios";
import Loader from "react-loader-spinner";
import { BASE_API_ } from "../../../utils/base-url.config";
import Cookies from "js-cookie";
import numeral from "numeral";
import { Tooltip } from "@chakra-ui/react";

class ListContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      deleteMessage: "",
      deleteError: [],
      loadDelete: false,
      ids: "",
    };
  }

  componentDidMount() {
    this.setState({ productlists: this.props.productlist });
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchActive !== prevProps.searchActive) {
      this.setState({ productlists: this.props.productSlist });
    }
  }

  closeDelete = () => {
    this.setState({ deleteActive: false });
  };

  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };

  deleteActionAccepted = () => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/products/v1/delete/${this.state.ids}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ deleteMessage: success.data, loadDelete: false });
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ loadDelete: false });
        this.setState({ deleteError: arrayError });
      });
  };

  ShortTotals = (total) => (total ? `${numeral(total).format("0.00a")}` : "0");

  renderProducts = () => {
    return this.props.productlist.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>{Id}</TId>
            <TItems>{pName}</TItems>
            <TStatus>
              <StatusContent>
                {data.active ? (
                  <>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </>
                ) : (
                  <>
                    <NotActiveC />
                    <p>Not Active</p>
                  </>
                )}
              </StatusContent>
            </TStatus>
            <TCategory>{data.category.name}</TCategory>
            <TCost>
              <Tooltip
                hasArrow
                label={`${numeral(data.price).format("0,00") || 0} KES`}
                bg="gray.600"
                color="white"
                fontSize="sm"
              >
                {data.price > 100000
                  ? this.ShortTotals(data.price) || 0
                  : numeral(data.price).format("0,00")}
              </Tooltip>
            </TCost>
            <TStock>{data.totalStock}</TStock>
            <TCost
              className={
                data.totalStock > data.reservedStock ? null : "OutStock"
              }
            >
              {data.totalStock > data.reservedStock
                ? "In Stock"
                : "Out of Stock"}
            </TCost>
            <TDate>
              {data.dayCreated}/{data.monthCreated}/{data.yearCreated}
            </TDate>
            <TAction>
              <DropDown activateDelete={this.activateDelete} ids={data._id} />
            </TAction>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  //rendering searchProducts
  renderSearchProducts = () => {
    return this.props.productSlist.map((data, index) => {
      let Id = data._id;
      let pName = data.name;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }
      if (Object.keys(pName).length > 20) {
        pName = data.name.slice(0, 20) + "...";
      }
      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>{Id}</TId>
            <TItems>{pName}</TItems>
            <TStatus>
              <StatusContent>
                {data.active ? (
                  <>
                    {" "}
                    <YellowC />
                    <p>Active</p>
                  </>
                ) : (
                  <>
                    <NotActiveC />
                    <p>Not Active</p>
                  </>
                )}
              </StatusContent>
            </TStatus>
            <TCategory>{data.category.name}</TCategory>
            <TCost>
              {" "}
              <Tooltip
                hasArrow
                label={`${numeral(data.price).format("0,00") || 0} KES`}
                bg="gray.600"
                color="white"
                fontSize="sm"
              >
                {data.price > 100000
                  ? this.ShortTotals(data.price) || 0
                  : numeral(data.price).format("0,00")}
              </Tooltip>
            </TCost>
            <TStock>{data.totalStock}</TStock>
            <TCost
              className={
                data.totalStock > data.reservedStock ? null : "OutStock"
              }
            >
              {data.totalStock > data.reservedStock
                ? "In Stock"
                : "Out of Stock"}
            </TCost>
            <TDate>
              {data.dayCreated}/{data.monthCreated}/{data.yearCreated}
            </TDate>
            <TAction>
              <DropDown activateDelete={this.activateDelete} ids={data._id} />
            </TAction>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  render() {
    return (
      <>
        <LatestWrapper>
          <LatestHeadWrap>
            <LatestHeadContainer>
              <LId>ID</LId>
              <LItems>Product name</LItems>
              <LStatus>Status</LStatus>
              <LCategory>Category</LCategory>
              <LCost>Price</LCost>
              <LCost>Stock</LCost>
              <LCost>Stock Status</LCost>
              <LDate>Added</LDate>
              <LAction>Action</LAction>
            </LatestHeadContainer>
          </LatestHeadWrap>

          {this.props.searchActive
            ? this.renderSearchProducts()
            : this.renderProducts()}
        </LatestWrapper>

        {this.props.searchActive ? (
          <Pagination2
            totalProducts={this.props.productSTotal}
            productsPerPage={this.props.perPage}
            current={this.props.currentS}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
            changesearchPage={this.props.changesearchPage}
          />
        ) : (
          <Pagination
            totalProducts={this.props.totalProducts}
            productsPerPage={this.props.perPage}
            current={this.props.current}
            paramslinks={this.props.pparams}
            historyProps={this.props.historyProps}
          />
        )}

        {this.state.deleteActive && (
          <DeleteContainer>
            {!this.state.loadDelete && !this.state.deleteMessage && (
              <DeleteWrapper>
                {this.state.deleteError.length !== 0 &&
                  this.state.deleteError[0].response && (
                    <DeleteErrors>
                      {this.state.deleteError[0].response}
                    </DeleteErrors>
                  )}

                {this.state.deleteError.length !== 0 &&
                  !this.state.deleteError[0].response && (
                    <DeleteErrors>
                      {this.state.deleteError[0].message}
                    </DeleteErrors>
                  )}

                <DeleteTitle>
                  <h5>Delete this product? </h5>
                  <CloseIcon onClick={this.closeDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                <DeleteText>
                  You’ll lose all this product details. We can’t recover them
                  when deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to <h5>permanently</h5> delete this
                  product?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn onClick={this.deleteActionAccepted}>Yes</YesBtn>
                </ButtonWrap>
              </DeleteWrapper>
            )}

            {this.state.loadDelete && (
              <DeleteWrapper>
                <LoadingWrapper>
                  <Loader
                    type="TailSpin"
                    color="black"
                    height={50}
                    width={50}
                  />
                </LoadingWrapper>
              </DeleteWrapper>
            )}

            {this.state.deleteMessage && !this.state.loadDelete && (
              <DeleteWrapper>
                <LoadingWrapper>
                  <DeleteMessageWrap>
                    <DeleteMessage>{this.state.deleteMessage}</DeleteMessage>
                    <DeleteMessage>
                      this popup will be closed automatically{" "}
                    </DeleteMessage>
                    <CloseButton onClick={this.closeDelete}>Close</CloseButton>
                  </DeleteMessageWrap>
                </LoadingWrapper>
              </DeleteWrapper>
            )}
          </DeleteContainer>
        )}
      </>
    );
  }
}

export default ListContent;
