import React from "react";
import { Wrapper, Container } from "./styles";
import AllShipping from "../ShippingComponents/AllShipping";
import AllMobileCouriers from "../../Mobile/components/ShippingControllers/Couriers";
import ViewDetails from "../ShippingComponents/ViewShipping";
import MobileViewDetails from '../../Mobile/components/ShippingControllers/Locations';
import AgentDetails from "../ShippingComponents/Agents";
import MobileAgentDetails from '../../Mobile/components/ShippingControllers/Agents';
import { connect } from "react-redux";
import { fetchCouriers } from "../../store/actions";
import Cookies from "js-cookie";

class ShippingSection extends React.Component {
  state = { page: undefined, perPage: 8, totalProducts: 0, avproducts: [] };

  componentDidMount() {
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getDetails = async () => {
        await this.props.fetchCouriers(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );

        sessionStorage.setItem(
          "couriers",
          JSON.stringify(this.props.couriers.couriers),
          {
            expires: 1,
          }
        );
      };

      getDetails();
    } else {
      const getDetails = async () => {
        await this.props.fetchCouriers(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
        sessionStorage.setItem(
          "couriers",
          JSON.stringify(this.props.couriers.couriers),
          {
            expires: 1,
          }
        );
      };

      getDetails();
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  changeComponent = () => {
    if (this.props.pparams.url === "/dashboard/shipping") {
      if (this.props.mobileActive) {
        return (
          <AllMobileCouriers
            pparams={this.props.pparams}
            couriers={this.props.couriers.couriers}
            totalCouriers={this.props.couriers.totalCouriers}
            allCouriers={this.props.couriers.allCouriers}
            current={this.props.couriers.current}
            perPage={this.state.perPage}
            historyProps={this.props.historyProps}
          />
        );
      } else {
        return (
          <AllShipping
            pparams={this.props.pparams}
            couriers={this.props.couriers.couriers}
            totalCouriers={this.props.couriers.totalCouriers}
            allCouriers={this.props.couriers.allCouriers}
            current={this.props.couriers.current}
            perPage={this.state.perPage}
            historyProps={this.props.historyProps}
          />
        );
      }
    } else if (this.props.pparams.path === "/dashboard/:title/:specific/:id") {
      if (this.props.pparams.params.specific === "view") {
        if (this.props.mobileActive) {
          return (
            <MobileViewDetails
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        } else {
          return (
            <ViewDetails
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        }
      } else if (this.props.pparams.params.specific === "agent") {
        if (this.props.mobileActive) {
          return (
            <MobileAgentDetails
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        } else {
          return (
            <AgentDetails
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
              pparams={this.props.pparams}
            />
          );
        }
      }
    }
  };
  render() {
    return (
      <Wrapper>
        <Container>{this.changeComponent()}</Container>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    couriers: state.couriers,
  };
};
export default connect(mapStateToProps, { fetchCouriers })(ShippingSection);
