import React from "react";
import axios from "axios";
import {
  Wrapper,
  Container,
  TestimonyWrap,
  TestimonyContainer,
  Title,
  ButtonControls,
  EditButton,
  DeleteButton,
  FormContent,
  FormContent1,
  EditSection,
  DeleteSection,
  EditWrapSection,
  EditContainer,
  TitleWrap,
  TitleText,
  FormContentWrap,
  FormContent2,
  FormContent3,
  EditButtonWrap,
  SaveEditButton,
  CancelEditButton,
  TestimonyButtonWrap,
  AddButton,
  DeleteWrapper,
  DeleteTitle,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteErrors,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  PageTransition,
  PageTWrap,
  DeletesWrap,
} from "./styles";
import Loader from "react-loader-spinner";
import { AiOutlinePlus } from "react-icons/ai";
import { BiPencil } from "react-icons/bi";
import deleteSvg from "../../../assets/icons/delete.svg";
import { RiDeleteBinLine } from "react-icons/ri";
import { connect } from "react-redux";
import { fetchTestimonial } from "../../../store/actions";
import Pagination from "./pagination";
import { BASE_API_ } from "../../../utils/base-url.config";
import Cookies from "js-cookie";

class Testimonials extends React.Component {
  state = {
    editContent: false,
    editLoading: false,
    editSuccess: "",
    editError: [],

    createContent: false,
    createLoading: false,
    createSuccess: "",
    createError: [],

    testimonyD: "",
    customerD: "",
    loadDelete: false,
    deleteSuccess: "",
    deleteError: [],
    activateDelete: false,
    perPage: 3,
    current: null,
  };

  componentDidMount() {
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getTestimonial = async () => {
        await this.props.fetchTestimonial(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      getTestimonial();
    } else {
      const getTestimonial = async () => {
        await this.props.fetchTestimonial(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      getTestimonial();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      const getToken = Cookies.get("_tk");
      let page;
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");
        const getTestimonial = async () => {
          await this.props.fetchTestimonial(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        getTestimonial();
      } else {
        const getTestimonial = async () => {
          await this.props.fetchTestimonial(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        getTestimonial();
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  //changes in the form
  handleChange = (e) => {
    if (e.target.name === "testimonyD") {
      this.setState({
        [e.target.name]: e.target.value,
        createError: [],
        editError: [],
      });
    } else if (e.target.name === "customerD") {
      this.setState({
        customerD: e.target.value,
        createError: [],
        editError: [],
      });
    }
  };

  activatecreate = () => {
    this.setState({ createContent: true });
  };

  deactivatecreate = () => {
    this.setState({
      createContent: false,
      createSuccess: "",
      createError: [],
      testimonyD: "",
      customerD: "",
    });
  };

  //submit the creation of blog
  handleSubmit = (e) => {
    e.preventDefault();
    if (!this.state.testimonyD || !this.state.customerD) {
      let errors = {};
      if (!this.state.testimonyD) {
        errors.testimonyD = "testimony required";
      }
      if (!this.state.customerD) {
        errors.customerD = "customer required";
      }

      this.setState({
        errors: errors,
      });
    } else {
      this.setState({ createLoading: true });
      this.postSubmission();
    }
  };

  //request to submit form

  postSubmission = () => {
    const formData = new FormData();

    formData.append("testimony", this.state.testimonyD);
    formData.append("customer", this.state.customerD);
    const getToken = Cookies.get("_tk");
    axios
      .post(`${BASE_API_}/testimonial/v1/testimonial/create`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ createSuccess: success.data, createLoading: false });
        window.location.reload();
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ createError: errorArray, createLoading: false });
      });
  };

  activateEdit = (data) => {
    this.setState({
      editContent: true,
      testimonyD: data.testimony,
      customerD: data.customer,
      testimonyId: data._id,
    });
  };

  deactivateEdit = () => {
    this.setState({
      editContent: false,
      testimonyD: "",
      customerD: "",
      editSuccess: "",
      editError: [],
      testimonyId: "",
    });
  };

  //submit the creation of blog
  handleSubmit2 = (e) => {
    e.preventDefault();
    if (!this.state.testimonyD || !this.state.customerD) {
      let errors = {};
      if (!this.state.testimonyD) {
        errors.testimonyD = "testimony required";
      }
      if (!this.state.customerD) {
        errors.customerD = "customer required";
      }

      this.setState({
        errors: errors,
      });
    } else {
      this.setState({ editLoading: true });
      this.editSubmission();
    }
  };

  //request to submit form

  editSubmission = () => {
    const formData = new FormData();

    formData.append("testimony", this.state.testimonyD);
    formData.append("customer", this.state.customerD);
    const getToken = Cookies.get("_tk");
    axios
      .put(
        `${BASE_API_}/testimonial/v1/edit/testimonial/${this.state.testimonyId}`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: "Bearer " + getToken,
          },
        }
      )
      .then((success) => {
        this.setState({ editSuccess: success.data, editLoading: false });
        window.location.reload();
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ editError: errorArray, editLoading: false });
      });
  };

  activatedelete = (id) => {
    this.setState({ activateDelete: true, ids: id });
  };

  closeDelete = () => {
    this.setState({ activateDelete: false, ids: "" });
  };

  //remove Testimony
  removeTestimony = (id) => {
     const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/testimonial/v1/testimonial/remove/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({ deleteSuccess: success.data, loadDelete: false });
        window.location.reload();
      })
      .catch((error) => {
        let errorArray = [];
        errorArray.push(error);
        this.setState({ deleteError: errorArray, loadDelete: false });
      });
  };

  render() {
    return (
      <div>
        <Wrapper>
          <Container>
            {" "}
            <PageTWrap>
              <TestimonyButtonWrap>
                <AddButton onClick={this.activatecreate}>
                  <AiOutlinePlus /> Add Testimonial
                </AddButton>
              </TestimonyButtonWrap>
              {this.props.testimonies.testimonies.map((data, index) => {
                return (
                  <TestimonyWrap key={data._id}>
                    <TestimonyContainer>
                      <Title>Testimonial #</Title>
                      <FormContent1>
                        <textarea type="text" readOnly value={data.testimony} />
                      </FormContent1>
                      <FormContent>
                        <input type="text" readOnly value={data.customer} />
                      </FormContent>
                    </TestimonyContainer>

                    <ButtonControls>
                      <EditButton onClick={() => this.activateEdit(data)}>
                        {" "}
                        <BiPencil />
                      </EditButton>
                      <DeleteButton
                        onClick={() => this.activatedelete(data._id)}
                      >
                        <RiDeleteBinLine />
                      </DeleteButton>
                    </ButtonControls>
                  </TestimonyWrap>
                );
              })}
            </PageTWrap>
            <PageTransition>
              <Pagination
                totalProducts={this.props.testimonies.totalTestimony}
                productsPerPage={this.state.perPage}
                current={this.props.testimonies.current}
                paramslinks={this.props.sparams}
                historyProps={this.props.historyProps}
              />
            </PageTransition>
          </Container>
        </Wrapper>

        {this.state.createContent && (
          <EditSection>
            <EditWrapSection>
              <EditContainer>
                <TitleWrap>
                  {!this.state.createLoading && !this.state.createSuccess ? (
                    <TitleText>Create Testimonials</TitleText>
                  ) : (
                    <TitleText>Successful</TitleText>
                  )}
                </TitleWrap>
                <hr />

                {!this.state.createLoading && !this.state.createSuccess && (
                  <>
                    {this.state.createError.length !== 0 &&
                      this.state.createError[0].response && (
                        <DeleteErrors>
                          {this.state.createError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.createError.length !== 0 &&
                      !this.state.createError[0].response && (
                        <DeleteErrors>
                          {this.state.createError[0].message}
                        </DeleteErrors>
                      )}
                    <form onSubmit={this.handleSubmit}>
                      <FormContentWrap>
                        <FormContent2>
                          <label>Testimonial*</label>
                          <textarea
                            type="text"
                            name="testimonyD"
                            value={this.state.testimonyD}
                            onChange={this.handleChange}
                          />
                        </FormContent2>
                        <FormContent3>
                          <label>Customer name*</label>
                          <input
                            type="text"
                            name="customerD"
                            value={this.state.customerD}
                            onChange={this.handleChange}
                          />
                        </FormContent3>
                      </FormContentWrap>

                      <EditButtonWrap>
                        <CancelEditButton onClick={this.deactivatecreate}>
                          Cancel
                        </CancelEditButton>
                        <SaveEditButton type="submit">Save</SaveEditButton>
                      </EditButtonWrap>
                    </form>
                  </>
                )}

                {this.state.createLoading && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.createSuccess && !this.state.createLoading && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.createSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.deactivatecreate}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </EditContainer>
            </EditWrapSection>
          </EditSection>
        )}

        {this.state.editContent && (
          <EditSection>
            <EditWrapSection>
              <EditContainer>
                <TitleWrap>
                  {!this.state.editLoading && !this.state.editSuccess ? (
                    <TitleText> Edit Testimonials</TitleText>
                  ) : (
                    <TitleText> Successful</TitleText>
                  )}
                </TitleWrap>
                <hr />

                {!this.state.editLoading && !this.state.editSuccess && (
                  <>
                    {this.state.editError.length !== 0 &&
                      this.state.editError[0].response && (
                        <DeleteErrors>
                          {this.state.editError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.editError.length !== 0 &&
                      !this.state.editError[0].response && (
                        <DeleteErrors>
                          {this.state.editError[0].message}
                        </DeleteErrors>
                      )}
                    <form onSubmit={this.handleSubmit2}>
                      <FormContentWrap>
                        <FormContent2>
                          <label>Testimonial*</label>
                          <textarea
                            type="text"
                            name="testimonyD"
                            value={this.state.testimonyD}
                            onChange={this.handleChange}
                          />
                        </FormContent2>
                        <FormContent3>
                          <label>Customer name*</label>
                          <input
                            type="text"
                            name="customerD"
                            value={this.state.customerD}
                            onChange={this.handleChange}
                          />
                        </FormContent3>
                      </FormContentWrap>

                      <EditButtonWrap>
                        <CancelEditButton onClick={this.deactivateEdit}>
                          Cancel
                        </CancelEditButton>
                        <SaveEditButton type="submit">Save</SaveEditButton>
                      </EditButtonWrap>
                    </form>
                  </>
                )}

                {this.state.editLoading && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.editSuccess && !this.state.editLoading && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.editSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically{" "}
                      </DeleteMessage>
                      <CloseButton onClick={this.deactivateEdit}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </EditContainer>
            </EditWrapSection>
          </EditSection>
        )}

        {this.state.activateDelete && (
          <DeleteSection>
            <DeleteWrapper>
              <DeleteTitle>
                {!this.state.loadDelete && !this.state.deleteSuccess ? (
                  <h5>Delete this Testimonial ? </h5>
                ) : (
                  <h5>Successful </h5>
                )}
              </DeleteTitle>

              {!this.state.loadDelete && !this.state.deleteSuccess && (
                <div>
                  {this.state.deleteError.length !== 0 &&
                    this.state.deleteError[0].response && (
                      <DeleteErrors>
                        {this.state.deleteError[0].response}
                      </DeleteErrors>
                    )}
                  {this.state.deleteError.length !== 0 &&
                    !this.state.deleteError[0].response && (
                      <DeleteErrors>
                        {this.state.deleteError[0].message}
                      </DeleteErrors>
                    )}

                  <DeletesWrap>
                    <img src={deleteSvg} alt="delete" />
                  </DeletesWrap>
                  <DeleteText>
                    You’ll lose all this Testimonial. We can’t recover them when
                    deleted.
                  </DeleteText>
                  <DeleteText>
                    Are you sure you want to permanently delete this Testimony?
                  </DeleteText>

                  <ButtonWrap>
                    <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                    <YesBtn
                      onClick={() => this.removeTestimony(this.state.ids)}
                    >
                      Yes
                    </YesBtn>
                  </ButtonWrap>
                </div>
              )}

              {this.state.loadDelete && (
                <LoadingWrapper>
                  <Loader
                    type="TailSpin"
                    color="black"
                    height={50}
                    width={50}
                  />
                </LoadingWrapper>
              )}

              {this.state.deleteSuccess && !this.state.loadDelete && (
                <LoadingWrapper>
                  <DeleteMessageWrap>
                    <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                    <DeleteMessage>
                      this popup will be closed automatically
                    </DeleteMessage>
                    <CloseButton onClick={this.closeDelete}>Close</CloseButton>
                  </DeleteMessageWrap>
                </LoadingWrapper>
              )}
            </DeleteWrapper>
          </DeleteSection>
        )}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    testimonies: state.testimonial,
  };
};
export default connect(mapStateToProps, {
  fetchTestimonial,
})(Testimonials);
