import React from "react";
import {
  HeadWrap,
  TitleWrap,
  EditBtn,
  TitleHead,
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,
  LatestHeadWrap,
  LatestHeadContainer,
  LId,
  LDate,
  LStatus,
  LItems,
  LAction,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TStatus,
  StatusContent,
  TItems,
  TAction,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  DeleteErrors,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  CreateWrap,
  CreateContainer,
  CreateWrapper,
  CreateTitle,
  CreateErrors,
  CreateText,
  CreateMessageWrap,
  CreateMessage,
  CreateBtn,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";
import { AiOutlineClose } from "react-icons/ai";
import { BsSearch } from "react-icons/bs";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import DropDown from "../DropDown";
import axios from "axios";
import Loader from "react-loader-spinner";
import listImage from "../../../assets/icons/NoList.svg";
import { BASE_API_ } from "../../../utils/base-url.config";
import Cookies from "js-cookie";

class AllShipping extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      deleteActive: false,
      activedrop: false,
      cancelOrder: false,
      cancelSuccess: "",
      cancelError: [],
      loadDelete: false,
      loadCancel: false,
      deleteSuccess: "",
      deleteError: [],

      createActive: false,
      courier: "",
      createError: [],
      loadCreate: false,
      createSuccess: "",

      editActive: false,
      editError: [],
      loadEdit: false,
      courierId: "",
      editSuccess: "",

      searchActive: false,
      itemSlist: [],
      currentS: 1,
      itemSTotal: null,
      allsetItems: [],
      pageLoading: false,
    };
  }

  componentDidMount() {
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
      }
    };
  }

  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetItems.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      itemSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.allCouriers;
    const resultData = allProducts.filter((shipping) => {
      let courier = shipping.courier.toLowerCase();
      return courier.includes(query.toLowerCase());
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPage = 1;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      itemSlist: currentItems,
      currentS: 1,
      itemSTotal: totalOrders,
      allsetItems: showOrders,
      results: showOrders,
      loading: true,
      searchActive: true,
    });
  };

  activateCreate = () => {
    this.setState({
      createActive: true,
      deleteActive: false,
      cancelOrder: false,
    });
  };
  closeDelete = () => {
    this.setState({
      deleteActive: false,
      cancelOrder: false,
      createActive: false,
      editActive: false,
    });
  };
  closeCancel = () => {
    this.setState({ cancelActive: false, cancelOrder: false });
  };
  activateDelete = (identity) => {
    this.setState({ deleteActive: true, ids: identity });
  };
  activateCancelOrder = (id) => {
    this.setState({ cancelOrder: true, deleteActive: false, ids: id });
  };

  activateEdit = (id, data) => {
    this.setState({ courierId: id, courier: data.courier, editActive: true });
  };

  handleChange = (e) => {
    this.setState({ courier: e.target.value });
  };
  //create function
  handleSubmit = (e) => {
    e.preventDefault();
    if (this.state.courier) {
      let values = {
        courier: this.state.courier,
      };
      const getToken = Cookies.get("_tk");
      axios
        .post(`${BASE_API_}/shipping/v1/courier/create`, values, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          this.setState({
            createSuccess: "Courier Created successfully",
            loadCreate: false,
          });
          window.location.reload();
        })
        .catch((error) => {
          let arrayError = [];
          arrayError.push(error);
          this.setState({ createError: arrayError, loadCreate: false });
        });
    }
  };

  //edit function
  handleEdit = (e) => {
    e.preventDefault();
     if (this.state.courier) {
       let values = {
         courierId: this.state.courierId,
         courier: this.state.courier,
       };
       const getToken = Cookies.get("_tk");
       axios
         .put(`${BASE_API_}/shipping/v1/courier/update`, values, {
           headers: {
             Authorization: "Bearer " + getToken,
           },
         })
         .then((success) => {
           this.setState({
             createSuccess: success.data,
             loadEdit: false,
           });
           window.location.reload();
         })
         .catch((error) => {
           let arrayError = [];
           arrayError.push(error);
           this.setState({ editError: arrayError, loadEdit: false });
         });
     }
  }

  deleteOrder = (id) => {
    this.setState({ loadDelete: true });
    const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/shipping/v1/removeCouriers/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        this.setState({
          deleteSuccess: "Courier Deleted successfully",
          loadDelete: false,
        });
        window.location.reload();
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        this.setState({ deleteError: arrayError, loadDelete: false });
      });
  };

  renderOrders = () => {
    return this.props.couriers.map((data, index) => {
      let courier;
      let namesCombined = data.courier;

      if (Object.keys(namesCombined).length > 15) {
        courier = namesCombined.slice(0, 15) + "...";
      } else {
        courier = namesCombined;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>{Id}</TId>
            <TStatus>
              <StatusContent>
                <p>{courier}</p>
              </StatusContent>
            </TStatus>
            <TItems>{data.numberOfLocations}</TItems>
            <TDate>
              {" "}
              {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
            </TDate>

            <TAction>
              <DropDown
                activateDelete={this.activateDelete}
                activateCancel={this.activateCancelOrder}
                activateEdit={this.activateEdit}
                link={"/dashboard/shipping/view"}
                ids={data._id}
                data={data}
              />
            </TAction>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  renderSearchedShipping = () => {
    return this.state.itemSlist.map((data, index) => {
      let courier;
      let namesCombined = data.courier;

      if (Object.keys(namesCombined).length > 15) {
        courier = namesCombined.slice(0, 15) + "...";
      } else {
        courier = namesCombined;
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>{Id}</TId>
            <TStatus>
              <StatusContent>
                <p>{courier}</p>
              </StatusContent>
            </TStatus>
            <TItems>{data.numberOfLocations}</TItems>
            <TDate>
              {" "}
              {data.dateCreated}.{data.monthCreated}.{data.yearCreated}
            </TDate>

            <TAction>
              <DropDown
                activateDelete={this.activateDelete}
                activateCancel={this.activateCancelOrder}
                activateEdit={this.activateEdit}
                link={"/dashboard/shipping/view"}
                ids={data._id}
                data={data}
              />
            </TAction>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };
  render() {
    return (
      <>
        <HeadWrap>
          <TitleWrap>
            <TitleHead>Shipping Options</TitleHead>

            <EditBtn onClick={this.activateCreate}>Create</EditBtn>
          </TitleWrap>
        </HeadWrap>

        <OrderContainer>
          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.couriers.length > 0 ? (
                <>
                  <SelectionWrapper>
                    <SearchWrapper>
                      <form onSubmit={this.onFormSubmit}>
                        <input
                          type="text"
                          name="query"
                          value={this.state.query}
                          onChange={this.handleInputChange}
                          placeholder="Search courier "
                        />
                        <button>
                          {" "}
                          <BsSearch />
                        </button>
                      </form>
                    </SearchWrapper>
                  </SelectionWrapper>

                  <LatestWrapper>
                    <LatestHeadWrap>
                      <LatestHeadContainer>
                        <LId>ID</LId>
                        <LStatus>Courier Provider</LStatus>
                        <LItems>No. of locations</LItems>
                        <LDate>Added</LDate>
                        <LAction>Action</LAction>
                      </LatestHeadContainer>
                    </LatestHeadWrap>

                    {this.state.searchActive ? (
                      <>
                        {this.state.itemSlist && (
                          <div>{this.renderSearchedShipping()}</div>
                        )}
                      </>
                    ) : (
                      <>
                        {this.props.couriers && (
                          <div>{this.renderOrders()}</div>
                        )}
                      </>
                    )}
                  </LatestWrapper>

                  {this.state.searchActive ? (
                    <Pagination2
                      totalItems={this.state.itemSTotal}
                      itemPerPage={this.props.perPage}
                      current={this.state.currentS}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      changesearchPage={this.changesearchPage}
                    />
                  ) : (
                    <Pagination
                      totalItems={this.props.totalCouriers}
                      itemPerPage={this.props.perPage}
                      current={this.props.current}
                      paramslinks={this.props.pparams}
                      historyProps={this.props.historyProps}
                      link={"/dashboard/shipping"}
                    />
                  )}
                </>
              ) : (
                <PContainer bg={"white"} className="Nolist">
                  <NoListWrapper>
                    <NoListContainer>
                      <img src={listImage} alt="NoList" />
                      <h1>List is Empty</h1>
                      <h5>Please create items on the list.</h5>
                    </NoListContainer>
                  </NoListWrapper>
                </PContainer>
              )}
            </>
          )}
          {/** create  */}
          {this.state.createActive && (
            <CreateWrap>
              <CreateContainer>
                <CreateWrapper>
                  <CreateTitle>
                    <h5>Create New Courier Provider? </h5>
                    <CloseIcon onClick={this.closeDelete}>
                      <AiOutlineClose />
                    </CloseIcon>{" "}
                  </CreateTitle>

                  {!this.state.loadCreate && !this.state.createSuccess && (
                    <div>
                      {this.state.createError.length !== 0 &&
                        this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].response}
                          </CreateErrors>
                        )}
                      {this.state.createError.length !== 0 &&
                        !this.state.createError[0].response && (
                          <CreateErrors>
                            {this.state.createError[0].message}
                          </CreateErrors>
                        )}
                      <form onSubmit={this.handleSubmit}>
                        <CreateText>
                          <label>Provider name*</label>
                          <input
                            type="text"
                            name="courier"
                            value={this.state.courier}
                            onChange={this.handleChange}
                          />
                          <p>please type in courier provider name</p>
                        </CreateText>

                        <ButtonWrap>
                          <CancelBtn onClick={this.closeDelete}>
                            Cancel
                          </CancelBtn>{" "}
                          <CreateBtn type="submit">Yes</CreateBtn>
                        </ButtonWrap>
                      </form>
                    </div>
                  )}

                  {this.state.loadCreate && (
                    <LoadingWrapper>
                      <Loader
                        type="TailSpin"
                        color="black"
                        height={50}
                        width={50}
                      />
                    </LoadingWrapper>
                  )}

                  {this.state.createSuccess && !this.state.loadCreate && (
                    <LoadingWrapper>
                      <CreateMessageWrap>
                        <CreateMessage>
                          {this.state.createSuccess}
                        </CreateMessage>
                        <CreateMessage>
                          this popup will be closed automatically
                        </CreateMessage>
                        <CloseButton onClick={this.closeDelete}>
                          Close
                        </CloseButton>
                      </CreateMessageWrap>
                    </LoadingWrapper>
                  )}
                </CreateWrapper>
              </CreateContainer>
            </CreateWrap>
          )}

          {/** edit  */}
          {this.state.editActive && (
            <CreateWrap>
              <CreateContainer>
                <CreateWrapper>
                  <CreateTitle>
                    <h5>Edit Courier Provider? </h5>
                    <CloseIcon onClick={this.closeDelete}>
                      <AiOutlineClose />
                    </CloseIcon>{" "}
                  </CreateTitle>

                  {!this.state.loadEdit && !this.state.editSuccess && (
                    <div>
                      {this.state.editError.length !== 0 &&
                        this.state.editError[0].response && (
                          <CreateErrors>
                            {this.state.editError[0].response}
                          </CreateErrors>
                        )}
                      {this.state.editError.length !== 0 &&
                        !this.state.editError[0].response && (
                          <CreateErrors>
                            {this.state.editError[0].message}
                          </CreateErrors>
                        )}
                      <form onSubmit={this.handleEdit}>
                        <CreateText>
                          <label>Provider name*</label>
                          <input
                            type="text"
                            name="courier"
                            value={this.state.courier}
                            onChange={this.handleChange}
                          />
                          <p>please type in courier provider name</p>
                        </CreateText>

                        <ButtonWrap>
                          <CancelBtn onClick={this.closeDelete}>
                            Cancel
                          </CancelBtn>{" "}
                          <CreateBtn type="submit">Yes</CreateBtn>
                        </ButtonWrap>
                      </form>
                    </div>
                  )}

                  {this.state.loadEdit && (
                    <LoadingWrapper>
                      <Loader
                        type="TailSpin"
                        color="black"
                        height={50}
                        width={50}
                      />
                    </LoadingWrapper>
                  )}

                  {this.state.editSuccess && !this.state.loadEdit && (
                    <LoadingWrapper>
                      <CreateMessageWrap>
                        <CreateMessage>{this.state.editSuccess}</CreateMessage>
                        <CreateMessage>
                          this popup will be closed automatically
                        </CreateMessage>
                        <CloseButton onClick={this.closeDelete}>
                          Close
                        </CloseButton>
                      </CreateMessageWrap>
                    </LoadingWrapper>
                  )}
                </CreateWrapper>
              </CreateContainer>
            </CreateWrap>
          )}

          {/** delete  */}
          {this.state.deleteActive && (
            <DeleteContainer>
              <DeleteWrapper>
                <DeleteTitle>
                  {!this.state.loadDelete && !this.state.deleteSuccess ? (
                    <h5>Delete this Courier? </h5>
                  ) : (
                    <h5>Successful </h5>
                  )}
                  <CloseIcon onClick={this.closeDelete}>
                    <AiOutlineClose />
                  </CloseIcon>{" "}
                </DeleteTitle>

                {!this.state.loadDelete && !this.state.deleteSuccess && (
                  <div>
                    {this.state.deleteError.length !== 0 &&
                      this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].response}
                        </DeleteErrors>
                      )}
                    {this.state.deleteError.length !== 0 &&
                      !this.state.deleteError[0].response && (
                        <DeleteErrors>
                          {this.state.deleteError[0].message}
                        </DeleteErrors>
                      )}
                    <DeleteText>
                      You’ll lose all this product details. We can’t recover
                      them when deleted.
                    </DeleteText>
                    <DeleteText>
                      Are you sure you want to <h5>permanently</h5> delete this
                      order?
                    </DeleteText>

                    <ButtonWrap>
                      <CancelBtn onClick={this.closeDelete}>Cancel</CancelBtn>{" "}
                      <YesBtn onClick={() => this.deleteOrder(this.state.ids)}>
                        Yes
                      </YesBtn>
                    </ButtonWrap>
                  </div>
                )}

                {this.state.loadDelete && (
                  <LoadingWrapper>
                    <Loader
                      type="TailSpin"
                      color="black"
                      height={50}
                      width={50}
                    />
                  </LoadingWrapper>
                )}

                {this.state.deleteSuccess && !this.state.loadDelete && (
                  <LoadingWrapper>
                    <DeleteMessageWrap>
                      <DeleteMessage>{this.state.deleteSuccess}</DeleteMessage>
                      <DeleteMessage>
                        this popup will be closed automatically
                      </DeleteMessage>
                      <CloseButton onClick={this.closeDelete}>
                        Close
                      </CloseButton>
                    </DeleteMessageWrap>
                  </LoadingWrapper>
                )}
              </DeleteWrapper>
            </DeleteContainer>
          )}
        </OrderContainer>
      </>
    );
  }
}

export default AllShipping;
