import React from "react";
import ListContent from "../../ProductlistLayouts/List";
import GridContent from "../../ProductlistLayouts/Grid";
import { BsSearch } from "react-icons/bs";
import { AiOutlinePlus } from "react-icons/ai";
import { FaListUl } from "react-icons/fa";
import Grid from "../../../assets/feather_grid.png";
import MobileListProduct from "../../../Mobile/components/ProductsController/AllProducts";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  CreateBtn,
  PContainer,
  SelectionWrapper,
  SearchWrapper,
  ListingWrapper,
  ListStyleWrap,
  GalleryStyleWrap,
  SelectWrapper,
  StatusSelect,
  PaidSelect,
  DateSelect,
  MobileSearchWrapper,
  MobileSearch,
  CreateBtn2,
  CreateBtnsWrap,
  CategoryBtn,
  CategoryBtn2,
  CategoryBtn3,
  ButtonsWrap,
  MobileBtnWrapper,
  NoListWrapper,
  NoListContainer,
} from "./style";
import listImage from "../../../assets/icons/NoList.svg";
import { BiRefresh } from "react-icons/bi";
import Loader from "react-loader-spinner";

class ProductSection extends React.Component {
  constructor(props) {
    super(props);

    // this.handleChange = handleChange

    this.state = {
      listActive: false,
      gridActive: false,
      productlist: this.props.products,
      productTotal: this.props.totalProducts,

      current: this.props.current,
      searchActive: false,
      productSlist: [],
      currentS: null,
      productSTotal: null,
      query: "",
      priceQuery: "",
      statusQuery: "",
      categoryQuery: "",
      pageLoading: false,
    };
  }

  componentDidMount() {
    

    document.onreadystatechange =  () => {
      if (document.readyState === "loading") {
        this.setState({ pageLoading: true });
       
      } else if (document.readyState === "interactive") {
        this.setState({ pageLoading: true });
       
      } else if (document.readyState === "complete") {
        this.setState({ pageLoading: false });
       
      }
    };
    if (this.props.pparams.url === "/dashboard/productlist") {
      this.setState({ listActive: true, gridActive: false, bgColor: "white" });
    } else if (this.props.pparams.path === "/dashboard/:title/:specific") {
      if (this.props.pparams.params.specific === "grid") {
        this.setState({
          listActive: false,
          gridActive: true,
          bgColor: "#faf9f9",
        });
      }
    }
  }

  componentDidUpdate(prevProps, state) {
    if (this.props.pparams.url !== prevProps.pparams.url) {
      if (this.props.pparams.url === "/dashboard/productlist") {
        this.setState({
          listActive: true,
          gridActive: false,
          bgColor: "white",
        });
      } else if (this.props.pparams.path === "/dashboard/:title/:specific") {
        if (this.props.pparams.params.specific === "grid") {
          this.setState({
            listActive: false,
            gridActive: true,
            bgColor: "#faf9f9",
          });
        }
      }
    }
    if (this.state.searchActive !== state.searchActive) {
    }
  }

  changeInnerComponent = () => {
    if (this.props.pparams.url === "/dashboard/productlist") {
      if (this.props.mobileActive === true) {
        return (
          <MobileListProduct
            productlist={this.props.products}
            searchActive={this.state.searchActive}
            productSlist={this.state.productSlist}
            totalProducts={this.props.totalProducts}
            currentS={this.state.currentS}
            productSTotal={this.state.productSTotal}
            current={this.props.current}
            perPage={this.props.perPage}
            pparams={this.props.pparams}
            historyProps={this.props.historyProps}
            changesearchPage={this.changesearchPage}
          />
        );
      } else {
        return (
          <ListContent
            productlist={this.props.products}
            searchActive={this.state.searchActive}
            productSlist={this.state.productSlist}
            totalProducts={this.props.totalProducts}
            currentS={this.state.currentS}
            productSTotal={this.state.productSTotal}
            current={this.props.current}
            perPage={this.props.perPage}
            pparams={this.props.pparams}
            historyProps={this.props.historyProps}
            changesearchPage={this.changesearchPage}
          />
        );
      }
    } else if (this.props.pparams.path === "/dashboard/:title/:specific") {
      if (this.props.pparams.params.specific === "grid") {
        if (this.props.mobileActive === true) {
          return (
            <MobileListProduct
              productlist={this.props.products}
              searchActive={this.state.searchActive}
              productSlist={this.state.productSlist}
              totalProducts={this.props.totalProducts}
              currentS={this.state.currentS}
              productSTotal={this.state.productSTotal}
              current={this.props.current}
              perPage={this.props.perPage}
              pparams={this.props.pparams}
              historyProps={this.props.historyProps}
              changesearchPage={this.changesearchPage}
            />
          );
        } else {
          return (
            <GridContent
              productlist={this.props.products}
              totalProducts={this.props.totalProducts}
              searchActive={this.state.searchActive}
              productSlist={this.state.productSlist}
              productSTotal={this.state.productSTotal}
              currentS={this.state.currentS}
              current={this.props.current}
              perPage={this.props.perPage}
              pparams={this.props.pparams}
              historyProps={this.props.historyProps}
              changesearchPage={this.changesearchPage}
            />
          );
        }
      }
    }
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.searchAllProducts;
    const resultData = allProducts.filter((products) => {
      let productName = products.name.toLowerCase();
      return productName.includes(query.toLowerCase());
    });
    const totalProducts = resultData.length;

    let showProducts = resultData;

    sessionStorage.setItem("products", JSON.stringify(showProducts));
    let currentPage = this.state.currentS;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showProducts.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      productSlist: currentItems,
      currentS: 1,
      productSTotal: totalProducts,
      allsetProducts: showProducts,
      results: showProducts,
      loading: true,
      searchActive: true,
    });
  };

  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.props.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetProducts.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      productSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  //changing selection for form
  handleSelectorWrap = (e) => {
    if (this.state.statusQuery || this.state.categoryQuery) {
      if (e.target.name === "categoryQuery") {
        let allProducts;
        if (!this.state.statusQuery) {
          allProducts = this.props.searchAllProducts;
        } else {
          allProducts = this.state.productSlist;
        }
        const resultData = allProducts.filter((products) => {
          let productName = products.category._id;
          return productName.includes(e.target.value);
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem("products", JSON.stringify(showProducts));

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          categoryQuery: e.target.value,
          productSlist: currentItems,
          currentS: 1,
          productSTotal: totalProducts,
          allsetProducts: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      } else if (e.target.name === "statusQuery") {
        let allProducts;
        if (!this.state.categoryQuery) {
          allProducts = this.props.searchAllProducts;
        } else {
          allProducts = this.state.productSlist;
        }

        const resultData = allProducts.filter((products) => {
          if (products.active.toString() === e.target.value) {
            return products;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem("products", JSON.stringify(showProducts));

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          statusQuery: e.target.value,
          productSlist: currentItems,
          currentS: 1,
          productSTotal: totalProducts,
          allsetProducts: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      }
    }
    //if none is avaliable
    else {
      if (e.target.name === "categoryQuery") {
        const allProducts = this.props.searchAllProducts;
        const resultData = allProducts.filter((products) => {
          let productName = products.category._id;
          return productName.includes(e.target.value);
        });
        const totalProducts = resultData.length;
        let showProducts = resultData;

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          categoryQuery: e.target.value,
          productSlist: currentItems,
          currentS: 1,
          productSTotal: totalProducts,
          allsetProducts: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
        sessionStorage.setItem("products", JSON.stringify(allProducts));
      } else if (e.target.name === "statusQuery") {
        const allProducts = this.props.searchAllProducts;
        const resultData = allProducts.filter((products) => {
          if (products.active.toString() === e.target.value) {
            return products;
          } else {
            return null;
          }
        });
        const totalProducts = resultData.length;

        let showProducts = resultData;

        sessionStorage.setItem("products", JSON.stringify(showProducts));

        let currentPage = 1;
        let itemsPerPage = this.props.perPage;
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = showProducts.slice(
          indexOfFirstItem,
          indexOfLastItem
        );

        this.setState({
          statusQuery: e.target.value,
          productSlist: currentItems,
          currentS: 1,
          productSTotal: totalProducts,
          allsetProducts: showProducts,
          results: showProducts,
          loading: true,
          searchActive: true,
        });
      }
    }
  };

  //refresh back to default
  refreshItems = () => {
    this.setState({ statusQuery: "", categoryQuery: " ", searchActive: false });
  };

  render() {
    return (
      <Container>
        <HeadWrap>
          <TitleWrap>
            <TitleHead>Products</TitleHead>
            <CreateBtnsWrap>
              <CategoryBtn to="/dashboard/categories">
                View Product Categories
              </CategoryBtn>
              <CreateBtn to="/dashboard/productlist/create">
                + Create New
              </CreateBtn>
            </CreateBtnsWrap>
          </TitleWrap>
          <TitleSub>View Product List</TitleSub>
        </HeadWrap>

        <MobileSearchWrapper>
          <MobileSearch>
            <form onSubmit={this.onFormSubmit}>
              <input
                type="text"
                name="query"
                value={this.state.query}
                onChange={this.handleInputChange}
                placeholder="Search..."
              />
              <button>
                {" "}
                <BsSearch />
              </button>
            </form>
          </MobileSearch>

          <ButtonsWrap>
            <CategoryBtn2 to="/dashboard/categories">
              View Product Categories
            </CategoryBtn2>
            <CreateBtn2 to="/dashboard/productlist/create">
              <AiOutlinePlus />
            </CreateBtn2>
          </ButtonsWrap>
        </MobileSearchWrapper>
        <MobileBtnWrapper>
          <CategoryBtn3 to="/dashboard/categories">
            View Product Categories
          </CategoryBtn3>
        </MobileBtnWrapper>

        {this.state.pageLoading ? (
          <PContainer bg={"white"} className="Nolist">
            <Loader type="TailSpin" color="black" height={50} width={50} />
          </PContainer>
        ) : (
          <>
            {this.props.products.length > 0 ? (
              <PContainer bg={this.state.bgColor}>
                <SelectionWrapper>
                  <SearchWrapper>
                    <form onSubmit={this.onFormSubmit}>
                      <input
                        type="text"
                        name="query"
                        value={this.state.query}
                        onChange={this.handleInputChange}
                        placeholder="Search..."
                      />
                      <button>
                        {" "}
                        <BsSearch />
                      </button>
                    </form>
                  </SearchWrapper>

                  <SelectWrapper>
                    <ListingWrapper>
                      <ListStyleWrap
                        to="/dashboard/productlist"
                        active={this.state.listActive}
                      >
                        <FaListUl />
                      </ListStyleWrap>
                      <GalleryStyleWrap
                        to="/dashboard/productlist/grid"
                        active={this.state.gridActive}
                      >
                        <img src={Grid} alt="" />
                      </GalleryStyleWrap>
                    </ListingWrapper>

                    <form onSubmit={this.onFormSubmit}>
                      <DateSelect>
                        <select
                          name="categoryQuery"
                          onChange={this.handleSelectorWrap}
                          value={this.state.categoryQuery}
                        >
                          <option value="">categories</option>
                          {this.props.categories.map((data) => {
                            return (
                              <option value={data._id}>{data.name}</option>
                            );
                          })}
                        </select>
                      </DateSelect>

                      <StatusSelect>
                        <select
                          name="statusQuery"
                          value={this.state.statusQuery}
                          onChange={this.handleSelectorWrap}
                        >
                          <option value="">Status</option>
                          <option value="true">active</option>
                          <option value="false">inactive</option>
                        </select>
                      </StatusSelect>

                      <PaidSelect onClick={this.refreshItems}>
                        <BiRefresh />
                      </PaidSelect>
                    </form>
                  </SelectWrapper>
                </SelectionWrapper>
                {this.changeInnerComponent()}
              </PContainer>
            ) : (
              <PContainer bg={"white"} className="Nolist">
                <NoListWrapper>
                  <NoListContainer>
                    <img src={listImage} alt="NoList" />
                    <h1>List is Empty</h1>
                    <h5>Please create items on the list.</h5>
                  </NoListContainer>
                </NoListWrapper>
              </PContainer>
            )}
          </>
        )}
      </Container>
    );
  }
}

export default ProductSection;
