/* eslint-disable array-callback-return */
import { useState, useEffect } from "react";
import axios from "axios";
import { BASE_API_ } from "../../utils/base-url.config";
import Cookies from "js-cookie";

const ProductCreationDetails = (validateProduct) => {
  const [values, setValues] = useState({
    name: "",
    price: "",
    label: "",
    description: "",
    quantity: "",
    active: false,
    status: "",
    imageDefault: "",
    category: "",
    featured: false,
    color:"",
    size: "",
    colorError: "",
    totalStock: 0,
    costperitem: 0,
  });

  const [sku, setSKU] = useState({});
  const [stock, setStock] = useState({});
  const [successMsg, setSuccessMsg] = useState("");
  const [submitError, setSubmitError] = useState([]);
  const [getcatgeories, setGetCategories] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);
  const [size, setSize] = useState([]);
  const [color, setColor] = useState([]);

  const [file, setFile] = useState([]);
  const [imagesUrl, setImageUrl] = useState([]);
  const [uploadPercentage, setUploadPercentage] = useState(0);
  //handle change on the inputs
  const handleChange = (e) => {
     
    setValues({
      ...values,
      colorError: "",
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validateProduct(values));
    setIsSubmit(true);
  };

  const handleSubmit2 = (e) => {
    e.preventDefault();
  };

  const sizeHandler = (e) => {
    e.preventDefault();

    setSize([...size, values.size]);
    setValues({ ...values, size: "" });
  };

  const removeSize = (sizes) => {
    let allSizes = size;
    allSizes.filter((data, index) => {
      if (data === sizes) {
        allSizes.splice(index, 1);
      }
    });

    setSize([...allSizes]);
  };

  const colorHandler = (e) => {
    e.preventDefault();
   
    if (values.color) {
      color.filter((data) => {
        if (data === values.color) {
          return setValues({ ...values, colorError: `${data} already exists` });
        } else {
          return null;
        }
      });

      if (!values.colorError) {
        setColor([...color, values.color]);
        setValues({ ...values, color: "" });
      }
    } else {
      setValues({ ...values, colorError: `please type the color` });
    }
  };

  const skuHandler = (e, data) => {
    setSKU({
      ...sku,
      [e.target.name]: e.target.value,
    });
  };

  const stockHandler = (e, data) => {
    setStock({
      ...stock,
      [e.target.name]: e.target.value,
    });
  };

  const imageHandler = (e, data) => {
    e.preventDefault();

    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        let size = parseFloat(e.target.files[0].size / (1024 * 1024)).toFixed(
          2
        );

        if (size > 2) {
          alert("please select image less than 2MB");
        } else {
          let imagesUrlArray = [...imagesUrl];
          if (file.length === 0) {
            let target = e.target.files[0];

            target.colortype = data;
            setFile([...file, target]);
            setImageUrl([
              ...imagesUrl,
              { type: data, images: [reader.result] },
            ]);
          } else {
            for (
              let iteration = 0;
              iteration < imagesUrlArray.length;
              iteration++
            ) {
              let totalIteration = iteration + 1;
              if (imagesUrlArray[iteration].type === data) {
                let target = e.target.files[0];

                target.colortype = data;

                imagesUrlArray[iteration].images.push(reader.result);
                setFile([...file, target]);
                return setImageUrl([...imagesUrlArray]);
              } else if (
                totalIteration === imagesUrlArray.length &&
                imagesUrlArray[iteration].type !== data
              ) {
                let target = e.target.files[0];

                target.colortype = data;
                setFile([...file, target]);
                setImageUrl([
                  ...imagesUrl,
                  { type: data, images: [reader.result] },
                ]);
              }
            }
          }
        }
      } else {
      }
    };
    if (e.target.files.length > 1) {
      for (let iteration = 0; iteration < e.target.files.length; iteration++) {
        reader.readAsDataURL(e.target.files[iteration]);
      }
    } else {
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  //get the categories
  const fetchCategories = () => {
    axios
      .get(`${BASE_API_}/category/v1/retrieve`)
      .then((success) => {
        setGetCategories(success.data);
      })
      .catch((error) => {
        setSubmitError([error]);
      });
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const postDetails = async () => {
        try {
          const formData = new FormData();

          for (let iteration = 0; iteration < file.length; iteration++) {
            formData.append("colorTypes", file[iteration].colortype);
            formData.append("files", file[iteration]);
          }

          let value = {
            ...values,
            colors: color,
            sku: sku,
            stock: stock,
            sizes: size,
          };
          const getToken = Cookies.get("_tk");
          const postProductText = await axios.post(
            `${BASE_API_}/products/v1/products/create`,
            value,
            {
              headers: {
                Authorization: "Bearer " + getToken,
              },
            }
          );

          if (file.length !== 0) {
            if (!postProductText) {
              setIsSubmit(false);
            } else {
              setIsSubmit(false);
              let productId = postProductText.data.product._id;
              const postImages = await axios.patch(
                `${BASE_API_}/products/v1/update/pImage/${productId}`,
                formData,
                {
                  headers: {
                    "Content-Type": "multipart/form-data",
                    Authorization: "Bearer " + getToken,
                  },
                  onUploadProgress: (progressEvent) => {
                    setUploadPercentage(
                      parseInt(
                        Math.round(
                          (progressEvent.loaded * 100) / progressEvent.total
                        )
                      )
                    );
                  },
                }
              );

              if (postImages.data) {
                setSuccessMsg(
                  `Product Created successfully and Images Uploaded -`
                );
                setIsSubmit(false);
                window.location.replace("/dashboard/productlist");
              } else {
                setIsSubmit(false);
              }
            }
          } else {
            setSuccessMsg("Product Created successfully");
            window.location.reload();
          }
        } catch (error) {
          setSubmitError([error]);
        }
      };

      postDetails();
    } else {
      setIsSubmit(false);
    }
  }, [values, errors, file, isSubmitting, stock, color, size, sku]);

  return {
    values,
    submitError,
    successMsg,
    handleChange,
    handleSubmit,
    imageHandler,
    imagesUrl,
    fetchCategories,
    getcatgeories,
    uploadPercentage,
    errors,
    isSubmitting,
    colorHandler,
    color,
    sku,
    stock,
    skuHandler,
    stockHandler,
    sizeHandler,
    handleSubmit2,
    size,
    removeSize,
  };
};

export default ProductCreationDetails;
