import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import React, { useEffect } from "react";
import GlobalStyle from "./globalStyles";
import Home from "./views/index";
import PassReset from "./views/passreset";
import PassToken from "./views/passtoken";
import Reset from "./components/ResetPassword";

import Login from "./views/Login";
import ProtectedRoute from "./protectedRoutes";
import ScrollTop from "./components/ScrollToTop";

function App() {
  useEffect(() => {}, []);

  return (
    <Router>
      <ScrollTop />
      <GlobalStyle />
      <Switch>
        <Route path="/reset" component={Reset} exact />
        <ProtectedRoute path="/dashboard/:title" component={Home} exact />
        <Route path="/resetToken/:id" component={PassToken} exact />
        <Route path="/passreset/:token" component={PassReset} exact />
        <ProtectedRoute
          path="/dashboard/:title/:specific"
          component={Home}
          exact
        />
        <ProtectedRoute
          path="/dashboard/:title/:specific/:id"
          component={Home}
          exact
        />
        <ProtectedRoute path="/dashboard" component={Home} exact />
        <Route path="/" component={Login} exact />
        <Route path="*" component={() => "404 NOT FOUND"} />
      </Switch>
    </Router>
  );
}

export default App;
