/* eslint-disable no-unused-vars */
import { useState, useEffect } from "react";
import Cookies from "js-cookie";
import { BASE_API_ } from "../../utils/base-url.config";
import axios from "axios";
const SocialDetails = (formsubmit, validate) => {
  const [values, setValues] = useState({
    facebookLink: "",
    facebookActive: false,
    instagramLink: "",
    instagramActive: false,
    twitterLink: "",
    twitterActive: false,
    pinterestLink: "",
    pinterestActive: false,
    youtubeLink: "",
    youtubeActive: false,
  });

  // eslint-disable-next-line no-unused-vars
  const [submitSuccess, setSubmitSuccess] = useState("");
  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);

  const handleChange = (e) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
    
  };

  const handleClick = (name, value) => {
    setValues({
      ...values,
      [name]: value,
    });

  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // setErrors(validate(values));
    setIsSubmit(true);
  };

  const getInfo = () => {
      const getToken = Cookies.get("_tk");
    axios
      .get(`${BASE_API_}/admin/v1/socials`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        let valueData = success.data;

        setValues({
          ...values,
          ...valueData,
        });
      })
      .catch((error) => {});
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      const loginFunc = async () => {
        try {
           const getToken = Cookies.get("_tk");
          const userId = Cookies.get("sid");
          await axios
            .post(`${BASE_API_}/admin/v1/socials/create/${userId}`, values, {
              headers: {
                Authorization: "Bearer " + getToken,
              },
            })
            .then((sucess) => {
              setIsSubmit(false);
              setSubmitSuccess("successful");
            })
            .catch((error) => {
              setSubmitError([error]);
              setIsSubmit(false);
            });
        } catch (error) {
          setIsSubmit(false);
        }
      };

      loginFunc();
    } else {
      setIsSubmit(false);
    }
  }, [errors, values, isSubmitting, formsubmit]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    isSubmitting,
    handleClick,
    getInfo,
    submitSuccess,
  };
};

export default SocialDetails;
