/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import {
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  SelectWrapper,
  StatusSelect,
  DateSelect,
  LatestWrapper,
  LatestHeadWrap,
  LatestHeadContainer,
  LCustomer,
  LDate,
  LStatus,
  LEmail,
  LAction,
  LatestTableContent,
  LatestTableContainer,
  TCustomer,
  Profile,
  ProfileDetail,
  TDate,
  TStatus,
  TEmail,
  TAction,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";
import ProfilePhoto from "../../../assets/profile.png";
import { BsSearch } from "react-icons/bs";
import { AiOutlineClose } from "react-icons/ai";
import Pagination from "./pagination";
import Dropdown from "../CDropdown";
import { BiRefresh } from "react-icons/bi";
import Loader from "react-loader-spinner";
import listImage from "../../../assets/icons/NoList.svg";
import {BASE_API_} from '../../../utils/base-url.config'
const AllCustomers = ({ customers, pparams, historyProps, searchProps }) => {
  // this.state = { deleteActive: false,  };
 const [pageLoading, setPageLoading] = useState(false);
  const [currentPage, setcurrentPage] = useState(1);
  const [itemsPerPage, setitemsPerPage] = useState(8);
  const [query, setQuery] = useState("");
  const [deleteActive, setDeleteActive] = useState(false);
  const [currentSPage, setcurrentSPage] = useState(1);
  const [itemSTotal, setItemSTotal] = useState(null);
  const [itemSlist, setItemSList] = useState([]);
  const [allsetItems, setAllSetItems] = useState([]);
  const [searchActive, setSearchActive] = useState(false);
  const [statusQuery, setStatusQuery] = useState("");
  // const [] = useState()
  // const [] = useState()
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = customers.slice(indexOfFirstItem, indexOfLastItem);

  const closeDelete = () => {
    setDeleteActive(false);
  };

  const changecustomerPage = (page) => {
    setcurrentPage(page);
  };

  useEffect(() => {
    document.onreadystatechange = () => {
      if (document.readyState === "loading") {
        setPageLoading(true);
      } else if (document.readyState === "interactive") {
        setPageLoading(true);
      } else if (document.readyState === "complete") {
        setPageLoading(false);
      }
    };
    let page;
    if (searchProps.search) {
      page = searchProps.search.split("").slice(3).join("");
      setcurrentPage(page);
    }
  }, []);

  const changesearchPage = (page) => {
    let currentPages = page;
    let itemsPerPages = itemsPerPage;
    const indexOfLastItem = currentPages * itemsPerPages;
    const indexOfFirstItem = indexOfLastItem - itemsPerPages;
    const currentItems = allsetItems.slice(indexOfFirstItem, indexOfLastItem);

    setcurrentSPage(currentPages);
    setItemSList(currentItems);
    setSearchActive(true);
  };

  const onFormSubmit = (e) => {
    e.preventDefault();
  };

  const handleInputChange = (event) => {
    const query = event.target.value;
    getSearchResults(query);
    setQuery(query);
  };

  const getSearchResults = (query) => {
    const allProducts = customers;

    const resultData = allProducts.filter((agent) => {
      let fullname = agent.firstname + " " + agent.lastname;

      let queryName = fullname.toLowerCase();

      return queryName.includes(query.toLowerCase());
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPages = 1;
    let itemsPerPages = itemsPerPage;
    const indexOfLastItem = currentPages * itemsPerPages;
    const indexOfFirstItem = indexOfLastItem - itemsPerPages;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    setcurrentSPage(1);
    setItemSTotal(totalOrders);
    setItemSList(currentItems);
    setAllSetItems(showOrders);
    setSearchActive(true);
  };

  //changing selection for form
  const handleSelectorWrap = (e) => {
      const allProducts = customers;

      const resultData = allProducts.filter((data) => {
        if (data.utype === e.target.value){
          return data
        } else {
          return null;
        }
      });

      const totalOrders = resultData.length;

      let showOrders = resultData;

      let currentPages = 1;
      let itemsPerPages = itemsPerPage;
      const indexOfLastItem = currentPages * itemsPerPages;
      const indexOfFirstItem = indexOfLastItem - itemsPerPages;
      const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);
      setStatusQuery(e.target.value);
      setcurrentSPage(1);
      setItemSTotal(totalOrders);
      setItemSList(currentItems);
      setAllSetItems(showOrders);
      setSearchActive(true);
  };

  //refresh back to default
 const  refreshItems = () => {
   setStatusQuery("");
   setSearchActive(false);
  };
  return (
    <>
      {pageLoading ? (
        <PContainer bg={"white"} className="Nolist">
          <Loader type="TailSpin" color="black" height={50} width={50} />
        </PContainer>
      ) : (
        <>
          {currentItems.length > 0 ? (
            <>
              <OrderContainer>
                <SelectionWrapper>
                  <SearchWrapper>
                    <form onSubmit={onFormSubmit}>
                      <input
                        type="text"
                        name="query"
                        value={query}
                        onChange={handleInputChange}
                        placeholder="Search customer"
                      />
                      <button>
                        {" "}
                        <BsSearch />
                      </button>
                    </form>
                  </SearchWrapper>

                  <SelectWrapper>
                    <form onSubmit={onFormSubmit}>
                      <StatusSelect>
                        <select
                          name="statusQuery"
                          value={statusQuery}
                          onChange={handleSelectorWrap}
                        >
                          <option value="">Status</option>
                          <option value="GUEST">Guest</option>
                          <option value="USER">User</option>
                        </select>
                      </StatusSelect>

                      <DateSelect onClick={refreshItems}>
                        <BiRefresh />
                      </DateSelect>
                    </form>
                  </SelectWrapper>
                </SelectionWrapper>

                <LatestWrapper>
                  <LatestHeadWrap>
                    <LatestHeadContainer>
                      <LCustomer>Customer</LCustomer>
                      <LEmail>Email</LEmail>
                      <LDate>Joined</LDate>
                      <LStatus>Status</LStatus>
                      <LAction>Action</LAction>
                    </LatestHeadContainer>
                  </LatestHeadWrap>
                  {searchActive ? (
                    <>
                      {itemSlist.map((data, index) => {
                        let Id = data._id;
                        if (Object.keys(Id).length > 5) {
                          Id = "..." + data._id.split("").slice(14).join("");
                        }
                        return (
                          <LatestTableContent key={data._id}>
                            <LatestTableContainer>
                              <TCustomer>
                                <Profile>
                                  {data.profile ? (
                                    <img
                                      src={`${BASE_API_}${data.profile.path}`}
                                      alt=""
                                    />
                                  ) : (
                                    <img src={ProfilePhoto} alt="" />
                                  )}
                                </Profile>
                                <ProfileDetail>
                                  <h1>
                                    {data.firstname} {data.lastname}
                                  </h1>
                                  <p>Customer ID: {Id}</p>
                                </ProfileDetail>
                              </TCustomer>
                              <TEmail>{data.email}</TEmail>
                              <TDate>
                                {data.dayCreated}.{data.monthCreated}.
                                {data.yearCreated}
                              </TDate>
                              <TStatus>{data.utype}</TStatus>
                              <TAction>
                                <Dropdown id={data._id} />
                              </TAction>
                            </LatestTableContainer>
                          </LatestTableContent>
                        );
                      })}
                    </>
                  ) : (
                    <>
                      {currentItems.map((data, index) => {
                        let Id = data._id;
                        if (Object.keys(Id).length > 5) {
                          Id = "..." + data._id.split("").slice(14).join("");
                        }
                        return (
                          <LatestTableContent key={data._id}>
                            <LatestTableContainer>
                              <TCustomer>
                                <Profile>
                                  <img src={ProfilePhoto} alt="" />
                                </Profile>
                                <ProfileDetail>
                                  <h1>
                                    {data.firstname} {data.lastname}
                                  </h1>
                                  <p>Customer ID: {Id}</p>
                                </ProfileDetail>
                              </TCustomer>
                              <TEmail>{data.email}</TEmail>
                              <TDate>
                                {data.dayCreated}.{data.monthCreated}.
                                {data.yearCreated}
                              </TDate>
                              <TStatus>{data.utype}</TStatus>
                              <TAction>
                                <Dropdown id={data._id} />
                              </TAction>
                            </LatestTableContainer>
                          </LatestTableContent>
                        );
                      })}
                    </>
                  )}
                </LatestWrapper>

                {searchActive ? (
                  <Pagination
                    changecustomerPage={changesearchPage}
                    totalItems={itemSTotal}
                    itemPerPage={itemsPerPage}
                    current={currentSPage}
                    paramslinks={pparams}
                    historyProps={historyProps}
                  />
                ) : (
                  <Pagination
                    changecustomerPage={changecustomerPage}
                    totalItems={customers.length}
                    itemPerPage={itemsPerPage}
                    current={currentPage}
                    paramslinks={pparams}
                    historyProps={historyProps}
                    link={"/dashboard/customers"}
                  />
                )}

                {deleteActive && (
                  <DeleteContainer>
                    <DeleteWrapper>
                      <DeleteTitle>
                        <h5>Delete this Order? {this.state.ids}</h5>
                        <CloseIcon onClick={closeDelete}>
                          <AiOutlineClose />
                        </CloseIcon>{" "}
                      </DeleteTitle>

                      <DeleteText>
                        You’ll lose all this product details. We can’t recover
                        them when deleted.
                      </DeleteText>
                      <DeleteText>
                        Are you sure you want to <h5>permanently</h5> delete
                        this order?
                      </DeleteText>

                      <ButtonWrap>
                        <CancelBtn onClick={closeDelete}>Cancel</CancelBtn>{" "}
                        <YesBtn>Yes</YesBtn>
                      </ButtonWrap>
                    </DeleteWrapper>
                  </DeleteContainer>
                )}
              </OrderContainer>
            </>
          ) : (
            <PContainer bg={"white"} className="Nolist">
              <NoListWrapper>
                <NoListContainer>
                  <img src={listImage} alt="NoList" />
                  <h1>List is Empty</h1>
                  <h5>Please wait for customers.</h5>
                </NoListContainer>
              </NoListWrapper>
            </PContainer>
          )}
        </>
      )}
    </>
  );
};

export default AllCustomers;
