import React from "react";

import ProfileSection from "../common/ProfileHeader";
import HomeSection from "../Home";
import { Wrapper, ComponentWrapper } from "./styles";
import Orders from "../Orders";
import Customers from "../Customers";
import Transactions from "../Transactions";
import MobileTransactions from "../../Mobile/components/TransactionController";
import SettingComp from "../SettingComp";
import Productlist from "../Productlists";
import ShippingSection from "../Shipping";
import MobileHome from "../../Mobile/components/Home";
import Blogs from "../BlogComponents";
import Categories from "../ProductlistComponents/Categories";
import MobileCategory from "../../Mobile/components/ProductsController/Categories";


class Dashboard extends React.Component {
  componentDidMount() {


  }

  changeComponent() {
    if (this.props.paramslinks.path === "/dashboard") {
      if (this.props.MobileActive) {
        return (
          <MobileHome
            pparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      } else {
        return (
          <HomeSection
            mobileActive={this.props.MobileActive}
            pparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      }
    } else if (
      this.props.paramslinks.path === "/dashboard/:title" ||
      this.props.paramslinks.path === "/dashboard/:title/:specific" ||
      "/dashboard/:title/:specific/:id"
    ) {
      if (this.props.paramslinks.params.title === "orders") {
        return (
          <Orders
            mobileActive={this.props.MobileActive}
            oparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      } else if (this.props.paramslinks.params.title === "customers") {
        return (
          <Customers
            mobileActive={this.props.MobileActive}
            cparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
          />
        );
      } else if (this.props.paramslinks.params.title === "transaction") {
        if (this.props.MobileActive) {
          return (
            <MobileTransactions
              pparams={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          );
        } else {
          return (
            <Transactions
              pparams={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          );
        }
      } else if (this.props.paramslinks.params.title === "blog") {
        return (
          <Blogs
            pparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      } else if (this.props.paramslinks.params.title === "setting") {
        return (
          <SettingComp
            sparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      } else if (this.props.paramslinks.params.title === "categories") {
        if (this.props.MobileActive === true) {
          return (
            <MobileCategory
              pparams={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          );
        } else {
          return (
            <Categories
              pparams={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          );
        }
      } else if (this.props.paramslinks.params.title === "productlist") {
        return (
          <Productlist
            mobileActive={this.props.MobileActive}
            pparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      } else if (this.props.paramslinks.params.title === "shipping") {
        return (
          <ShippingSection
            mobileActive={this.props.MobileActive}
            pparams={this.props.paramslinks}
            searchProps={this.props.searchProps}
            historyProps={this.props.historyProps}
          />
        );
      }
    }
  }
  render() {
    return (
      <Wrapper>
        <ComponentWrapper>
          <ProfileSection toggle={this.props.toggle} />
          {this.changeComponent()}
        </ComponentWrapper>
      </Wrapper>
    );
  }
}

export default Dashboard;
