/* eslint-disable jsx-a11y/heading-has-content */
/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import {
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TCustomer,
  TDate,
  TStatus,
  TEmail,
  DeleteWrapper,
  DeleteTitle,
  CloseIcon,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  DeleteContainer,
  OrdersWrapper,
  OrdersLatestWrapper,
  TId,
  OrderId,
  ViewButton,
  Payment,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";

import ProfilePhoto from "../../../../assets/profile.png";
import { BsSearch } from "react-icons/bs";
import { AiOutlineRight } from "react-icons/ai";
import Pagination from "./pagination";
import Loader from "react-loader-spinner";
import listImage from "../../../../assets/icons/NoList.svg";

const AllMobileCustomers = ({
  customers,
  pparams,
  historyProps,
  searchProps,
}) => {
  // this.state = { deleteActive: false,  };

  const [pageLoading, setPageLoading] = useState(false);
  const [currentPage, setcurrentPage] = useState(1);
  const [itemsPerPage, setitemsPerPage] = useState(8);
  const [query, setQuery] = useState("");
  const [currentSPage, setcurrentSPage] = useState(1);
  const [itemSTotal, setItemSTotal] = useState(null);
  const [itemSlist, setItemSList] = useState([]);
  const [allsetItems, setAllSetItems] = useState([]);
  const [searchActive, setSearchActive] = useState(false);
  const [statusQuery, setStatusQuery] = useState("");
  // const [] = useState()
  // const [] = useState()
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = customers.slice(indexOfFirstItem, indexOfLastItem);


  const changecustomerPage = (page) => {
    setcurrentPage(page);
  };

  useEffect(() => {
      document.onreadystatechange = () => {
        if (document.readyState === "loading") {
          setPageLoading(true);
        } else if (document.readyState === "interactive") {
          setPageLoading(true);
        } else if (document.readyState === "complete") {
          setPageLoading(false);
        }
      };
    let page;
    if (searchProps.search) {
      page = searchProps.search.split("").slice(3).join("");
      setcurrentPage(page);
    }
  }, []);

  const changesearchPage = (page) => {
    let currentPages = page;
    let itemsPerPages = itemsPerPage;
    const indexOfLastItem = currentPages * itemsPerPages;
    const indexOfFirstItem = indexOfLastItem - itemsPerPages;
    const currentItems = allsetItems.slice(indexOfFirstItem, indexOfLastItem);

    setcurrentSPage(currentPages);
    setItemSList(currentItems);
    setSearchActive(true);
  };

  const onFormSubmit = (e) => {
    e.preventDefault();
  };

  const handleInputChange = (event) => {
    const query = event.target.value;
    getSearchResults(query);
    setQuery(query);
  };

  const getSearchResults = (query) => {
    const allProducts = customers;

    const resultData = allProducts.filter((agent) => {
      let fullname = agent.firstname + " " + agent.lastname;

      let queryName = fullname.toLowerCase();

      return queryName.includes(query.toLowerCase());
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPages = 1;
    let itemsPerPages = itemsPerPage;
    const indexOfLastItem = currentPages * itemsPerPages;
    const indexOfFirstItem = indexOfLastItem - itemsPerPages;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    setcurrentSPage(1);
    setItemSTotal(totalOrders);
    setItemSList(currentItems);
    setAllSetItems(showOrders);
    setSearchActive(true);
  };

  //changing selection for form
  const handleSelectorWrap = (e) => {
    const allProducts = customers;

    const resultData = allProducts.filter((data) => {
      if (data.utype === e.target.value) {
        return data;
      } else {
        return null;
      }
    });

    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPages = 1;
    let itemsPerPages = itemsPerPage;
    const indexOfLastItem = currentPages * itemsPerPages;
    const indexOfFirstItem = indexOfLastItem - itemsPerPages;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);
    setStatusQuery(e.target.value);
    setcurrentSPage(1);
    setItemSTotal(totalOrders);
    setItemSList(currentItems);
    setAllSetItems(showOrders);
    setSearchActive(true);
  };

  //refresh back to default
  const refreshItems = () => {
    setStatusQuery("");
    setSearchActive(false);
  };
  return (
    <OrderContainer>
      <SelectionWrapper>
        <SearchWrapper>
          <form onSubmit={onFormSubmit}>
            <input
              type="text"
              name="query"
              value={query}
              onChange={handleInputChange}
              placeholder="Search customer"
            />
            <button>
              {" "}
              <BsSearch />
            </button>
          </form>
        </SearchWrapper>
      </SelectionWrapper>

      {pageLoading ? (
        <PContainer bg={"white"} className="Nolist">
          <Loader type="TailSpin" color="black" height={50} width={50} />
        </PContainer>
      ) : (
        <>
          {currentItems.length > 0 ? (
            <>
              <OrdersWrapper>
                <OrdersLatestWrapper>
                  <LatestWrapper>
                    {searchActive ? (
                      <div>
                        {itemSlist.map((data, index) => {
                          let Id = data._id;
                          if (Object.keys(Id).length > 5) {
                            Id = "..." + data._id.split("").slice(14).join("");
                          }
                          return (
                            <LatestTableContent key={data._id}>
                              <LatestTableContainer>
                                <TId>
                                  <OrderId>CustomerID: {Id}</OrderId>

                                  <ViewButton
                                    to={`/dashboard/customers/view/${data._id}`}
                                  >
                                    <h1>View more</h1>{" "}
                                    <div>
                                      <AiOutlineRight />
                                    </div>{" "}
                                  </ViewButton>
                                </TId>

                                <TDate>
                                  <div>
                                    <h1></h1>
                                    <h5></h5>
                                  </div>

                                  <Payment
                                    className={
                                      data.utype === "GUEST" ? "guest" : null
                                    }
                                  >
                                    {data.utype}
                                  </Payment>
                                </TDate>

                                <TCustomer>
                                  <h1>Customer</h1>

                                  <h2>
                                    {data.firstname} {data.lastname}
                                  </h2>
                                </TCustomer>

                                <TEmail>
                                  <h1>Email</h1>

                                  <h2>{data.email}</h2>
                                </TEmail>
                                <TStatus>
                                  <h1>Joined</h1>

                                  <h2>
                                    {" "}
                                    {data.dayCreated}.{data.monthCreated}.
                                    {data.yearCreated}
                                  </h2>
                                </TStatus>
                              </LatestTableContainer>
                            </LatestTableContent>
                          );
                        })}
                      </div>
                    ) : (
                      <>
                        {currentItems.map((data, index) => {
                          let Id = data._id;
                          if (Object.keys(Id).length > 5) {
                            Id = "..." + data._id.split("").slice(14).join("");
                          }
                          return (
                            <LatestTableContent key={data._id}>
                              <LatestTableContainer>
                                <TId>
                                  <OrderId>CustomerID: {Id}</OrderId>

                                  <ViewButton
                                    to={`/dashboard/customers/view/${data._id}`}
                                  >
                                    <h1>View more</h1>{" "}
                                    <div>
                                      <AiOutlineRight />
                                    </div>{" "}
                                  </ViewButton>
                                </TId>

                                <TDate>
                                  <div>
                                    <h1></h1>
                                    <h5></h5>
                                  </div>

                                  <Payment
                                    className={
                                      data.utype === "GUEST" ? "guest" : null
                                    }
                                  >
                                    {data.utype}
                                  </Payment>
                                </TDate>

                                <TCustomer>
                                  <h1>Customer</h1>

                                  <h2>
                                    {data.firstname} {data.lastname}
                                  </h2>
                                </TCustomer>

                                <TEmail>
                                  <h1>Email</h1>

                                  <h2>{data.email}</h2>
                                </TEmail>
                                <TStatus>
                                  <h1>Joined</h1>

                                  <h2>
                                    {" "}
                                    {data.dayCreated}.{data.monthCreated}.
                                    {data.yearCreated}
                                  </h2>
                                </TStatus>
                              </LatestTableContainer>
                            </LatestTableContent>
                          );
                        })}
                      </>
                    )}
                  </LatestWrapper>
                </OrdersLatestWrapper>
              </OrdersWrapper>

              {searchActive ? (
                <Pagination
                  changecustomerPage={changesearchPage}
                  totalItems={itemSTotal}
                  itemPerPage={itemsPerPage}
                  current={currentSPage}
                  paramslinks={pparams}
                  historyProps={historyProps}
                />
              ) : (
                <Pagination
                  changecustomerPage={changecustomerPage}
                  totalItems={customers.length}
                  itemPerPage={itemsPerPage}
                  current={currentPage}
                  paramslinks={pparams}
                  historyProps={historyProps}
                  link={"/dashboard/customers"}
                />
              )}
            </>
          ) : (
            <PContainer bg={"white"} className="Nolist">
              <NoListWrapper>
                <NoListContainer>
                  <img src={listImage} alt="NoList" />
                  <h1>List is Empty</h1>
                  <h5>Please wait for customers.</h5>
                </NoListContainer>
              </NoListWrapper>
            </PContainer>
          )}
        </>
      )}
    </OrderContainer>
  );
};

export default AllMobileCustomers;
