import React from "react";
import PassKeyReset from "../components/passwordComponentts/sendPassword";

class PassReset extends React.Component {
  state = { token: this.props.match.params.token };

  formSubmit = () => {
    this.props.history.replace("/");
  };
  render() {
    return (
      <>
        <PassKeyReset tk={this.state.token} formsubmit={this.formSubmit} />
      </>
    );
  }
}

export default PassReset;
