import { useState, useEffect } from "react";

import axios from "axios";
import { BASE_API_ } from "../../utils/base-url.config";
const CustomerDetails = (validate) => {
  const [values, setValues] = useState({
    email: "",
    firstname: "",
    lastname: "",
    phonenumber: "",
    address: "",
    city: "",
    country: "",
    utype: "",
  });

  const [submitError, setSubmitError] = useState([]);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmit] = useState(false);

  const handleChange = (e) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrors(validate(values));
    setIsSubmit(true);
  };

  const getInfo = (data, Id) => {
    if (data.length !== 0) {
      data.map((data, index) => {
        if (data._id === Id) {
          setValues({
            ...values,
         
               firstname: data.firstname,
            lastname: data.lastname,
            email: data.email,
            phonenumber: data.phonenumber,
            utype: data.utype,
          });

          axios
            .get(
              `${BASE_API_}/user/v1/getShipping/${data.utype}/${Id}`
            )
            .then((success) => {
              setValues({
                ...values,
                country: success.data.country,
                city: success.data.city,
                address: success.data.address,
                firstname: data.firstname,
                lastname: data.lastname,
                email: data.email,
                phonenumber: data.phonenumber,
                utype: data.utype,
              });
            })
            .catch((error) => {
              setSubmitError([error]);
            });
            return null;
        } else {
          return null;
        }
      });
    }
  };

  useEffect(() => {

    if (Object.keys(errors).length === 0 && isSubmitting) {
      const postItem = async () => {
        try {
          await axios
            .post("/registers", values)
            .then((sucess) => {
              // let data = sucess.data;
              // localStorage.setItem("_tk", JSON.stringify(data.token));
              // localStorage.setItem("sid", JSON.stringify(data.id));
              // FormSubmit();
            })
            .catch((error) => {
           
              // setSubmitError();
              setIsSubmit(false);
            });
        } catch (error) {
          
          setIsSubmit(false);
        }
      };

      postItem();
    }
  }, [errors, values, isSubmitting]);

  return {
    values,
    errors,
    submitError,
    handleChange,
    handleSubmit,
    getInfo
  };
};

export default CustomerDetails;
