/* eslint-disable no-unused-vars */
import React, {  useState } from "react";
import {
  PaginationContainer,
  PaginationWrapper,
  PButton,
  PageNum,
  Num,
  NButton,
} from "./styles";
const Pagination = ({
  totalItems,
  itemPerPage,
  current,
  historyProps,
  link
}) => {
  const [pageNumLimit, setPageNumLimit] = useState(3);
  const [maxpageNumLimit, setMaxPageNumLimit] = useState(3);
  const [minpageNumLimit, setMinPageNumLimit] = useState(0);
  const pageNum = [];
 
  for (
    let iteration = 1;
    iteration <= Math.ceil(totalItems / itemPerPage);
    iteration++
  ) {
    pageNum.push(iteration);
  }

  if (document.readyState === "interactive") {
    if (parseInt(current) + 1 > maxpageNumLimit) {
      setMaxPageNumLimit(maxpageNumLimit + pageNumLimit);
      if (parseInt(current) + 1 === pageNum.length) {
        setMinPageNumLimit(minpageNumLimit - 1);
      } else {
        setMinPageNumLimit(minpageNumLimit + pageNumLimit);
      }
      
    }
  }

  const handleNext = () => {
    //adds to the current and moves to next page
    if (parseInt(current) + 1 <= pageNum.length) {
      let nextPage = parseInt(current) + 1;
      historyProps.push(`${link}?p=${nextPage}`);
    }
    //shifts to the hidden pageNumbers when end is reached
    if (parseInt(current) + 1 > maxpageNumLimit) {
      setMaxPageNumLimit(maxpageNumLimit + pageNumLimit);
      if (parseInt(current) + 1 === pageNum.length) {
        setMinPageNumLimit(minpageNumLimit - 1);
      } else {
        setMinPageNumLimit(minpageNumLimit + pageNumLimit);
      }
    }
  };

  const handlePrev = () => {
    //adds to the current and moves to next page

   
    if (parseInt(current) > 1) {
      let PrevPage = parseInt(current) - 1;
      historyProps.push(`${link}?p=${PrevPage}`);

      //shifts to the hidden pageNumbers when end is reached

      if ((parseInt(current) - 1) % pageNumLimit === 0) {
        setMaxPageNumLimit(maxpageNumLimit - pageNumLimit);
        setMinPageNumLimit(minpageNumLimit - pageNumLimit);
      }
    }
  };

  return (
    <PaginationContainer>
      <PaginationWrapper>
        <PButton onClick={handlePrev}>Previous</PButton>
        <PageNum>
          {pageNum.map((data, index) => {
            if (data < maxpageNumLimit + 1 && data > minpageNumLimit) {
              return (
                <Num
                  key={index}
                  to={`${link}?p=${data}`}
                  className={parseInt(current) === data ? "active" : null}
                >
                  {data}
                </Num>
              );
            } else {
              return null;
            }
          })}
        </PageNum>
        <NButton onClick={handleNext}>Next</NButton>
      </PaginationWrapper>
    </PaginationContainer>
  );
};

export default Pagination;
