import React from "react";
import {
  Wrapper,
  Container,
  NoListWrapper,
  NoListContainer,
  PContainer,
  PTitle,
} from "./styles";
import AllOrders from "../OrderComponents/AllOrders";
import AllMobileOrders from "../../Mobile/components/OrdersController/AllOrders";
import OrderDetails from "../OrderComponents/OrderDetails";
import OrderMobileDetails from "../../Mobile/components/OrdersController/OrderDetails";
import { connect } from "react-redux";
import {
  fetchOrders,
  fetchTopSelling,
  fetchPaySummary,
  fetchOrderMonth,
} from "../../store/actions";
import listImage from "../../assets/icons/NoList.svg";
import Cookies from "js-cookie";

import Loader from "react-loader-spinner";

class OrdersSection extends React.Component {
  state = {
    page: undefined,
    perPage: 8,
    totalProducts: 0,
    avproducts: [],
    pageLoading: false,
  };

  componentDidMount() {
 
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");
      const getOrders = async () => {
        await this.props.fetchOrders(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );

        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.orders.orders)
        );
      };

      getOrders();
    } else {
      const getOrders = async () => {
        await this.props.fetchOrders(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
        await this.props.fetchTopSelling(this.redirect, getToken);
        await this.props.fetchPaySummary(this.redirect, getToken);
        await this.props.fetchOrderMonth(this.redirect, getToken);
        sessionStorage.setItem(
          "orders",
          JSON.stringify(this.props.orders.orders)
        );
      };

      getOrders();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      if (this.props.oparams.path !== "/dashboard/:title/:specific/:id") {
          const getToken = Cookies.get("_tk");
        let page;
        if (this.props.searchProps.search) {
          page = this.props.searchProps.search.split("").slice(3).join("");

          const getOrders = async () => {
            await this.props.fetchOrders(
              page,
              this.state.perPage,
              this.redirect,
              getToken
            );
            await this.props.fetchTopSelling(this.redirect, getToken);
            await this.props.fetchPaySummary(this.redirect, getToken);
            await this.props.fetchOrderMonth(this.redirect, getToken);

            sessionStorage.setItem(
              "orders",
              JSON.stringify(this.props.orders.orders)
            );
          };

          getOrders();
        } else {
          const getOrders = async () => {
            await this.props.fetchOrders(
              page,
              this.state.perPage,
              this.redirect,
              getToken
            );
            await this.props.fetchTopSelling(this.redirect, getToken);
            await this.props.fetchPaySummary(this.redirect, getToken);
            await this.props.fetchOrderMonth(this.redirect, getToken);

            sessionStorage.setItem(
              "orders",
              JSON.stringify(this.props.orders.orders)
            );
          };

          getOrders();
        }
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  changeComponent = () => {
    if (this.props.oparams.url === "/dashboard/orders") {
      if (this.props.mobileActive) {
        return (
          <AllMobileOrders
            ordersMonth={this.props.ordersMonth}
            paysummary={this.props.orderspaysummary}
            TopSelling={this.props.topselling.selling}
            pparams={this.props.oparams}
            orders={this.props.orders.orders}
            totalOrders={this.props.orders.totalOrders}
            current={this.props.orders.current}
            perPage={this.state.perPage}
            historyProps={this.props.historyProps}
            searchAllOrders={this.props.orders.allOrders}
          />
        );
      } else {
        
        return (
          <AllOrders
            ordersMonth={this.props.ordersMonth}
            paysummary={this.props.orderspaysummary}
            TopSelling={this.props.topselling.selling}
            pparams={this.props.oparams}
            orders={this.props.orders.orders}
            totalOrders={this.props.orders.totalOrders}
            current={this.props.orders.current}
            perPage={this.state.perPage}
            historyProps={this.props.historyProps}
            searchAllOrders={this.props.orders.allOrders}
          />
        );
      }
    } else if (this.props.oparams.path === "/dashboard/:title/:specific/:id") {
      if (this.props.oparams.params.specific === "view") {
        if (this.props.mobileActive) {
          return (
            <OrderMobileDetails
              historyProps={this.props.historyProps}
              pparams={this.props.oparams}
            />
          );
        } else {
          return (
            <OrderDetails
              historyProps={this.props.historyProps}
              pparams={this.props.oparams}
            />
          );
        }
      }
    }
  };
  render() {
    return (
      <Wrapper>
        <Container>
          {this.state.pageLoading ? (
            <PContainer bg={"white"} className="Nolist">
              <Loader type="TailSpin" color="black" height={50} width={50} />
            </PContainer>
          ) : (
            <>
              {this.props.orders.orders.length > 0 ? (
                <>{this.changeComponent()} </>
              ) : (
                <>
                  <PTitle>Orders</PTitle>
                  <PContainer bg={"white"} className="Nolist">
                    <NoListWrapper>
                      <NoListContainer>
                        <img src={listImage} alt="NoList" />
                        <h1>List is Empty</h1>
                        <h5>Please wait for the orders.</h5>
                      </NoListContainer>
                    </NoListWrapper>
                  </PContainer>
                </>
              )}
            </>
          )}
        </Container>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    orders: state.orders,
    topselling: state.orderTopSelling,
    orderspaysummary: state.orderspaysummary,
    ordersMonth: state.ordersMonth,
  };
};
export default connect(mapStateToProps, {
  fetchOrders,
  fetchTopSelling,
  fetchPaySummary,
  fetchOrderMonth,
})(OrdersSection);
