/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Container,
  TitleHead,
  TitleSub,
  TitleWrap,
  HeadWrap,
  EditBtn,
  PContainer,
  MainWrapper,
  SideWrapper,
  ProductDetailsWrap,
  FormContentWrap,
  ProductDescriptionWrap,
  ImageUploadWrapper,
  FormContentWrap1,
  FormContent,
  FormContentDesc,
  ImageTitle,
  ImageContainer,
  Image,
  ImageWrap,
  PriceWrapper,
  StatusWrapper,
  TagsWrapper,
  CategoriesWrapper,
  VisibilityWrapper,
  PriceContainer,
  StatusContainer,
  TagsContainer,
  VContainer,
  ContentWrap,
  Check,
  CatWrapper,
  ContentWrap2,
  FeaturedWrapper,
  FeaturedContainer,
  DeleteContainer,
  DeleteWrapper,
  DeleteErrors,
  DeleteTitle,
  CloseIcon,
  DeletesWrap,
  DeleteText,
  ButtonWrap,
  CancelBtn,
  YesBtn,
  LoadingWrapper,
  DeleteMessageWrap,
  DeleteMessage,
  CloseButton,
  DeleteIcon,
  ButtonsWraps,
  HeadTitle,
  TStockWrapper,
  TTStock,
  TTReserved,
  SideWrap1,
  ProductPriceWrap,
  ProductSize,
  ProductSizeTitle,
  SizeDisplay,
  SizeDisplayContent,
  ImageContainer2,
  ImageStockWrap,
  SKUWrap,
  ImageStock,
} from "./styles";
import { RiDeleteBinLine } from "react-icons/ri";
import deleteSvg from "../../../../assets/icons/delete.svg";
import Loader from "react-loader-spinner";
import { BiPencil } from "react-icons/bi";
import {BASE_API_} from '../../../../utils/base-url.config';
import Cookies from "js-cookie";

const MobileViewProduct = ({ products, pparams, historyProps, categories }) => {
  const [renderProduct, setRenderProduct] = useState({});
  const [submitError, setSubmitError] = useState([]);

  const [deleteSuccess, setDeleteSuccess] = useState("");
  const [loadDelete, setLoadDelete] = useState(false);
  const [deleteError, setDeleteError] = useState([]);
  const [ids, setIds] = useState("");
  const [deleteActive, setDeleteActive] = useState(false);

  useEffect(() => {
    let id = pparams.params.id;
    const getProducts = JSON.parse(sessionStorage.getItem("products") || "[]");

    if (getProducts.length !== 0) {
      const productDetails = async () => {
        const checkProduct = await getProducts.filter((data) => {
          if (data._id === id) {
            
            return data;
          }
       
          return null;
        });
        if (checkProduct.length !== 0) {
          setRenderProduct(...checkProduct);
        } else {
          historyProps.goBack();
        }
      };

      productDetails();
    } else {
      const getToken = Cookies.get("_tk");
      axios
        .get(`${BASE_API_}/products/v1/products/${id}`, {
          headers: {
            Authorization: "Bearer " + getToken,
          },
        })
        .then((success) => {
          setRenderProduct({ ...success.data });
        })
        .catch((error) => {
          setSubmitError([error]);
        });
    }
  }, [pparams, historyProps]);

  const deleteOrder = (id) => {
    setLoadDelete(true);
     const getToken = Cookies.get("_tk");
    axios
      .delete(`${BASE_API_}/products/v1/delete/${id}`, {
        headers: {
          Authorization: "Bearer " + getToken,
        },
      })
      .then((success) => {
        setDeleteSuccess("Order Deleted successfully");
        setLoadDelete(false);
        window.location.replace("/dashboard/productlist");
      })
      .catch((error) => {
        let arrayError = [];
        arrayError.push(error);
        setDeleteError(arrayError);
        setLoadDelete(false);
      });
  };

  const closeDelete = () => {
    setDeleteActive(false);
  };

  const activateDelete = (identity) => {
    setDeleteActive(true);
    setIds(identity);
    //this.setState({ deleteActive: true, ids: identity });
  };

  return (
    <Container>
      <HeadWrap>
        <HeadTitle>Products Details</HeadTitle>
        <TitleWrap>
          <TitleHead>Products Details</TitleHead>
          <ButtonsWraps>
            <EditBtn to={`/dashboard/productlist/update/${renderProduct._id}`}>
              <BiPencil />
            </EditBtn>
            <DeleteIcon onClick={() => activateDelete(renderProduct._id)}>
              <RiDeleteBinLine />
            </DeleteIcon>
          </ButtonsWraps>
        </TitleWrap>
      </HeadWrap>

      <PContainer>
        <MainWrapper>
          <ProductDetailsWrap>
            <FormContentWrap>
              <label htmlFor="productName">Product title*</label>
              <input
                type="text"
                name="name"
                value={renderProduct.name}
                readOnly
                id="productName"
              />
            </FormContentWrap>

            <FormContentDesc>
              <label htmlFor="descriptions">Product Description*</label>
              <textarea
                type="text"
                name="description"
                value={renderProduct.description}
                readOnly
                id="descriptions"
              />
            </FormContentDesc>
          </ProductDetailsWrap>

          <ProductPriceWrap>
            <FormContentWrap1>
              <FormContent>
                <label htmlFor="cost">Cost per item</label>
                <input
                  type="text"
                  name="costperitem"
                  value={renderProduct.costperitem}
                  readOnly
                  id="cost"
                />
              </FormContent>
              <FormContent>
                <label htmlFor="Price">Price *</label>
                <input
                  type="text"
                  name="price"
                  value={renderProduct.price}
                  readOnly
                  id="Price"
                />
              </FormContent>
            </FormContentWrap1>
          </ProductPriceWrap>

          <ProductSize>
            <ProductSizeTitle>Size</ProductSizeTitle>

            <SizeDisplay>
              {renderProduct.sizes && (
                <>
                  {renderProduct.sizes.map((data, index) => {
                    return (
                      <SizeDisplayContent key={index}>
                        <p>{data}</p>
                      </SizeDisplayContent>
                    );
                  })}
                </>
              )}
            </SizeDisplay>
          </ProductSize>

          <ImageUploadWrapper>
            <ImageWrap>
              <ImageTitle>Color</ImageTitle>
              {renderProduct.productTypes && (
                <>
                  {renderProduct.productTypes.map((data, index) => {
                    return (
                      <ImageContainer2 key={index}>
                        <ImageTitle>{data.Colortype}</ImageTitle>
                        <ImageStockWrap>
                          <SKUWrap>
                            <label>SKU</label>
                            <p>
                              SKUcan contain product color, vendor or packaging
                              type.(i.e Sets)
                            </p>
                            <input
                              type="text"
                              name={`sku-${data}`}
                              value={data.SKU}
                              readOnly
                            />
                          </SKUWrap>

                          <ImageStock>
                            <label>Stock*</label>
                            <input
                              type="text"
                              name={`stock-${data}`}
                              value={data.Stock}
                              readOnly
                            />
                          </ImageStock>
                        </ImageStockWrap>
                        <ImageContainer>
                          {data.Images.length !== 0 && (
                            <>
                              {data.Images.map((mainData) => {
                                return (
                                  <>
                                    <Image key={mainData.imageId._id}>
                                      <img
                                        src={`${BASE_API_}${mainData.imageId.path}`}
                                        alt=""
                                      />
                                    </Image>
                                  </>
                                );
                              })}
                            </>
                          )}
                        </ImageContainer>
                      </ImageContainer2>
                    );
                  })}
                </>
              )}
            </ImageWrap>
          </ImageUploadWrapper>

          <SideWrap1>
            <StatusWrapper>
              <StatusContainer>
                <label>Status *</label>
                <p>{renderProduct.active ? "Active" : "Inactive"}</p>
              </StatusContainer>
            </StatusWrapper>

            <FeaturedWrapper>
              <FeaturedContainer>
                <label>Featured </label>
                <p>{renderProduct.featured ? "Active" : "Inactive"}</p>
              </FeaturedContainer>
            </FeaturedWrapper>

            <TagsWrapper>
              <TagsContainer>
                <label>Tags </label>
                <p>{renderProduct.tags}</p>
              </TagsContainer>
            </TagsWrapper>
            <CategoriesWrapper>
              <CatWrapper>
                <label>Categories</label>
                {categories.length !== 0 && renderProduct.category && (
                  <div>
                    {categories.map((data, index) => {
                      return (
                        <ContentWrap key={data._id}>
                          <input
                            type="radio"
                            id={data.name}
                            name="category"
                            value={data._id}
                            readOnly
                            checked={
                              data._id === renderProduct.category._id
                                ? true
                                : false
                            }
                          />
                          <label htmlFor={data.name}>
                            <p>{data.name}</p>
                          </label>
                        </ContentWrap>
                      );
                    })}
                  </div>
                )}
              </CatWrapper>
            </CategoriesWrapper>
          </SideWrap1>

          <VisibilityWrapper>
            <VContainer>
              <label>Visibility</label>
              <ContentWrap2>
                <Check className={renderProduct.active ? "active" : ""} />{" "}
                <p>Published on site</p>
              </ContentWrap2>
            </VContainer>
          </VisibilityWrapper>

          <TStockWrapper>
            <TTStock>
              <label htmlFor="TotalStock">Total Stock* </label>
              <input
                type="text"
                name="totalStock"
                value={renderProduct.totalStock}
                readOnly
                id="TotalStock"
              />
            </TTStock>
            <hr />
            <TTReserved>
              <label htmlFor="Reserved">Reserved </label>
              <input
                type="text"
                name="reserved"
                value={renderProduct.reservedStock}
                readOnly
                id="Reserved"
              />
            </TTReserved>
          </TStockWrapper>
        </MainWrapper>
      </PContainer>

      {deleteActive && (
        <DeleteContainer>
          <DeleteWrapper>
            <DeleteTitle>
              {!loadDelete && !deleteSuccess ? (
                <h5>Delete this Product?</h5>
              ) : (
                <h5>Successful</h5>
              )}
            </DeleteTitle>

            {!loadDelete && !deleteSuccess && (
              <div>
                {deleteError.length !== 0 && deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].response}</DeleteErrors>
                )}
                {deleteError.length !== 0 && !deleteError[0].response && (
                  <DeleteErrors>{deleteError[0].message}</DeleteErrors>
                )}

                <DeletesWrap>
                  <img src={deleteSvg} alt="delete" />
                </DeletesWrap>
                <DeleteText>
                  You’ll lose all this product details. We can’t recover them
                  when deleted.
                </DeleteText>
                <DeleteText>
                  Are you sure you want to permanently delete this product?
                </DeleteText>

                <ButtonWrap>
                  <CancelBtn onClick={closeDelete}>Cancel</CancelBtn>{" "}
                  <YesBtn onClick={() => deleteOrder(ids)}>Yes</YesBtn>
                </ButtonWrap>
              </div>
            )}

            {loadDelete && (
              <LoadingWrapper>
                <Loader type="TailSpin" color="black" height={50} width={50} />
              </LoadingWrapper>
            )}

            {deleteSuccess && !loadDelete && (
              <LoadingWrapper>
                <DeleteMessageWrap>
                  <DeleteMessage>{deleteSuccess}</DeleteMessage>
                  <DeleteMessage>this popup will be closed automatically</DeleteMessage>
                  <CloseButton onClick={closeDelete}>Close</CloseButton>
                </DeleteMessageWrap>
              </LoadingWrapper>
            )}
          </DeleteWrapper>
        </DeleteContainer>
      )}
    </Container>
  );
};

export default MobileViewProduct;
