import React from "react";
import { Route, Redirect } from "react-router-dom";
import Cookies from "js-cookie";

const protectedRoutes = ({ component: Component, ...rest }) => {
  //sessionStorage.setItem("link", )
  const isAuth = !!Cookies.get("_tk");
  return (
    <Route
      {...rest}
      component={(compnentProps) =>
        isAuth ? (
          <>
            <Component
              {...compnentProps}
              paramslinks={compnentProps.match}
              searchProps={compnentProps.history.location}
              historyProps={compnentProps.history}
            />
          </>
        ) : (
          <Redirect
            to={{
              pathname: "/",
              state: { from: compnentProps.location },
            }}
          />
        )
      }
    />
  );
};

export default protectedRoutes;
