import React, { useState } from "react";
import {
  SideMenuContainer,
  SideMenuLink,
  SideMenuLinkB,
  SideMenuWrap,
  SideMenuItem,
  Wrapper,
  WrapContainer,
  DropdownLink,
  DropDownItem,
} from "./styles";

const SubMenu = ({ navdata, active, activeslide, changewidth, toggle }) => {
  const [subnav, setSubNav] = useState(false);

  const showSubnav = (e) => {
    e.preventDefault();
    changewidth();
    setSubNav((prevState) => !prevState);
  };

  return (
    <SideMenuContainer>
      {navdata.checkPath ? (
        <SideMenuLinkB
          onClick={navdata.subNav && showSubnav}
          name={navdata.name}
          active={active}
        >
          <SideMenuWrap activeslide={activeslide}>
            <img src={navdata.icon} alt="" />
            <SideMenuItem activeslide={activeslide}>
              {navdata.title}
            </SideMenuItem>
          </SideMenuWrap>
        </SideMenuLinkB>
      ) : (
        <SideMenuLink to={navdata.path} name={navdata.name} active={active} onClick={toggle}>
          <SideMenuWrap activeslide={activeslide}>
            <img src={navdata.icon} alt="" />
            <SideMenuItem activeslide={activeslide}>
              {navdata.title}
            </SideMenuItem>
          </SideMenuWrap>
        </SideMenuLink>
      )}
      {subnav && (
        <Wrapper activeslide={activeslide}>
          <WrapContainer>
            {navdata.subNav.map((subdata, index) => {
              return (
                <DropdownLink key={index}>
                  <DropDownItem to={subdata.path} activeClassName="current">
                    {subdata.title}
                  </DropDownItem>
                </DropdownLink>
              );
            })}
          </WrapContainer>
        </Wrapper>
      )}
    </SideMenuContainer>
  );
};

export default SubMenu;
