import React from "react";
import {
  Wrapper,
  Container,
  TitleHead,
  OrderContainer,
  SelectionWrapper,
  SearchWrapper,
  LatestWrapper,
  LatestTableContent,
  LatestTableContainer,
  TId,
  TDate,
  TCustomers,
  TWrapper,
  ViewWrapper,
  TDetailsWrapper,
  TnsTitle,
  TnsDate,
  TnsBilling,
  TnsPhone,
  TnsItems,
  TnsItemClick,
  TnsPaid,
  OrderId,
  ViewButton,
  TItems,
  TCost,
  OrdersWrapper,
  OrdersLatestWrapper,
  ViewContainer,
  TDetailsContainer,
  NoListWrapper,
  NoListContainer,
  PContainer,
} from "./styles";

import { BsSearch } from "react-icons/bs";
import { AiOutlineRight } from "react-icons/ai";
import { connect } from "react-redux";
import { fetchTransactions } from "../../../store/actions";
import Pagination from "./pagination";
import Pagination2 from "./pagination2";
import Loader from "react-loader-spinner";
import listImage from "../../../assets/icons/NoList.svg";
import Cookies from "js-cookie";

class MobileTransactionSection extends React.Component {
  state = {
    selectedTransact: false,
    page: undefined,
    perPage: 8,
    orderDetail: {},
    searchActive: false,
    orderSlist: [],
    currentS: 1,
    orderSTotal: null,
    allsetOrders: [],
    query: "",
  };

  componentDidMount() {
  
    const getToken = Cookies.get("_tk");
    let page;
    if (this.props.searchProps.search) {
      page = this.props.searchProps.search.split("").slice(3).join("");

      const ProductFetching = async () => {
        await this.props.fetchTransactions(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      ProductFetching();
    } else {
      const ProductFetching = async () => {
        await this.props.fetchTransactions(
          page,
          this.state.perPage,
          this.redirect,
          getToken
        );
      };

      ProductFetching();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.searchProps.search !== prevProps.searchProps.search) {
      const getToken = Cookies.get("_tk");
      let page;
      if (this.props.searchProps.search) {
        page = this.props.searchProps.search.split("").slice(3).join("");

        const ProductFetching = async () => {
          await this.props.fetchTransactions(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        ProductFetching();
      } else {
        const ProductFetching = async () => {
          await this.props.fetchTransactions(
            page,
            this.state.perPage,
            this.redirect,
            getToken
          );
        };

        ProductFetching();
      }
    }
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };

  //changing pagination for search Page
  changesearchPage = (page) => {
    let currentPage = page;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = this.state.allsetOrders.slice(
      indexOfFirstItem,
      indexOfLastItem
    );
    this.setState({
      orderSlist: currentItems,
      currentS: currentPage,
      loading: true,
      searchActive: true,
    });
  };

  onFormSubmit = (e) => {
    e.preventDefault();
  };

  handleInputChange = (event) => {
    const query = event.target.value;

    this.setState(
      {
        query: query,
      },
      () => {
        this.getSearchResults(query);
      }
    );
  };

  getSearchResults = (query) => {
    const allProducts = this.props.transactions.allOrders;

    const resultData = allProducts.filter((orders) => {
      let username;
      if (orders.userId) {
        let userFirstname = orders.userId.firstname;
        let userLastname = orders.userId.lastname;

        username = userLastname + " " + userFirstname;
      }

      if (orders.guestId) {
        let userFirstname = orders.guestId.firstname;
        let userLastname = orders.guestId.lastname;

        username = userLastname + " " + userFirstname;
      }

      let customerName = username.toLowerCase();
      return customerName.includes(query.toLowerCase());
    });
    const totalOrders = resultData.length;

    let showOrders = resultData;

    let currentPage = 1;
    let itemsPerPage = this.state.perPage;
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = showOrders.slice(indexOfFirstItem, indexOfLastItem);

    this.setState({
      orderSlist: currentItems,
      currentS: 1,
      orderSTotal: totalOrders,
      allsetOrders: showOrders,
      searchActive: true,
    });
  };

  viewTransaction = (transaction) => {
    this.setState({ selectedTransact: true, orderDetail: transaction });
  };

 

  renderOrders = () => {
    return this.props.transactions.orders.map((data, index) => {
      let username;
      if (data.userId) {
        let userFirstname = data.userId.firstname;
        let userLastname = data.userId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (data.guestId) {
        let userFirstname = data.guestId.firstname;
        let userLastname = data.guestId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>TransactionID: {Id}</OrderId>

              <ViewButton onClick={() => this.viewTransaction(data)}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Customer</h1>

              <h2>{username}</h2>
            </TCustomers>

            <TItems>
              <h1>Method</h1>

              <h2>{data.payment_method}</h2>
            </TItems>

            <TCost>
              <h1>Paid</h1>

              <h2>{data.total}</h2>
            </TCost>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  renderSearchedOrders = () => {
    return this.state.orderSlist.map((data, index) => {
      let username;
      if (data.userId) {
        let userFirstname = data.userId.firstname;
        let userLastname = data.userId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      if (data.guestId) {
        let userFirstname = data.guestId.firstname;
        let userLastname = data.guestId.lastname;

        let namesCombined = userLastname + " " + userFirstname;

        if (Object.keys(namesCombined).length > 15) {
          username = namesCombined.slice(0, 15) + "...";
        } else {
          username = namesCombined;
        }
      }

      let Id = data._id;
      if (Object.keys(Id).length > 5) {
        Id = data._id.split("").slice(13).join("") + "...";
      }

      return (
        <LatestTableContent key={data._id}>
          <LatestTableContainer>
            <TId>
              <OrderId>TransactionID: {Id}</OrderId>

              <ViewButton onClick={() => this.viewTransaction(data)}>
                <h1>View more</h1>{" "}
                <div>
                  <AiOutlineRight />
                </div>{" "}
              </ViewButton>
            </TId>

            <TDate>
              <div>
                <h1>Date Created :</h1>
                <h5>
                  {data.dayCreated}.{data.monthCreated}.{data.yearCreated}
                </h5>
              </div>
            </TDate>

            <TCustomers>
              <h1>Customer</h1>

              <h2>{username}</h2>
            </TCustomers>

            <TItems>
              <h1>Method</h1>

              <h2>{data.payment_method}</h2>
            </TItems>

            <TCost>
              <h1>Paid</h1>

              <h2>{data.total}</h2>
            </TCost>
          </LatestTableContainer>
        </LatestTableContent>
      );
    });
  };

  render() {
    return (
      <>
        <Wrapper>
          <Container>
            <TitleHead>Transactions</TitleHead>

            {this.state.pageLoading ? (
              <PContainer bg={"white"} className="Nolist">
                <Loader type="TailSpin" color="black" height={50} width={50} />
              </PContainer>
            ) : (
              <>
                {this.props.transactions.orders.length > 0 ? (
                  <OrderContainer>
                    <TWrapper>
                      <SelectionWrapper>
                        <SearchWrapper>
                          <form onSubmit={this.onFormSubmit}>
                            <input
                              type="text"
                              name="query"
                              value={this.state.query}
                              onChange={this.handleInputChange}
                              placeholder="Search customer"
                            />
                            <button>
                              {" "}
                              <BsSearch />
                            </button>
                          </form>
                        </SearchWrapper>
                      </SelectionWrapper>

                      <OrdersWrapper>
                        <OrdersLatestWrapper>
                          <LatestWrapper>
                            {this.state.searchActive ? (
                              <>
                                {this.state.orderSlist && (
                                  <div>{this.renderSearchedOrders()}</div>
                                )}
                              </>
                            ) : (
                              <>
                                {this.props.transactions.orders && (
                                  <div>{this.renderOrders()}</div>
                                )}
                              </>
                            )}
                          </LatestWrapper>
                        </OrdersLatestWrapper>
                      </OrdersWrapper>

                      {this.state.searchActive ? (
                        <Pagination2
                          totalItems={this.state.orderSTotal}
                          itemPerPage={this.state.perPage}
                          current={this.state.currentS}
                          paramslinks={this.props.pparams}
                          historyProps={this.props.historyProps}
                          changesearchPage={this.changesearchPage}
                        />
                      ) : (
                        <Pagination
                          totalItems={this.props.transactions.totalOrders}
                          itemPerPage={this.state.perPage}
                          current={this.props.transactions.current}
                          paramslinks={this.props.pparams}
                          historyProps={this.props.historyProps}
                          link={"/dashboard/transaction"}
                        />
                      )}
                    </TWrapper>
                  </OrderContainer>
                ) : (
                  <PContainer bg={"white"} className="Nolist">
                    <NoListWrapper>
                      <NoListContainer>
                        <img src={listImage} alt="NoList" />
                        <h1>List is Empty</h1>
                        <h5>Please create items on the list.</h5>
                      </NoListContainer>
                    </NoListWrapper>
                  </PContainer>
                )}
              </>
            )}
          </Container>

          {this.state.selectedTransact ? (
            <ViewWrapper>
              <ViewContainer>
                <TDetailsContainer>
                  <TnsTitle>
                    <h1>Transaction Details </h1>
                  </TnsTitle>
                  <TDetailsWrapper>
                    <TnsDate>
                      <h1>Date</h1>
                      <h5>
                        {this.state.orderDetail.dayCreated}.
                        {this.state.orderDetail.monthCreated}.
                        {this.state.orderDetail.yearCreated}
                      </h5>
                    </TnsDate>

                    <TnsBilling>
                      <h1>TransactionId</h1>

                      <h5>{this.state.orderDetail.transactionId}</h5>
                    </TnsBilling>

                    <TnsPhone>
                      <h1>Phone number</h1>
                      {this.state.orderDetail.userId && (
                        <h5>{this.state.orderDetail.userId.phonenumber}</h5>
                      )}

                      {this.state.orderDetail.guestId && (
                        <h5>{this.state.orderDetail.guestId.phonenumber}</h5>
                      )}
                    </TnsPhone>

                    <TnsItems>
                      <h1>Order code</h1>
                      <TnsItemClick>
                        <h5>{this.state.orderDetail.orderCode}</h5>
                        
                      </TnsItemClick>
                    </TnsItems>

                    <TnsPaid>
                      <h1>Paid</h1>
                      <h5>{this.state.orderDetail.total}</h5>
                    </TnsPaid>
                  </TDetailsWrapper>
                </TDetailsContainer>
              </ViewContainer>
            </ViewWrapper>
          ) : null}
        </Wrapper>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    transactions: state.transactions,
  };
};
export default connect(mapStateToProps, {
  fetchTransactions,
})(MobileTransactionSection);
