/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  OrderContainer,
  Wrapper,
  Container,
  ProfileWrapper,
  FormContent,
  FormContentWrap,
  Profile,
  FormContent1,

} from "./styles";

import user from "../../../assets/profile.png";
import CustomerFormActions from "../../FormValidation/customerForm";
import {BASE_API_} from '../../../utils/base-url.config'
const ViewCustomer = ({ customers, Id }) => {
  
  const {
    values,
    getInfo
  } = CustomerFormActions();

  useEffect(() => {
    const getdetails = async () => {
      getInfo(customers, Id);   
    };
    getdetails();
  }, [customers]);

  return (
    <OrderContainer>
      <Wrapper>
        <Container>
          <form>
            <FormContentWrap>
              <FormContent>
                <label htmlFor="firstname">First name</label>
                <input
                  type="text"
                  name="firstname"
                  value={values.firstname}
                  id="firstname"
                />
              </FormContent>
              <FormContent>
                <label htmlFor="lastname">last name</label>
                <input
                  id="lastname"
                  type="text"
                  name="lastname"
                  value={values.lastname}
                />
              </FormContent>
            </FormContentWrap>
            <FormContentWrap>
              <FormContent>
                <label htmlFor="email">Email</label>
                <input
                  id="email"
                  type="email"
                  name="email"
                  value={values.email}
                />
              </FormContent>
              <FormContent>
                <label htmlFor="phonenumber">Phone number</label>
                <input
                  id="phonenumber"
                  type="text"
                  value={values.phonenumber}
                  name="phonenumber"
                />
              </FormContent>
            </FormContentWrap>
            <FormContent1>
              <label>Address</label>
              <input type="text" value={values.address} name="address" />
              <input type="text" value={values.city} name="city" />
              <input type="text" value={values.country} name="country" />
            </FormContent1>
          </form>
          <ProfileWrapper>
            <Profile>
              {values.profile ? (
                <img
                  className="default"
                  src={`${BASE_API_}${values.profile.path}`}
                  alt=""
                />
              ) : (
                <img className="default" src={user} alt="" />
              )}
            </Profile>
          </ProfileWrapper>
        </Container>
        <hr />
      </Wrapper>
    </OrderContainer>
  );
};

export default ViewCustomer;
