import React from "react";

import Navigation from "../components/common/Navigation";
import MobileNavigation from "../Mobile/common/MobileMenu";

import { Wrapper, MobileWrapper } from "./styles";
import DashBoard from "../components/Dashboard";
import {connect} from 'react-redux';
import {CheckMobile} from '../store/actions'
class Home extends React.Component {
  constructor(props){
    super(props);
     this.state = {
       openMobileMenu: false,
       navwidth: "200",
       mobileView: false,
       webView:false,
     };
  }

  componentDidMount() {
    
    const mobileRespond = async ()=>{

  
      const screenChange =  window.matchMedia("(max-width: 1365px)");
     
      if (document.readyState === "loading") {
          if (screenChange.matches) {
            this.setState({ loading: true, mobileView: true, webView: false });
          } else {
            this.setState({ loading: true, mobileView: false, webView: true });
          }
      } else if (document.readyState === "interactive") {
          if (screenChange.matches) {
            this.setState({ loading: true, mobileView: true, webView: false });
          } else {
            this.setState({ loading: true, mobileView: false, webView: true });
          }
      } else if (document.readyState === "complete") {
        if (screenChange.matches) {
          this.setState({ mobileView: true, webView: false });
        } else {
          this.setState({ mobileView: false, webView: true });
        }
      }

      screenChange.addEventListener("change", (e) => {
       
       const adjustedMobile = e.matches;
       if (adjustedMobile) {
         this.setState({ mobileView: true, webView: false });
       } else {
         this.setState({ mobileView: false, webView: true });
       }
     });
    }

    mobileRespond();
    
  }

  componentDidUpdate(prevProps, state) {
    
  }
  toggle = () => {
    this.setState({ openMobileMenu: !this.state.openMobileMenu });
  };
  render() {
    return (
      <>
        {this.state.webView === true && (
          <Wrapper>
            <Navigation
              width={this.state.navwidth}
              paramslinks={this.props.paramslinks}
            />
            <DashBoard
              paramslinks={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          </Wrapper>
        )}

        {this.state.mobileView === true && (
          <MobileWrapper>
            <MobileNavigation
              toggle={this.toggle}
              mobileactive={this.state.openMobileMenu}
              paramslinks={this.props.paramslinks}
            />
            <DashBoard
              toggle={this.toggle}
              MobileActive={this.state.mobileView}
              paramslinks={this.props.paramslinks}
              searchProps={this.props.searchProps}
              historyProps={this.props.historyProps}
            />
          </MobileWrapper>
        )}
      </>
    );
  }
}
const mapStateToProps = (state)=>{
  return {
    acticeMobile: state.mobile
  }
}
export default connect(mapStateToProps, {CheckMobile})(Home);
