import React from "react";
import { Wrapper, Container, TitleHead, TitleSub } from "./styles";
import Allcustomers from "../CustomerComponents/AllCustomers";
import AllMobileCustomers from '../../Mobile/components/CustomerController/AllCustomers';
import ViewCustomers from "../CustomerComponents/ViewCustomer";
import ViewMobileCustomers from '../../Mobile/components/CustomerController/ViewCustomer';
import { connect } from "react-redux";
import { getallUsers } from "../../store/actions";
import Cookies from "js-cookie";

class CustomerSection extends React.Component {
  componentDidMount() {
    const getToken = Cookies.get("_tk");
    this.props.getallUsers(this.redirect, getToken);
  }

  redirect = () => {
    // window.location.reload();
    this.props.historyProps.replace("/");
  };
  changeComponent = () => {
    if (this.props.cparams.path === "/dashboard/:title") {
      if (this.props.cparams.params.title === "customers") {
        if (this.props.mobileActive) {
          return (
            <AllMobileCustomers
              customers={this.props.customers}
              searchProps={this.props.searchProps}
            />
          );
        } else {
          return (
            <Allcustomers
              customers={this.props.customers}
              searchProps={this.props.searchProps}
            />
          );
        }
      }
    } else if (this.props.cparams.path === "/dashboard/:title/:specific/:id") {
      if (this.props.cparams.params.specific === "view") {
        if (this.props.mobileActive) {
          return (
            <ViewMobileCustomers
              customers={this.props.customers}
              Id={this.props.cparams.params.id}
            />
          );
        } else {
          return (
            <ViewCustomers
              customers={this.props.customers}
              Id={this.props.cparams.params.id}
            />
          );
        }
      }
    }
  };
  render() {
   
    return (
      <Wrapper>
        <Container>
          <TitleHead>Customers</TitleHead>
          <TitleSub>View Customer List</TitleSub>
          {this.changeComponent()}
        </Container>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    customers: state.customers,
    errors: state.errors,
  };
};
export default connect(mapStateToProps, { getallUsers })(CustomerSection);
